<template>
  <div class="dashboard-search-group">
    <el-row
      justify="space-around"
      type="flex"
      class="row-bg"
      :gutter="6">
      <el-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
        <el-select
          filterable
          :filter-method="filterDis"
          @focus="resetDis"
          size="mini"
          :disabled="isDistrictUser"
          @change="getCircles"
          v-model="form.district"
          placeholder="District"
          clearable>
          <el-option
            v-for="item of districts"
            :key="item.dist_name"
            :label="item.dist_name"
            :value="item.dist_name">
          </el-option>
        </el-select>
      </el-col>
      <el-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
        <el-select
          filterable
          :filter-method="filterCir"
          @focus="resetCir"
          size="mini"
          style="width: 100%"
          @change="submitForm"
          v-model="form.circle"
          placeholder="Circle" clearable>
          <el-option
            v-for="item of circles"
            :key="item.gid"
            :label="item.name"
            :value="item.name">
          </el-option>
        </el-select>
      </el-col>
    </el-row>
    <el-select
      filterable
      :filter-method="filterAgen"
      @focus="resetAgen"
      class="dashboard-search-item"
      size="mini"
      v-model="form.funding_agency_id"
      placeholder="Source of Funding"
      @change="submitForm"
      clearable>
      <el-option
        v-for="item of agencies"
        :key="item.id"
        :label="item.name"
        :value="item.id">
      </el-option>
    </el-select>
    <el-select
      filterable
      :filter-method="filterDep"
      @focus="resetDep"
      class="dashboard-search-item"
      size="mini"
      :disabled="me.hasRole(2) || me.hasRole(6)"
      @change="submitForm"
      v-model="form.department_id"
      placeholder="Department"
      clearable>
      <el-option
        v-for="item of departments"
        :key="item.id"
        :label="item.name"
        :value="item.id">
      </el-option>
    </el-select>
    <el-select
      filterable
      :filter-method="filterStruc"
      @focus="resetStruc"
      size="mini"
      @change="submitForm"
      class="dashboard-search-item"
      v-model="form.structure"
      placeholder="Type" clearable>
      <el-option
        v-for="item of structures"
        :key="item.id"
        :label="item.name"
        :value="item.id">
      </el-option>
    </el-select>
    <el-row
      justify="space-around"
      type="flex"
      class="row-bg"
      :gutter="6">
      <el-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
        <el-date-picker
          class="dashboard-search-item"
          size="mini"
          v-model="form.from_date"
          @change="submitForm"
          type="date"
          :picker-options="startDateOptions"
          placeholder="Start date">
        </el-date-picker>
      </el-col>
      <el-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
        <el-date-picker
          class="dashboard-search-item"
          size="mini"
          v-model="form.to_date"
          @change="submitForm"
          type="date"
          :picker-options="toDateOptions"
          placeholder="End date">
        </el-date-picker>
      </el-col>
    </el-row>
    <el-input
      class="dashboard-search-item"
      size="mini"
      placeholder="Unique Project ID"
      v-model="form.id"
      @blur="submitForm"
      @keyup.enter.native.prevent.stop="submitForm"
      clearable>
    </el-input>
  </div>
</template>

<script>
  import api from '@/api'
  import { mapGetters } from 'vuex'
  import { cloneDeep } from 'lodash'
  import { filterArr } from '@/utils/Array'

  export default {
    name: 'filter-form',
    data () {
      return {
        startDateOptions: {
          disabledDate: this.disabledFromDate
        },
        toDateOptions: {
          disabledDate: this.disabledToDate
        },
        _districts: [],
        districts: [],
        circles: [],
        _circles: [],
        departments: [],
        _departments: [],
        structures: [],
        _structures: [],
        agencies: [],
        _agencies: [],
        form: {
          id: undefined,
          from_date: undefined,
          to_date: undefined,
          department_id: undefined,
          structure_id: undefined,
          district: undefined,
          circle: undefined,
          funding_agency_id: undefined,
          year: undefined
        }
      }
    },
    computed: {
      ...mapGetters([
        'me'
      ]),
      isDistrictUser () { return this.me.hasRole(4) }
    },
    methods: {
      disabledFromDate (time) {
        if (this.form.to_date) {
          return time.getTime() > new Date(this.form.to_date)
        }
      },
      disabledToDate (time) {
        if (this.form.from_date) {
          return time.getTime() < new Date(this.form.from_date)
        }
      },
      filterDis (value) {
        this.districts = filterArr(value, this._districts, 'dist_name')
      },
      filterCir (value) {
        this.circles = filterArr(value, this._circles, 'name')
      },
      filterAgen (value) {
        this.agencies = filterArr(value, this._agencies, 'name')
      },
      filterStruc (value) {
        this.structures = filterArr(value, this._structures, 'name')
      },
      filterDep (value) {
        this.departments = filterArr(value, this._departments, 'name')
      },
      resetDis () {
        this.districts = this._districts
      },
      resetCir () {
        this.circles = this._circles
      },
      resetAgen () {
        this.agencies = this._agencies
      },
      resetStruc () {
        this.structures = this._structures
      },
      resetDep () {
        this.departments = this._departments
      },
      initData () {
        if (this.isDistrictUser) {
          this.districts = [this.me.district]
          this._districts = [this.me.district]
          this.form.district = this.me.district
          this.getCircles()
        } else {
          api.getDistricts()
            .then(res => {
              this.districts = res.data
              this._districts = res.data
            })
        }
        api.getFundingAgencies()
          .then(res => {
            let agenciesData = res.data
            agenciesData.sort(function (a, b) {
              var nameA = a.name.toUpperCase()
              var nameB = b.name.toUpperCase()
              if (nameA < nameB) {
                return -1
              }
              if (nameA > nameB) {
                return 1
              }
              return 0
            })
            this.agencies = agenciesData
            this._agencies = agenciesData
          })
        if (this.me.hasRole(2) || this.me.hasRole(6)) {
          this.departments = [this.me.department]
          this._departments = [this.me.department]
          this.form.department_id = this.me.department.id
        } else {
          api.getDepartments()
            .then(res => {
              this.departments = res.data
              this._departments = res.data
            })
        }
        api.getStructures()
          .then(res => {
            this.structures = res.data
            this._structures = res.data
          })
      },
      getCircles () {
        this.form.circle = undefined
        this.circles = undefined
        if (this.form.district && this.form.district !== '') {
          api.getCircles(this.form.district)
            .then(res => {
              this.circles = res.data
              this._circles = res.data
            })
        }
        this.submitForm()
      },
      updateFunding (id) {
        this.form.funding_agency_id = id
        this.$emit('filterEdit', this.form)
      },
      submitForm () {
        let filters = cloneDeep(this.form)
        if (filters.department_id === '') {
          filters.department_id = undefined
        }
        if (filters.district === '') {
          filters.district = undefined
        }
        if (filters.structure_id === '') {
          filters.structure_id = undefined
        }
        if (filters.circle === '') {
          filters.circle = undefined
        }
        if (filters.funding_agency_id === '') {
          filters.funding_agency_id = undefined
        }
        if (filters.id === '' || filters.id === null) {
          filters.id = undefined
        }
        if (filters.from_date !== undefined && filters.from_date !== null) {
          filters.from_date = new Date(filters.from_date)
          filters.from_date.setHours(0, 0, 0)
        }
        if (filters.to_date !== undefined && filters.to_date !== null) {
          filters.to_date = new Date(filters.to_date)
          filters.to_date.setHours(23, 59, 59)
        }
        this.$emit('filterEdit', filters)
      }
    },
    created () {
      this.initData()
    }
  }
</script>

<style scoped>
  .dashboard-search-item {
    width: 100%;
    margin-top: 6px;
  }
</style>



