<template>
  <div
    class="order-popup">
    <div>Lat: {{ construction.latitude }} Long: {{ construction.longitude }}</div>

    <div class="order-item">
      <i class="material-icons construction-icon">domain</i>
      <div class="order-info">
        <div class="highlight-text" @click="link(construction.orders[0])">{{ construction.name }}</div>
      </div>
    </div>
    <div class="reports-container" v-if="construction.orders">
      <div v-for="order of [construction.orders[0]]">
        <div
          v-for="(report, index) of order.reports"
          :key="report.id"
          class="report-item">
          <!-- Header -->
          <div class="report-header">
            <div class="title-text">VISIT - {{ index + 1 }}</div>
            <div>{{ report.local_time | formatDate }}</div>
          </div>
          <!-- Images -->
          <el-carousel height="140" :interval="2000" arrow="always">
            <el-carousel-item v-if="report.image1">
              <img class="images-container-img" :src="report.image1" alt="Image 1">
            </el-carousel-item>
            <el-carousel-item  v-if="report.image2">
              <img class="images-container-img" :src="report.image2" alt="Image 2">
            </el-carousel-item>
            <el-carousel-item v-if="report.image3">
              <img class="images-container-img" :src="report.image3" alt="Image 3">
            </el-carousel-item>
            <el-carousel-item  v-if="report.image4">
              <img class="images-container-img" :src="report.image4" alt="Image 4">
            </el-carousel-item>
            <div class="image-description">{{ report.description }}</div>
          </el-carousel>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from '@/api'
  import {mapGetters} from 'vuex'
  export default {
    name: 'OrderPopup',

    filters: {
      formatDate (date) {
        return typeof date === 'string' ? `${date.slice(0, 10)} ${date.slice(11, 19)}` : date
      }
    },

    data () {
      return {
        construction: {},
        linkAble: false
      }
    },
    computed: {
      ...mapGetters([
        'me'
      ])
    },
    methods: {
      link (order) {
        if (this.linkAble) {
          return
        }
        let form = {
          order_id: order.id
        }
        api.checkExistMIS(form)
            .then(res => {
              let total = res.data.total
              if (total === 0) {
                // this.$router.push({ path: `projects?order_id=${this.order.id}` })
                if (!(this.me.hasRole(6) || this.me.hasRole(5) || this.me.hasRole(4))) {
                  window.location.href = '/#/add-project?order_id=' + order.id
                } else {
                  this.$notify.warning({
                    title: 'MIS not found',
                    message: ''
                  })
                }
                // this.$router.push({ path: `add-project?order_id=${this.order.id}` })
              } else {
                this.$notify.warning({
                  title: 'MIS has already been created',
                  message: ''
                })
                window.location.href = '/#/projects?order_id=' + order.id
              }
            })
            .catch(error => {
              console.log(error)
            })
      }
    }
  }
</script>

<style scoped>
  .order-popup {
    color: #afafaf;
  }

  .order-item {
    display: flex;
    padding: 10px 0;
    cursor: pointer;
  }

  .construction-icon {
    font-size: 38px;
    line-height: 42px;
    margin-right: 5px;
    flex: none;
  }

  .order-info {
    flex: 1;
  }

  .highlight-text {
    color: var(--brand-primary-light);
    font-weight: bold;
    font-size: 15px;
  }

  .title-text {
    font-weight: bold;
    color: rgb(26, 26, 26);
    font-size: 15px;
  }

  .reports-container {
    max-height: 360px;
    overflow-x: hidden;
    overflow-y: auto;
  }

  .report-item:not(:last-child) {
    margin-bottom: 5px;
  }

  .report-header {
    display: flex;
    justify-content: space-between;
    padding: 5px 0;
  }

  .images-container {
    position: relative;
    width: 100%;
    background-color: #cccccc;
    height: 180px;
  }

  .images-container-img {
    width: 100%;
    height: 100%;
    margin: auto;
    object-fit: cover;
  }

  .image-description {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1005;
    padding: 4px;
    color: white;

    background-color: rgba(0,0,0,0.5);
  }
</style>



