import Cesium from 'cesium/Cesium'
import CartesianConverter from './CartesianConverter'

export default class RectangleConverter {
  static fromDegreesPoints (points) {
    return Cesium.Rectangle.fromCartesianArray(CartesianConverter.fromDegreesPoints(points))
  }
}



