import Cesium from 'cesium/Cesium'
import { flatten } from 'lodash'

export default class CartesianConverter {
  /**
   * Convert degrees array to cartesian3 array
   * @param {array} degreesPoints Eg: [ ...[lng, lat] ] same as GeoJSON coordinates[0]
   */
  static fromDegreesPoints (points) {
    return Cesium.Cartesian3.fromDegreesArray(flatten(points))
  }

  static fromDegrees (point) {
    return Cesium.Cartesian3.fromDegrees(point[0], point[1])
  }
}



