<template>
  <div class="user-management">
    <div style="width: 100%; display: flex; flex-flow: row;">
      <el-form size="mini" style="flex: 1" label-width="80px">
        <el-form-item label="Filters">
          <el-select
            style="width: 600px"
            v-model="filtersForm.departments"
            multiple
            @change="getTableData"
            collapse-tags
            size="mini"
            placeholder="Select departments">
            <el-option
              v-for="item in departments"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-input
            size="mini"
            style="width: 600px"
            v-model="filtersForm.search"
            placeholder="Search user (name, mobile, email)"
            @keyup.enter.native.prevent.stop="getTableData"/>
          <el-button size="mini" :autofocus="true" type="primary" icon="el-icon-search" @click="getTableData" plain>Search</el-button>
          <el-button size="mini" type="primary" icon="el-icon-refresh" @click="resetSearch" plain>Reset</el-button>
        </el-form-item>
      </el-form>
      <div style="float: right">
        <el-button type="primary"  @click="showAddUserForm" plain>+ Add User</el-button>
      </div>
    </div>
    <el-pagination
      size="mini"
      @size-change="sizeChanged"
      @current-change="pageChanged"
      :page-sizes="[10, 15, 20, 50]"
      :page-size="filtersForm.page_size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalUser">
    </el-pagination>
    <el-table size="mini" :loading="loading" :data="tableData" style="overflow-y: auto">
      <el-table-column
          prop="created_at"
          label="Created at"
          min-width="140">
      </el-table-column>
      <el-table-column
          prop="updated_at"
          label="Updated at"
          min-width="140">
      </el-table-column>
      <el-table-column
          prop="name"
          label="Name"
          min-width="140">
      </el-table-column>
      <el-table-column
          prop="mobile"
          label="Mobile"
          min-width="140">
      </el-table-column>
      <el-table-column
          prop="email"
          label="Email"
          min-width="140">
      </el-table-column>
      <el-table-column
          label="Department"
          min-width="240">
        <template slot-scope="props">
          {{ props.row.department ? props.row.department.name : '' }}
        </template>
      </el-table-column>
      <el-table-column
          label="Operators"
          min-width="100">
        <template slot-scope="props">
          <el-button-group>
            <el-button size="mini" @click="showEditUserForm(props.row)" type="primary" icon="el-icon-edit"></el-button>
            <el-button size="mini" @click="deleteThisUser(props.row)" type="danger" icon="el-icon-delete"></el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="Create User" :visible.sync="dialogVisible">
      <el-form style="margin: 20px;" :label-position="position" :model="createdUserForm" ref="createdUserForm" :rules="rules">
        <el-row :gutter="20">
          <el-col :span="12">
            <i class="fa fa-user"></i>
            <label>NAME</label>
            <el-form-item prop="name">
              <el-input size="large" v-model="createdUserForm.name" placeholder="Enter your username"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <i class="fa fa-envelope-o"></i>
            <label>EMAIL</label>
            <el-form-item prop="email">
              <el-input size="large" v-model="createdUserForm.email" placeholder="Enter your email"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="12">
            <i class="fa fa-mobile"></i>
            <label>PHONE</label>
            <el-form-item prop="mobile">
              <el-input size="large"  v-model="createdUserForm.mobile" placeholder="Enter your mobile no."></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <i class="fa fa-university"></i>
            <label>DEPARTMENT</label>
            <el-form-item prop="department_id">
              <el-select size="large" v-model="createdUserForm.department_id" placeholder="Select your department" style="width:100%">
                <el-option v-for="item of departments" :label="item.name" :value="item.id" :key="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="12">
            <i class="fa fa-lock"></i>
            <label>PASSWORD</label>
            <el-form-item prop="password">
              <el-input size="large" type="password" v-model="createdUserForm.password" placeholder="*********"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <i class="fa fa-lock"></i>
            <label>CONFIRM PASSWORD</label>
            <el-form-item prop="password_confirmation">
              <el-input size="large" type="password" v-model="createdUserForm.password_confirmation" placeholder="*********"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item >
          <el-button type="primary" style="float: right" @click="handleSubmittedUserForm(createdUserForm)" :loading="addUserRequest.isRequesting()">SUBMIT</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog title="Change User Profile" :visible.sync="dialogProfileVisible">
      <el-form>
        <el-form-item >
          <el-input size="large" v-model="profileUserForm.name" ></el-input>
        </el-form-item>

        <el-form-item >
          <el-input size="large" v-model="profileUserForm.mobile"></el-input>
        </el-form-item>

        <el-form-item >
          <el-input size="large" v-model="profileUserForm.email"></el-input>
        </el-form-item>

        <el-form-item >
          <el-select size="large" v-model="profileUserForm.department_id" placeholder="Select your department" style="width:100%">
            <el-option v-for="item of departments" :label="item.name" :value="item.id" :key="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item >
          <el-button type="primary" style="float: right;" @click="handleChangeUserProfile(profileUserForm)">SUBMIT</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { cloneDeep } from 'lodash'
import api from '@/api'

export default {
  name: 'web-users',

  data () {
    return {
      textSearch: undefined,
      position: 'top',
      dialogProfileVisible: false,
      dialogVisible: false,
      departments: [],
      loading: false,
      tableData: [],
      filtersForm: {
        departments: [],
        search: undefined,
        page_size: 10,
        page: 1,
        role: 2
      },
      totalUser: null,
      profileUserForm: {
        name: '',
        email: '',
        mobile: '',
        department: ''
      },
      createdUserForm: {
        name: '',
        mobile: '',
        email: '',
        department_id: '',
        password: '',
        password_confirmation: ''
      },
      rules: {
        email: [
          { required: true, message: 'Please input email address', trigger: 'blur' },
          { type: 'email', message: 'Please input correct email address', trigger: 'blur, change' }
        ],

        password: [
          { required: true, message: 'Please input password', trigger: 'blur' },
          { min: 6, message: 'The password must be at least 6 characters', trigger: 'blur' }
        ],

        mobile: [
          { required: true, message: 'Please input phone number' },
          { validator: (rule, value, callback) => callback(/^\d+$/.test(value) ? undefined : new Error('Only accept number')) },
          { max: 11, message: 'Please input not greater than 11 characters' },
          { min: 10, message: 'Please input at least 10 charaters' }
        ],

        department_id: [
          { required: true, type: 'number', message: 'Please choose department', trigger: 'change' }
        ],

        password_confirmation: [
          { required: true, message: 'Please input confirm password' },
          { validator: (rule, value, callback) => callback(value !== this.createdUserForm.password ? new Error('The password confirmation does not match.') : undefined), trigger: 'blur' }
        ],

        name: [
          { required: true, message: 'Please input name' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([
      'addUserRequest',
      'deleteUserRequest',
      'editUserRequest',
      'userFilters'
    ]),
    addUserStatus () {
      return this.addUserRequest.status
    },
    deleteUserStatus () {
      return this.deleteUserRequest.status
    },
    updateUserStatus () {
      return this.editUserRequest.status
    }
  },
  methods: {
    ...mapActions([
      'addUser',
      'deleteUser',
      'updateUser'
    ]),
    sizeChanged (val) {
      this.filtersForm.page_size = val
      this.filtersForm.page = 1
      this.getTableData()
    },
    pageChanged (val) {
      this.filtersForm.page = val
      this.getTableData()
    },
    resetSearch () {
      this.filtersForm = {
        departments: [],
        search: undefined,
        page_size: 10,
        page: 1,
        role: 2
      }
      this.getTableData()
    },
    getTableData () {
      this.tableData = []
      api.getUsers(this.filtersForm)
        .then(res => {
          this.tableData = res.data.data
          this.totalUser = res.data.total
        })
        .catch(error => {
          console.log(error)
        })
    },
    handleSubmittedUserForm (createdUserForm) {
      this.$refs['createdUserForm'].validate((valid) => {
        if (valid) {
          this.addUser(createdUserForm)
        } else {
          return false
        }
      })
    },
    showAddUserForm () {
      this.dialogVisible = true
      if (this.$refs['createdUserForm']) {
        this.$refs['createdUserForm'].resetFields()
      }
    },
    deleteThisUser (user) {
      this.$confirm('Are you sure delete user?')
        .then(_ => {
          this.deleteUser(user.id)
        })
        .catch(_ => { })
    },
    showEditUserForm (user) {
      this.profileUserForm = cloneDeep(user)
      this.dialogProfileVisible = true
    },
    handleChangeUserProfile () {
      this.updateUser(this.profileUserForm)
    },
    getDepartments () {
      api.getDepartments()
        .then(res => {
          this.departments = res.data
        })
        .catch(err => console.log(err))
    }
  },

  mounted () {
    this.getTableData()
  },
  created () {
    this.getDepartments()
  },

  watch: {
    addUserStatus () {
      if (this.addUserRequest.isSuccess()) {
        this.$notify.success({
          title: 'User created'
        })
        this.dialogVisible = false
        this.$refs['createdUserForm'].resetFields()
      } else if (this.addUserRequest.isFailure()) {
        let myMessage = ''
        if (this.addUserRequest.error.value.errors.name) { myMessage = myMessage + this.addUserRequest.error.value.errors.name }
        if (this.addUserRequest.error.value.errors.email) { myMessage = myMessage + this.addUserRequest.error.value.errors.email }
        if (this.addUserRequest.error.value.errors.mobile) { myMessage = myMessage + this.addUserRequest.error.value.errors.mobile }
        this.$notify.error({
          title: 'User create failed',
          message: myMessage
        })
      }
    },
    deleteUserStatus () {
      if (this.deleteUserRequest.isSuccess()) {
        this.$notify.success({
          title: 'User deleted '
        })
      } else if (this.deleteUserRequest.isFailure()) {
        this.$notify.error({
          title: 'Delete user failed',
          message: this.deleteUserRequest.error.message
        })
      }
    },
    updateUserStatus () {
      if (this.editUserRequest.isSuccess()) {
        this.$notify.success({
          title: 'Updated successfully'
        })
        this.dialogProfileVisible = false
      } else if (this.editUserRequest.isFailure()) {
        this.$notify.error({
          title: 'Updated failed',
          message: this.editUserRequest.error.message
        })
      }
    }
  }
}
</script>

<style scoped>
.user-management {
  display: flex;
  padding: 10px;
  flex-flow: column;
  height: 100%;
}
</style>



