<template>
  <div class="user-management">
    <div style="width: 100%; display: flex; flex-flow: row;">
      <v-dialog title="Add Source of Funding" style="margin-left: auto;" max-width="500px" v-model="dialogVisible">
        <el-button @click="showAddFundingForm()" slot="activator" size="mini" type="primary" icon="el-icon-plus" plain>Add Source of Funding</el-button>
        <v-card>
          <v-card-title>
            <span class="headline">{{ title }}</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-lg>
              <v-layout wrap>
                <v-flex xs12>
                  <v-select
                    v-model="form.group_id"
                    :items="fundingSources"
                    :messages="errors.group_id"
                    label="Select Funding Group"
                    item-value="id"
                    item-text="name"/>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    v-model="form.name"
                    :messages="errors.name"
                    label="Name"/>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    v-model="form.full_name"
                    :messages="errors.full_name"
                    label="Full Name"/>
                </v-flex>
                <v-flex xs6>
                  <div style="font-size: 16px; color: rgba(0, 0, 0, 0.65); margin-top:8px;">Marker color</div>
                </v-flex>
                <v-flex xs6>
                  <el-color-picker style="float: right;" v-model="form.marker_color"></el-color-picker>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialogVisible = false">Cancel</v-btn>
            <v-btn
              color="blue darken-1" flat
              :loading="loading"
              @click="submit">
              Submit
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
    <el-pagination
      size="mini"
      @size-change="sizeChanged"
      @current-change="pageChanged"
      :page-sizes="[10, 15, 20, 50]"
      :page-size="filtersForm.page_size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalFunding">
    </el-pagination>
    <el-table size="mini" :loading="loading" :data="tableData" style="overflow-y: auto">
      <el-table-column
          prop="group_name"
          label="Group name"
          min-width="150">
      </el-table-column>
      <el-table-column
          prop="name"
          label="Name"
          min-width="150">
      </el-table-column>
      <el-table-column
          prop="full_name"
          label="Full name"
          min-width="150">
      </el-table-column>
      <el-table-column
          label="Marker color"
          min-width="150">
          <template slot-scope="props">
            <div :style="{ 'background-color': props.row.marker_color }" style="width: 20px; height: 20px; border-radius:50%;"> </div>
          </template>
      </el-table-column>
      <el-table-column
          label="Operators"
          width="150">
        <template slot-scope="props">
          <el-button-group>
            <el-tooltip content="Edit Source of Funding" placement="top-start">
              <el-button size="mini" @click="showEditFundingForm(props.row)" type="primary" icon="el-icon-edit"></el-button>
            </el-tooltip>
            <el-tooltip content="Delete Source of Funding" placement="top-end">
              <el-button size="mini" @click="confirmDeleteFunding(props.row)" type="danger" icon="el-icon-delete"></el-button>
            </el-tooltip>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import api from '@/api'
import { getFundingError } from '@/utils/projectUtils'

export default {
  name: 'funding-listing',
  components: {
  },
  data () {
    return {
      title: '',
      errors: {},
      fundingSources: [],
      dialogVisible: false,
      loading: false,
      tableData: [],
      filtersForm: {
        search: undefined,
        page_size: 10,
        page: 1
      },
      totalFunding: null,
      form: {
        group_id: undefined,
        name: '',
        full_name: '',
        marker_color: '#FF0000',
        src: 'create_by_user'
      }
    }
  },
  methods: {
    submit () {
      this.errors = getFundingError(this.form)
      if (!this.errors.valid) {
        delete this.errors.valid
        delete this.errors.alert
        this.$notify({
          title: 'Invalid',
          message: Object.values(this.errors).join(', '),
          type: 'error'
        })
        return
      }
      this.loading = true
      if (this.form.id === -1) {
        api.addFundingAgency(this.form)
          .then(res => {
            this.$notify.success({
              title: 'New source of funding has been successfully created'
            })
            this.getTableData()
            this.loading = false
            this.dialogVisible = false
          })
          .catch(error => {
            this.$notify.error({
              title: error.message
            })
            if (error.status_code === 422) {
              this.errors = error.errors
            }
            this.getTableData()
            this.loading = false
          })
      } else {
        api.updateFundingAgency(this.form)
          .then(res => {
            this.$notify.success({
              title: 'Source of funding has been edited'
            })
            this.getTableData()
            this.loading = false
            this.dialogVisible = false
          })
          .catch(error => {
            this.$notify.error({
              title: error.message
            })
            if (error.status_code === 422) {
              this.errors = error.errors
            }
            this.getTableData()
            this.loading = false
          })
      }
    },
    showEditFundingForm (funding) {
      this.form = {
        id: funding.id,
        group_id: funding.group_id,
        name: funding.name,
        full_name: funding.full_name,
        marker_color: funding.marker_color,
        src: funding.src
      }
      this.title = 'Edit Source of Funding'
      this.dialogVisible = true
    },
    showAddFundingForm (funding) {
      this.form = {
        id: -1,
        group_id: undefined,
        name: '',
        full_name: '',
        marker_color: '#FF0000',
        src: 'create_by_user'
      }
      this.title = 'Add Source of Funding'
      this.dialogVisible = true
    },
    sizeChanged (val) {
      this.filtersForm.page_size = val
      this.filtersForm.page = 1
      this.getTableData()
    },
    pageChanged (val) {
      this.filtersForm.page = val
      this.getTableData()
    },
    resetSearch () {
      this.filtersForm = {
        search: undefined,
        page_size: 10,
        page: 1
      }
      this.getTableData()
    },
    getTableData () {
      this.loading = true
      this.tableData = []
      api.getFundingSources(this.filtersForm)
        .then(res => {
          this.loading = false
          let tmps = res.data
          let fundArr = []
          this.fundingSources = tmps
          for (let i = 0; i < tmps.length; i++) {
            const gourp = tmps[i]
            const groupName = gourp.name
            for (let j = 0; j < gourp.funding_agencies.length; j++) {
              const ele = gourp.funding_agencies[j]
              const fund = {
                id: ele.id,
                group_id: ele.group_id,
                group_name: groupName,
                name: ele.name,
                full_name: ele.full_name,
                marker_color: ele.marker_color
              }
              fundArr.push(fund)
            }
          }
          this.totalFunding = fundArr.length
          this.tableData = fundArr.splice((this.filtersForm.page - 1) * this.filtersForm.page_size, this.filtersForm.page_size)
        })
        .catch(error => {
          console.log(error)
          this.loading = false
        })
    },
    confirmDeleteFunding (funding) {
      this.$confirm('Are you sure delete this Source of Funding?')
        .then(_ => {
          this.deleteFunding(funding)
        })
        .catch(_ => { })
    },
    deleteFunding (funding) {
      api.deleteFunding(funding.id)
        .then(res => {
          this.$notify.success('User has been successfully deleted')
          this.getTableData()
        })
        .catch(e => {
          this.$notify.error(e.message)
        })
    }
  },

  mounted () {
    this.getTableData()
  },
  created () {

  },

  watch: {

  }
}
</script>

<style scoped>
.user-management {
  display: flex;
  padding: 10px;
  flex-flow: column;
  height: 100%;
}
</style>



