<template>
  <router-link v-if="to" class="box" :to="to"><slot></slot></router-link>
  <a @click.prevent="$emit('click')" v-else class="box"><slot></slot></a>
</template>

<script>
export default {
  name: 'nav-bar-item',

  props: {
    to: {
      type: String,
      default: undefined
    }
  }
}
</script>

<style lang="css" scoped>
.nav {
  text-decoration: none;
  height: 100%;
  color: #FFFFFF;
  flex: 0 1 auto;
  min-width: 48px;
  padding: 0 10px;
  text-align: center;
  line-height: 56px;
  vertical-align: middle;

  display: flex;
  align-items: center;
  justify-content: center;
}

.nav:hover, .nav:focus {
  background-color: #1976D2;
  cursor: pointer;
}
.box {
  text-decoration: none;
  height: 100%;
  color: #FFFFFF;
  text-align: center;
  vertical-align: middle;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 1 auto;
}
.top-bar-item {
  justify-content: space-around;
  border-right: 0.5px ridge gray;;
}
</style>



