<template>
  <div class="dashboard-search-group">
    <el-row
      :gutter="6"
      justify="space-around"
      type="flex"
      class="row-bg">
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <el-input
          size="mini"
          placeholder="Project Name"
          @blur="searchChanged"
          v-model="form.construction"
          @change="searchChanged"
          clearable>
        </el-input>
      </el-col>
      <!-- <el-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
        <el-input
          size="mini"
          placeholder="Allocation Order"
          v-model="form.workOrder"
          @blur="searchChanged"
          @keyup.enter.native.prevent.stop="searchChanged"
          clearable>
        </el-input>
      </el-col> -->
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'search-form',
    data () {
      return {
        form: {
          construction: undefined,
          workOrder: ''
        }
      }
    },
    methods: {
      searchChanged () {
        if (this.form.construction === '') {
          this.form.construction = undefined
        }
        if (this.form.workOrder === '') {
          this.form.workOrder = undefined
        }
        this.$emit('searchChanged', this.form)
      }
    }
  }
</script>

<style scoped>
</style>



