<template>
<el-button 
  class="load-btn"
  type="text"
  :loading="loading"
  :style="buttonStyle"
  :class="buttonClass"
  :icon="loadGroupBtnIcon"
  @click="handleClick">
  <span v-if="text">{{ text }}</span>
</el-button>
</template>

<script>
export default {
  name: 'circle-checkbox',

  props: {
    size: {
      default: '40px'
    },

    text: {
      type: String
    },

    value: {
      type: Boolean,
      default: true
    },

    intermediate: {
      type: Boolean,
      default: false
    },

    loading: {
      type: Boolean,
      default: false
    },

    highlight: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    buttonStyle () {
      return {
        'width': this.size,
        'height': this.size
      }
    },

    buttonClass () {
      return {
        'active': this.shouldActiveLoadButton,
        'btn-highlight': this.highlight
      }
    },

    shouldActiveLoadButton () {
      return (this.loadGroupBtnIcon || this.text) && !this.loading
    },

    loadGroupBtnIcon () {
      return this.loading ? '' : this.intermediate ? 'el-icon-minus' : this.value ? 'el-icon-circle-check' : ''
    }
  },

  methods: {
    handleClick () {
      this.$emit('click')
    }
  }
}
</script>

<style lang="css" scoped>
.load-btn {
  border-radius: 50%;
  margin: auto;
  z-index: 1;
}

.btn-highlight {
  border: 1px solid var(--brand-primary);
}

.load-btn:hover, .load-btn:focus {
  border: 1px solid var(--brand-primary);
}

.active {
  background-color: var(--brand-primary);
  color: white;
}
</style>



