<template>
  <base-layer>
    <div class="project-page-content">
      <div class="project-top-actions">
        <filter-form
          :loading="loading"
          :v-model="filtersForm"
          :value="filtersForm"
          style="flex: 1"
          @change="getData"
          @search="getData"
          @reset="resetSearch"
        />

        <!--<router-link :to="{ path: 'add-project' }">-->
          <!--<el-button-->
            <!--plain-->
            <!--type="primary"-->
            <!--icon="el-icon-circle-plus-outline">-->
            <!--Create Project-->
          <!--</el-button>-->
        <!--</router-link>-->
      </div>
      <div v-if="me.hasRole(1) || me.hasRole(5) || me.hasRole(6)" style="width: 100%;">
        <el-button style="float: right" @click.stop="exportExcel">Export Excel</el-button>
      </div>
      <el-pagination
        size="mini"
        @size-change="sizeChanged"
        @current-change="pageChanged"
        :page-sizes="[5, 10, 15, 20, 50]"
        :page-size="filtersForm.page_size"
        :current-page="filtersForm.page"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalProject">
      </el-pagination>
      <list-project :projects="projects" @onDeleteAction="deleteItem" @onExportReport="exportPdf">
      </list-project>
    </div>
  </base-layer>
</template>

<script>
  import BaseLayer from '../../containers/BaseLayer'
  import ListProject from '../../containers/Projects/Projects'
  import FilterForm from './FilterForm'
  import {mapGetters, mapMutations} from 'vuex'
  import api from '@/api'
  import { cloneDeep } from 'lodash'
  import {convertUTCToLocalDate} from '@/utils/Date'

  export default {
    created () {
      this.filtersForm = cloneDeep(this.projectFilters)
      this.$store.dispatch('changeIndex', '5')
    },
    mounted () {
      this.filtersForm.order_id = this.$route.query.order_id
      this.getData()
    },
    destroyed () {
      this.filtersForm = {
        district: undefined,
        circle: undefined,
        construction_id: undefined,
        department_id: undefined,
        funding_agency_id: undefined,
        search: undefined,
        from_date: undefined,
        to_date: undefined,
        status: undefined,
        page_size: 10,
        page: 1,
        order_id: undefined
      }
      this.PROJECT_FILTERS(this.filtersForm)
    },
    data () {
      return {
        loading: false,
        totalProject: 0,
        projects: [],
        filtersForm: undefined
      }
    },
    computed: {
      ...mapGetters(['token', 'projectFilters', 'me'])
    },
    components: {
      FilterForm,
      ListProject,
      BaseLayer
    },
    methods: {
      ...mapMutations(['PROJECT_FILTERS']),
      exportPdf (itemId) {
        const token = this.token
        window.open(`/api/summary/report/${itemId}?v=${new Date().getTime()}&token=${token}`, '_blank')
      },
      exportExcel () {
        let filters = this.filtersForm
        let form = {
          search: filters.search ? filters.search : '',
          construction_id: filters.construction_id ? filters.construction_id : '',
          department_id: filters.department_id ? filters.department_id : '',
          funding_agency_id: filters.funding_agency_id ? filters.funding_agency_id : '',
          district: filters.district ? filters.district : '',
          circle: filters.circle ? filters.circle : '',
          status: filters.status ? filters.status : ''
        }
        if (filters.from_date !== undefined && filters.from_date !== null) {
          form.from_date = new Date(filters.from_date)
          form.from_date.setHours(0, 0, 0)
          form.from_date = form.from_date.toUTCString()
        }
        if (filters.to_date !== undefined && filters.to_date !== null) {
          form.to_date = new Date(filters.to_date)
          form.to_date.setHours(23, 59, 59)
          form.to_date = form.to_date.toUTCString()
        }
        if (this.$route.query.name) {
          form.search = this.$route.query.name
        }
        const token = this.token
        let fil = `search=${form.search}&status=${form.status}&circle=${form.circle}&department_id=${form.department_id}&district=${form.district}&from_date=${form.from_date}&to_date=${form.to_date}&construction_id=${form.construction_id}&funding_agency_id=${form.funding_agency_id}`
        window.open(`/api/exports/projects?v=${new Date().getTime()}&token=${token}&${fil}`, '_blank')
      },
      sizeChanged (val) {
        this.filtersForm.page_size = val
        this.filtersForm.page = 1
        this.getData()
      },
      pageChanged (val) {
        this.filtersForm.page = val
        this.getData()
      },
      resetSearch () {
        this.$router.push({ path: `projects` })
        this.filtersForm = {
          district: undefined,
          circle: undefined,
          construction_id: undefined,
          // year: new Date(),
          department_id: undefined,
          funding_agency_id: undefined,
          search: undefined,
          from_date: undefined,
          to_date: undefined,
          status: undefined,
          page_size: 10,
          page: 1,
          order_id: undefined
        }
        this.getData()
      },
      getData () {
        let filters = this.filtersForm
        let form = {
          page_size: filters.page_size,
          search: filters.search,
          construction_id: filters.construction_id,
          page: filters.page,
          department_id: filters.department_id,
          funding_agency_id: filters.funding_agency_id,
          district: filters.district,
          circle: filters.circle,
          status: filters.status,
          order_id: filters.order_id
        }
        if (filters.from_date !== undefined && filters.from_date !== null) {
          form.from_date = new Date(filters.from_date)
          form.from_date.setHours(0, 0, 0)
        }
        if (filters.to_date !== undefined && filters.to_date !== null) {
          form.to_date = new Date(filters.to_date)
          form.to_date.setHours(23, 59, 59)
        }
        if (filters.year instanceof Date) {
          form.year = filters.year.getFullYear()
        }
        if (this.$route.query.name) {
          form.search = this.$route.query.name
        }
        this.projects = []
        api.getProjects(form)
          .then(res => {
            this.projects = res.data.data.map(project => {
              project.created_at = convertUTCToLocalDate(project.created_at)
              project.updated_at = convertUTCToLocalDate(project.updated_at)
              project.name = project.order.construction.name
              project.district = project.order.construction.area.dist_name
              project.circle = project.order.construction.area.name
              return project
            })
            this.totalProject = res.data.total
            if (this.totalProject === 0) {
              this.$notify({
                title: 'MIS not found',
                type: 'warning'
              })
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      deleteItem (itemId) {
        this.$confirm('Are you sure delete item?')
          .then(_ => {
            api.deleteProjects(itemId)
              .then(_ => {
                this.$notify({
                  title: 'Success',
                  type: 'success'
                })
                this.getData()
              })
              .catch(error => {
                console.log(error)
              })
          })
          .catch(_ => { })
      }
    }
  }
</script>

<style lang="css" scoped>
  .project-page-content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    padding: 10px;
  }
  .project-top-actions {
    display: inline-flex;
  }
</style>



