import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
import Login from '../pages/Login'
import Register from '../pages/Register'
import Home from '../pages/Dashboard/Home'
import Dashboard from '../pages/DashboardV2'
import UserRegistration from '../pages/UsersRegistration/UserRegistrationPage'
import UserManagement from '../pages/UsersManagement/UserManagementPage'
import Imagery from '../pages/ImageryPage'
import Projects from '../pages/Projects/ProjectsPage'
import EditProject from '../pages/Projects/EditProjectPage'
import ProjectDetails from '../pages/Projects/ProjectDetailsPage'
import CreateProject from '../pages/Projects/CreateProjectPage'
import FundingManagement from '../pages/FundingManagement/FundingManagementPage'
import Help from '../pages/Help'
import NotificationManagement from '../pages/NotificationsManagement/NotiManagementPage'
import KpiManagement from '../pages/KpiManagement/KpiManagement'

Vue.use(Router)

function checkAuth () {
  return new Promise((resolve, reject) => {
    const token = store.state.auth.token
    if (!token) {
      reject(new Error('Not login yet'))
    } else {
      // Refresh browser or open new will have only token in localStorage but not state.auth.me
      // So it should fetch user info from server

      // Always check token before go into page
      // Because token maybe has expired
      store.dispatch('getMe')
        .then(me => resolve())
        .catch(error => reject(error instanceof Error ? error : new Error(error.message)))
    }
  })
}

function requireAuth (to, from, next) {
  checkAuth()
    .then(() => next())
    .catch(error => {
      console.error(error)
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      })
    })
}

function requireAllPermissions (to, from, next, permissions) { // eslint-disable-line
  checkAuth()
    .then(() => {
      if (store.state.auth.me.hasAllPermissions(permissions)) {
        next()
      } else {
        const msg = 'You do not have permission to access route'
        alert(msg)
        next(new Error(msg))
      }
    })
    .catch(error => {
      console.error(error)
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      })
    })
}

export default new Router({
  routes: [
    { path: '/', component: Home, beforeEnter: requireAuth },
    { path: '/home', component: Home, beforeEnter: requireAuth },
    { path: '/dashboard', component: Dashboard, beforeEnter: requireAuth },
    { path: '/help', component: Help, beforeEnter: requireAuth },
    { path: '/login', component: Login },
    { path: '/register', component: Register },
    { path: '/users/registration', component: UserRegistration, beforeEnter: requireAuth },
    { path: '/users/management', component: UserManagement, beforeEnter: requireAuth },
    { path: '/imagery', component: Imagery, beforeEnter: requireAuth },
    { path: '/projects', component: Projects, beforeEnter: requireAuth },
    { path: '/projects/:id', component: ProjectDetails, beforeEnter: requireAuth },
    { path: '/funding', component: FundingManagement, beforeEnter: requireAuth },
    { path: '/projects/:id/editor', component: EditProject, beforeEnter: requireAuth },
    { path: '/add-project', component: CreateProject, beforeEnter: requireAuth },
    { path: '/notifications/management', component: NotificationManagement, beforeEnter: requireAuth },
    { path: '/kpi', component: KpiManagement, beforeEnter: requireAuth }
  ]
})



