<template>
    <div  class="user-registration">
      <div style="width: 100%; display: flex;margin-bottom: 20px;">
<!--        <el-form label-width="50px" style="flex: 1">-->
<!--          <el-form-item label="Search">-->
            <div>
              <el-select
                style="width: 600px"
                v-model="filtersForm.departments"
                multiple
                filterable
                :filter-method="filterDep"
                @focus="resetDep"
                @change="getTableData"
                collapse-tags
                size="mini"
                placeholder="Select departments">
                <el-option
                  v-for="item in departments"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
              <br>
              <el-input  size="mini" style="width: 600px; margin-top: 10px" v-model="filtersForm.search" placeholder="Search user (name, mobile, email)" @keyup.enter.native.prevent.stop="getTableData"></el-input>
              <el-button size="mini" style="height: 28px;margin-left: 10px;" :autofocus="true" type="primary" icon="el-icon-search" @click="getTableData" plain>Search</el-button>
              <el-button size="mini" style="height: 28px" type="primary" icon="el-icon-refresh" @click="resetSearch" plain>Reset</el-button>
            </div>

<!--          </el-form-item>-->
<!--        </el-form>-->
        <div style="position: fixed; right: 2%;" >
          <el-button type="primary" :disabled="selectedUsers === []" @click="deleteSelected" plain>Delete</el-button>
        </div>
      </div>
      <el-pagination
        size="mini"
        @size-change="sizeChanged"
        @current-change="pageChanged"
        :page-sizes="[10, 15, 20, 50]"
        :page-size="filtersForm.page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalUser">
      </el-pagination>
      <el-table size="mini" @selection-change="onChangeSelected" :data="tableData" style="overflow-y: auto">
        <el-table-column
            type="selection"
            width="55">
        </el-table-column>
        <el-table-column
            prop="created_at"
            label="Created at"
            min-width="140">
        </el-table-column>
        <el-table-column
            prop="updated_at"
            label="Updated at"
            min-width="140">
        </el-table-column>
        <el-table-column
            prop="name"
            label="Name"
            min-width="140">
        </el-table-column>
        <el-table-column
            prop="mobile"
            label="Mobile"
            min-width="140">
        </el-table-column>
        <el-table-column
          label="Department"
          min-width="200">
          <template slot-scope="props">
            {{ props.row.department ? props.row.department.name : '' }}
          </template>
        </el-table-column>
        <el-table-column
            prop="email"
            label="Email"
            min-width="180">
        </el-table-column>
        <el-table-column
            label="Operators"
            min-width="140">
          <template slot-scope="props">
            <el-button-group>
              <el-button size="mini" type="danger" @click="deleteAppUserID(props.row)">Delete</el-button>
              <el-tooltip content="Set new password" placement="top-end">
                <el-button size="mini" @click="showSetPasswordForm(props.row)" type="primary" icon="el-icon-key"></el-button>
              </el-tooltip>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
      <set-password-user ref="setPasswordForm"/>
    </div>
</template>

<script>
import api from '@/api'
import { filterArr } from '@/utils/Array'
import SetPasswordUser from '../UsersManagement/SetPasswordUser'

export default {
  name: 'app-users',
  components: {SetPasswordUser},
  data () {
    return {
      activeIndex: '1',
      tableData: [],
      selectedUsers: [],
      departments: [],
      _departments: [],
      filtersForm: {
        department_id: undefined,
        search: undefined,
        page_size: 10,
        page: 1,
        role: 3
      },
      totalUser: null
    }
  },
  methods: {
    showSetPasswordForm (user) {
      this.$refs.setPasswordForm.setUser(user)
    },
    resetDep () {
      this.departments = this._departments
    },
    filterDep (value) {
      this.departments = filterArr(value, this._departments, 'name')
    },
    onChangeSelected (val) {
      this.selectedUsers = val
    },
    sizeChanged (val) {
      this.filtersForm.page_size = val
      this.filtersForm.page = 1
      this.getTableData()
    },
    pageChanged (val) {
      this.filtersForm.page = val
      this.getTableData()
    },
    resetSearch () {
      this.filtersForm = {
        search: undefined,
        page_size: 10,
        page: 1,
        role: 3
      }
      this.getTableData()
    },
    getTableData () {
      this.tableData = []
      api.getUsers(this.filtersForm)
        .then(res => {
          this.tableData = res.data.data
          this.totalUser = res.data.total
        })
        .catch(error => {
          console.log(error)
        })
    },
    deleteAppUserID (item) {
      this.$confirm('Are you sure delete user?')
        .then(_ => {
          this.deleteUser(item.id)
        })
        .catch(_ => { })
    },

    deleteSelected () {
      this.$confirm('Are you sure delete selected users?')
        .then(_ => {
          for (let item of this.selectedUsers) {
            this.deleteUser(item.id)
          }
        })
        .catch(_ => { })
    },
    deleteUser (userId) {
      api.deleteUser(userId).then(res => {
        this.$notify({
          type: 'success',
          message: 'Deleted success'
        })
        this.getTableData()
      }).catch(e => {
        this.$notify({
          type: 'error',
          message: 'Deleted failed'
        })
      })
    }
  },
  mounted () {
    this.getTableData()
    api.getDepartments()
      .then(res => {
        this.departments = res.data
        this._departments = res.data
      })
      .catch(err => console.log(err))
  }
}
</script>

<style scoped>

.user-registration {
  display: flex;
  flex-flow: column;
  height: 100%;
  width: 100%;
}
</style>



