export const fixCurrency = (cost) => {
  if (cost === 1) {
    return cost.toString() + ' Lakh'
  } else {
    return cost.toString() + ' Lakhs'
  }
}

export const dcToDeg = (val) => {
  if (val === 0) {
    return 0
  }
  return Math.floor(Math.abs(val))
}

export const dcToMin = (val) => {
  if (val === 0) {
    return 0
  }
  return Math.floor((Math.abs(val) - Math.floor(Math.abs(val))) * 60)
}

export const dcToSec = (val) => {
  if (val === 0) {
    return 0
  }
  return (Math.abs(val) - dcToDeg(val) - dcToMin(val) / 60) * 3600
}

export const getQuarter = (d) => {
  d = d || new Date()
  let quarters = ['4th quarter (Jan - March)', '1st quarter (April - June)', '2nd quarter (July - Sept)', '3rd quarter (Oct - Dec)']
  return quarters[Math.floor(d.getMonth() / 3)]
}

export const getFundingError = (form) => {
  let error = {
    valid: true,
    alert: ''
  }
  if (!form.group_id) {
    error.valid = false
    error.group_id = 'Group is required'
  }
  if (!form.name || form.name === '') {
    error.valid = false
    error.name = 'Name is required'
  }
  if (!form.full_name || form.full_name === '') {
    error.valid = false
    error.full_name = 'Full name is required'
  }
  if (!form.marker_color || form.marker_color === '') {
    error.valid = false
    error.marker_color = 'Marker color is required'
  }
  return error
}

export const getFinancialProgressError = (form, amount, percent, financialProgress) => {
  let error = {
    valid: true,
    alert: ''
  }
  if (!form.amount) {
    error.valid = false
    error.amount = `Amount is required!`
  } else if (form.amount <= 0) {
    error.valid = false
    error.amount = `Amount must be greater than 0!`
  }
  if (!form.sanction_date) {
    error.valid = false
    error.sanction_date = `Date of Sanction is required!`
  }
  if (!form.release_date) {
    error.valid = false
    error.release_date = `Date of Release is required!`
  }
  let tmpAmount = parseFloat(amount) * parseFloat(percent) / 100
  let tmp = tmpAmount
  for (let i = 0; i < financialProgress.length; i++) {
    const ele = financialProgress[i]
    if (form.index === i) {
      tmp -= form.amount
    } else {
      tmp -= ele.amount
    }
  }
  if (form.index === -1) {
    tmp -= form.amount
  }
  if (tmp < 0) {
    error.valid = false
    error.financial_progresses = `Total amount of financial progresses must be less than ${percent}% estimated cost - ${tmpAmount}, got total value ${tmpAmount - tmp}!`
  }
  if (form.uc_status === 'Submitted') {
    if (!form.uc_documents || form.uc_documents.length === 0) {
      error.valid = false
      error.uc_documents = `Must have utilization certificate!`
    }
    if (!form.uc_date) {
      error.valid = false
      error.uc_date = `Certificate must have date of submission!`
    }
  }
  return error
}

export const getPhysicalProgressError = (form, physicalProgress) => {
  let error = {
    valid: true,
    alert: ''
  }
  let prePhysicalPercent = 0
  let preFinancialPercent = 0
  for (let i = 0; i < physicalProgress.length; i++) {
    const ele = physicalProgress[i]
    if (ele.report_id === form.report_id) {
      if (form.physical_percent < prePhysicalPercent) {
        error.valid = false
        error.physical_percent = `Physical percent is not valid!`
      }
      if (form.financial_percent < preFinancialPercent) {
        error.valid = false
        error.financial_percent = `Financial percent is not valid!`
      }
    }
    prePhysicalPercent = ele.physical_percent
    preFinancialPercent = ele.financial_percent
  }
  if (form.status === 'Completed' || form.physical_percent === 100) {
    if (!form.cc_documents || form.cc_documents.length === 0) {
      error.valid = false
      error.cc_documents = `Physical progresses completed must have completion certificate!`
    }
  }
  if (form.cc_status === 'Submitted') {
    if (!form.cc_documents || form.cc_documents.length === 0) {
      error.valid = false
      error.cc_documents = `Must have completion certificate!`
    }
    if (!form.cc_date) {
      error.valid = false
      error.cc_date = `Certificate must have date of submission!`
    }
  }
  return error
}

export const getError = (form) => {
  let error = {
    valid: true,
    alert: ''
  }
  if (form.estimated_cost <= 0) {
    error.valid = false
    error.estimated_cost = 'Estimated cost must be greater than 0'
  }
  if (form.central_share + form.state_share < 100) {
    error.valid = false
    error.central_share = 'Share ratio required'
    error.state_share = 'Share ratio required'
  }
  if (form.central_share + form.state_share < 100) {
    error.valid = false
    error.central_share = 'Share ratio required'
    error.state_share = 'Share ratio required'
  }

  if (form.central_share > 0 && form.central_funding_agencies.length === 0) {
    error.valid = false
    error.central_funding_agencies = 'Source of Funding - Central required'
  }

  if (form.state_share > 0 && form.state_funding_agencies.length === 0) {
    error.valid = false
    error.state_funding_agencies = 'Source of Funding - State required'
  }

  if (!form.project_gist || form.project_gist === '') {
    error.valid = false
    error.project_gist = 'Project gist required'
  }

  if (!form.remarks || form.remarks === '') {
    error.valid = false
    error.remarks = 'Remarks gist required'
  }
  if (form.financial_progresses) {
    let er = form.financial_progresses.find(val => {
      return parseFloat(val.amount) > form.estimated_cost
    })
    if (er) {
      error.valid = false
      error.financial_progresses = `Amount of financial progresses must be less than estimated cost - ${form.estimated_cost}, got value ${er.amount}!`
    }
  }
  if (form.physical_progresses) {
    let prePhysicalPercent = 0
    let preFinancialPercent = 0
    for (let i = 0; i < form.physical_progresses.length; i++) {
      const ele = form.physical_progresses[i]
      if (ele.physical_percent < prePhysicalPercent) {
        error.valid = false
        error.physical_percent = `Physical percent is not valid!`
      }
      if (ele.financial_percent < preFinancialPercent) {
        error.valid = false
        error.financial_percent = `Financial percent is not valid!`
      }
      prePhysicalPercent = ele.physical_percent
      preFinancialPercent = ele.financial_percent
    }
  }
  return error
}



