<template>
  <el-table
      style="overflow-y: auto"
      size="mini"
      :data="projects">
    <el-table-column
      label="ID"
      width="110">
      <template slot-scope="scope" >
        {{ scope.row.order.construction.id }}
      </template>
    </el-table-column>
    <el-table-column
        sortable
        width="110"
        prop="created_at"
        label="Created At">
    </el-table-column>
    <el-table-column
        sortable
        width="110"
        prop="updated_at"
        label="Updated At">
    </el-table-column>
    <el-table-column
        sortable
        min-width="200"
        prop="name"
        label="Project Name">
    </el-table-column>

    <el-table-column
      prop="district"
      label="District"
      width="110"
      sortable>
    </el-table-column>
    <el-table-column
        prop="circle"
        label="Circle"
        width="110"
        sortable>
    </el-table-column>
    <el-table-column
        label="Estimated Cost"
        min-width="110">
        <template slot-scope="props" >
          {{ fixCurrency(props.row.estimated_cost)}}
        </template>
    </el-table-column>
    <!-- <el-table-column
        label="Central Share"
        prop="central_share"
        min-width="110">
    </el-table-column>
    <el-table-column
      prop="state_share"
      label="State Share"
      min-width="110">
    </el-table-column> -->
    <el-table-column
        label="Status"
        prop="order"
        min-width="90">
      <template slot-scope="scope" >
        {{ scope.row.order.report.physical_progress.status }}
      </template>
    </el-table-column>
    <el-table-column
      prop="state_share"
      label="Physical progress (%)"
      min-width="155">
      <template slot-scope="scope" >
        {{ scope.row.order.report.physical_progress.physical_percent }}
      </template>
    </el-table-column>
    <el-table-column
      prop="uuid"
      label="Financial progress (%)"
      min-width="155">
      <template slot-scope="scope" >
        {{ scope.row.order.report.physical_progress.financial_percent }}
      </template>
    </el-table-column>
    <el-table-column
        min-width="160"
        label="Actions">
      <template slot-scope="scope" >
        <v-btn-toggle>
          <v-btn
            title="Documents"
            @click="showItem(scope.row)"
            fab flat dark small color="primary">
            <v-icon dark>photo_library</v-icon>
          </v-btn>
          <v-btn
            title="Download report"
            @click="$emit('onExportReport', scope.row.id)"
            fab flat dark small color="primary">
            <v-icon dark>cloud_download</v-icon>
          </v-btn>
          <v-btn
            v-if="!(me.hasRole(6) || me.hasRole(5) || me.hasRole(4))"
            title="Edit"
            @click="$router.push({ path: 'projects/' + scope.row.id + '/editor' })"
            fab flat dark small color="primary">
            <v-icon dark>edit</v-icon>
          </v-btn>
          <v-btn
            v-if="me.hasRole(1)"
            title="Delete"
            @click="$emit('onDeleteAction', scope.row.id)"
            fab flat dark small color="error">
            <v-icon dark>delete</v-icon>
          </v-btn>
        </v-btn-toggle>
      </template>
    </el-table-column>
    <v-dialog
      v-if="itemShow"
      v-model="ccDialog"
      width="500">
      <v-card>
        <v-card-text>
          <div style="font-weight: bold">Financial Progresses</div>
          <div v-for="item of itemShow.financial_progresses">
            <div>{{ 'Instalment: ' + item.instalment + ' - Amount: ' + item.amount}}</div>
            <div style="margin-left: 30px">
              <div>{{ 'Sanction Date: ' + item.sanction_date + ' | Release Date: ' + item.release_date }}</div>
              <div>UC Documents</div>
              <div style="display: flex; flex-flow: column">
                <a v-for="doc of item.uc_documents" target="_blank" :href="doc.url">{{ doc.name}}</a>
              </div>
            </div>
          </div>
          <div style="font-weight: bold">Physical Progresses</div>
          <div v-for="item of itemShow.order.reports">
            <div>{{ getStatus(item) }}</div>
            <div style="margin-left: 30px">
              <div>CC Documents</div>
              <div style="display: flex; flex-flow: column">
                <a v-for="doc of item.physical_progress.cc_documents" target="_blank" :href="doc.url">{{ doc.name}}</a>
              </div>
              <div>Photos</div>
              <div style="display: flex; flex-flow: column">
                <a target="_blank" v-if="item.image1" :href="item.image1">Image 1</a>
                <a target="_blank" v-if="item.image2" :href="item.image2">Image 2</a>
                <a target="_blank" v-if="item.image3" :href="item.image3">Image 3</a>
                <a target="_blank" v-if="item.image4" :href="item.image4">Image 4</a>
              </div>
            </div>
          </div>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            flat
            @click="ccDialog = false">
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </el-table>
</template>

<script>
import { mapGetters } from 'vuex'
import { getQuarter } from '@/utils/projectUtils'

export default {
  name: 'list-project',
  components: {
  },
  data () {
    return {
      itemShow: undefined,
      ccDialog: false
    }
  },
  props: {
    projects: Array
  },
  computed: {
    ...mapGetters([
      'me'
    ]),
    isDistrictUser () { return this.me.hasRole(4) }
  },
  methods: {
    getStatusText (report) {
      return `${report.physical_progress.status}: ${report.physical_progress.physical_percent} / ${report.physical_progress.financial_percent}`
    },
    showItem (item) {
      this.itemShow = item
      this.ccDialog = true
    },
    getStatus (item) {
      return 'Year: ' + item.local_time.substring(0, 4) + ' - Quarter: ' + getQuarter(new Date(item.local_time)) + ' - Status: ' + item.physical_progress.status
    },
    fixCurrency (str) {
      let totalText = ''
      let val = str
      if (val === 1) {
        totalText = val.toString() + ' Lakh'
      } else {
        totalText = val.toString() + ' Lakhs'
      }
      return totalText
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
</style>



