<template>
  <div class="imagery-view">
    <div class="imagery-sidebar">
      <el-menu class="sidebar" style="display: flex; flex-direction: column">
        <li class="imagery-text">Quick Fly Camera</li>
        <li class="imagery-search-container">
          <div class="imagery-search-group">
            <el-select size="mini"  class="imagery-search-item" 
              @change="getCircles"
              v-model="form.district"
              filterable
              :filter-method="filterDis"
              @focus="resetDis"
              placeholder="District Name">
              <el-option
                  v-for="item of districts"
                  :key="item.dist_name"
                  :label="item.dist_name"
                  :value="item.dist_name">
              </el-option>
            </el-select>
            <el-select size="mini" class="imagery-search-item" 
              @change="getImages"
              v-model="form.circle"
              filterable
              :filter-method="filterCir"
              @focus="resetCir"
              placeholder="Circle Name">
              <el-option
                  v-for="item of circles"
                  :key="item.name"
                  :label="item.name"
                  :value="item">
              </el-option>
            </el-select>
          </div>
        </li>
        <li class="imagery-text">Sample Imagery</li>
        <li class="imagery-list" style="flex-grow: 1;overflow-y: scroll; margin-bottom: 60px">
          <div style="height: 100%">
            <image-item v-for="image of images" :key="image.id" :image="image" :hasCompareMode="form.hasCompareActive"
                        @flyCameraClick="handleFlyCamera(image)"
                        @activeChanged="val => handleActiveChange(image, val)"
                        @activeLeftChanged="val => handleLeftActiveChange(image, val)"
                        @activeRightChanged="val => handleRightActiveChange(image, val)"/>
          </div>

        </li>
      </el-menu>
    </div>
    <div class="cesium-map-view">
      <div  class="compare-slider">
        <compare-layers-slider
            @splitPositionChanged="updateSplitPosition"
            :compareMode="form.hasCompareActive"
            :leftCompareContent="form.leftItems"
            :rightCompareContent="form.rightItems" >
        </compare-layers-slider>
      </div>
      <div id="cesium-map" style="width: 100%; height: 100%">
      </div>
      <el-button-group class="zoom-button">
        <el-button size="small"  @click="zoomIn" icon="el-icon-plus"></el-button>
        <el-button size="small" @click="zoomOut" icon="el-icon-minus"></el-button>
      </el-button-group>
    </div>
    <div class="switch-compare" >
      <div style="color: white; margin-right: 6px">Compare</div>
      <el-switch @change="changeStateCompareMode" v-model="form.hasCompareActive" :name="'Compare'"/>
    </div>
  </div>
</template>

<script>
