          tile_url: 'https://arunachalmonitoring.com/storage/assets/NITAP_jote_21st_Dec_11/{z}/{x}/{-y}.png',
          isActive: false,
          isLeft: false,
          isRight: false,
          layer_type: {
            name: 'tiles'
          },
          side: '',
          bounds: {
            northeast: {
              latitude: 27.04764227470518,
              longitude: 93.46885114178539
            },
            southwest: {
              latitude: 27.03383963199206,
              longitude: 93.43916501389216
            }
          }
        },
        {
          id: 16,
          name: 'State Assembly Itanagar',
          date: '2017-02-03',
          tile_url: 'https://arunachalmonitoring.com/storage/assets/State_Assembly_Itanagar_3rd_Feb_17/{z}/{x}/{-y}.png',
          isActive: false,
          isLeft: false,
          isRight: false,
          layer_type: {
