          },
          side: '',
          bounds: {
            northeast: {
              latitude: 27.09108826677439,
              longitude: 93.62346166558520
            },
            southwest: {
              latitude: 27.08605958197279,
              longitude: 93.61607353132038
            }
          }
        },
        {
          id: 17,
          name: 'State Assembly Itanagar',
          date: '2014-09-31',
          tile_url: 'https://arunachalmonitoring.com/storage/assets/State_Assembly_Itanagar_31st_Oct_14/{z}/{x}/{-y}.png',
          isActive: false,
          isLeft: false,
          isRight: false,
          layer_type: {
            name: 'tiles'
          },
          side: '',
          bounds: {
            northeast: {
              latitude: 27.09108826677439,
              longitude: 93.62346166558520
            },
            southwest: {

              latitude: 27.08605958197279,
              longitude: 93.61607353132038
            }
          }
        },
        {
          id: 18,
          name: 'State Assembly Itanagar',
          date: '2009-10-23',
          tile_url: 'https://arunachalmonitoring.com/storage/assets/State_Assembly_Itanagar_23rd_Nov_09/{z}/{x}/{-y}.png',
          isActive: false,
          isLeft: false,
          isRight: false,
          layer_type: {
            name: 'tiles'
          },
          side: '',
          bounds: {
            northeast: {
              latitude: 27.09108826677439,
              longitude: 93.62346166558520
            },
            southwest: {

