              longitude: 93.61607353132038
            }
          }
        },
        {
          id: 1,
          name: 'Landsat5',
          date: '2011-02-04',
          tile_url: 'https://arunachalmonitoring.com/storage/assets/Landsat5_20110204/{z}/{x}/{-y}.png',
          layer_type: {
            name: 'tiles'
          },
          isActive: false,
          isLeft: false,
          isRight: false,
          side: '',
          bounds: {
            northeast: {
              latitude: 29.80981615597482,
              longitude: 96.14124226113076
            },
            southwest: {
              latitude: 27.89488427959322,
              longitude: 93.68386458702764
            }
          }
        },
        {
          id: 2,
          name: 'Landsat8',
          date: '2017-02-04',
          tile_url: 'https://arunachalmonitoring.com/storage/assets/Landsat8_20170204/{z}/{x}/{-y}.png',
          isActive: false,
          isLeft: false,
          isRight: false,
          layer_type: {
            name: 'tiles'
          },
          side: '',
          bounds: {
            northeast: {
              latitude: 29.92048752061753,
              longitude: 96.10744248236790
            },
            southwest: {
              latitude: 27.79829546372920,
              longitude: 93.74112995466272
            }
          }
        },
        {
          id: 3,
          name: 'Sentinel2',
          date: '2017-01-19',
          tile_url: 'https://arunachalmonitoring.com/storage/assets/Sentinel2_20170119/{z}/{x}/{-y}.png',
          isActive: false,
          isLeft: false,
          isRight: false,
          layer_type: {
            name: 'tiles'
          },
          side: '',
          bounds: {
            northeast: {
              latitude: 28.92432859077432,
              longitude: 95.15194300707363
            },
            southwest: {
              latitude: 27.92060128047384,
              longitude: 94.01645695200986
            }
          }
        }
      ],
      form: {
        compareSplitValue: 0.5,
        hasCompareActive: false,
        leftItems: [],
        rightItems: [],
        district: undefined,
        circle: undefined
      }
    }
  },
  methods: {
    resetDis () {
      this.districts = this._districts
    },
    resetCir () {
      this.circles = this._circles
    },
    filterDis (value) {
      this.districts = filterArr(value, this._districts, 'dist_name')
    },
    filterCir (value) {
      this.circles = filterArr(value, this._circles, 'name')
    },
    initData () {
      api.getDistricts()
        .then(res => {
          this.districts = res.data
          this._districts = res.data
        })
      this.initMap()
    },
    getCircles () {
      this.form.circle = undefined
      api.getCircles(this.form.district)
        .then(res => {
          this.circles = res.data
          this._circles = res.data
        })
    },
    getImages (val) {
      this.form.circle = val.name
      if (this._dataSource) {
        this._map.dataSources.removeAll(true)
      }
      if (val.geojson) {
        this._dataSource = Cesium.GeoJsonDataSource.load({
          type: 'Feature',
          geometry: JSON.parse(val.geojson)
        }, {
          fill: Cesium.Color.TRANSPARENT,
          strokeWidth: 5
        })
        this._map.dataSources.add(this._dataSource)
        this._map.zoomTo(this._dataSource)
      }
    },
    initMap () {
      Cesium.BingMapsApi.defaultKey = 'Am31D9IqI1bdAefQv_p1hakt5ageoOFtgG7W06-_TRWl1oLxfvbBF-AwR9WdVZe-'
      Cesium.Ion.defaultAccessToken = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiI0ZDNiMjJiNy05MjQ5LTQzNTMtOWQxOS0zMTBiMDA3YmZjMTMiLCJpZCI6MTQyMCwic2NvcGVzIjpbImFzciIsImdjIl0sImlhdCI6MTUzODk2ODExOX0.5-h0LaJySf1aIr7Gd32r-LXM_0TZT3akr0SWqqTnWF0'
      // let credit = new Cesium.Credit({
      //   text: 'ARUNACHAL PRADESH',
      //   link: 'http://www.arunachalmonitoring.com/'
      // })
      this._map = new Cesium.Viewer('cesium-map', {
        timeline: false,
        animation: false,
        scene3DOnly: false,
        navigationHelpButton: false,
        navigationInstructionsInitiallyVisible: false
      })
      // this._map.scene.frameState.creditDisplay.destroy()
      // this._map.scene.frameState.creditDisplay.addDefaultCredit(credit)
      // let credit2 = this._map.scene.frameState.creditDisplay._defaultImageCredits[0]
      // this._map.scene.frameState.creditDisplay.removeDefaultCredit(credit2)
      // console.log(this._map.scene.frameState.creditDisplay)
      this._compareModeController = new CompareModeController(this._map)
      this._imageLayers = SatelliteImagesController.newInstance(this._map)
    },
    changeStateCompareMode () {
      this._imageLayers.removeImages(this.images)
      if (this.form.hasCompareActive) {
        this._compareModeController.active(0.5)
      } else {
        this._compareModeController.deactive()
        this.form.leftItems = []
        this.form.rightItems = []
        for (let image of this.images) {
          image.isActive = false
          image.isLeft = false
          image.isRight = false
          image.side = ''
        }
      }
    },
    zoomOut () {
      let currentMagnitude = this._map.camera.getMagnitude()
      if (currentMagnitude > 8000000) {
        this._map.camera.zoomOut(currentMagnitude / 20)
      } if (currentMagnitude) {
        this._map.camera.zoomOut(currentMagnitude / 50)
      }
    },
    zoomIn () {
      let currentMagnitude = this._map.camera.getMagnitude()

      if (currentMagnitude > 8000000) {
        this._map.camera.zoomIn(currentMagnitude / 10)
      } if (currentMagnitude > 6500000) {
        this._map.camera.zoomIn(currentMagnitude / 50)
      } else {
        return
      }
    },
    handleFlyCamera (image) {
      let bounds = image.bounds
      this._map.camera.flyTo({
        destination: Cesium.Rectangle.fromDegrees(bounds.southwest.longitude, bounds.southwest.latitude, bounds.northeast.longitude, bounds.northeast.latitude)
      })
    },
    handleActiveChange (image, val) {
      image.isActive = val
      if (!this.form.hasCompareActive) {
        if (val) {
          this._imageLayers.addImages([image])
        } else {
          this._imageLayers.removeImages([image])
        }
      }
    },
    handleLeftActiveChange (image, val) {
      image.isLeft = !image.isLeft
      if (this.form.hasCompareActive) {
        if (image.isLeft) {
          this._compareModeController.addCompareLayers('left', [image])
          this.form.leftItems.push(image.date)
        } else {
          this._compareModeController.removeCompareLayers('left', [image])
          const index = this.form.leftItems.indexOf(image.date)
          this.form.leftItems.splice(index, 1)
        }
      }
    },
    handleRightActiveChange (image, val) {
      image.isRight = !image.isRight
      if (this.form.hasCompareActive) {
        if (image.isRight) {
          this._compareModeController.addCompareLayers('right', [image])
          this.form.rightItems.push(image.date)
        } else {
          this._compareModeController.removeCompareLayers('right', [image])
          const index = this.form.rightItems.indexOf(image.date)
          this.form.rightItems.splice(index, 1)
        }
      }
    },
    updateSplitPosition (val) {
      this._compareModeController.active(val)
    }
  },
  mounted () {
    this.initData()
  }
}
</script>

<style scoped>

.imagery-view {
  width: 100%;
  position: relative;
  height: 100%;
  display: flex;
}
.imagery-sidebar {
  width: 300px;
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
}
.sidebar {
  border: transparent;
  width: 300px;
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #324157;
}

.imagery-search-container {
  border-bottom:1px ridge gray;
}

.imagery-search-group {
  width: 100%;
  padding: 6px;
  display: flex;
  flex-flow: row;
  overflow-x: hidden;
  overflow-y: hidden;
}

.imagery-search-item {
  width: 100%;
  margin: 4px;
}

.imagery-text {
  color: gray;
  margin : 10px 0 0 0;
  padding: 0px 10px;
}


.cesium-map-view {
  position: relative;
  width: 100%;
  height: 100%;
}

.zoom-button {
  position: absolute;
  z-index: 1002;
  left: 6px;
  top: 6px;
}
.switch-compare {
  display: inline-flex;
  position: absolute;
  left: 20px;
  bottom: 20px;
}

.compare-slider {
  width: 100%;
  height: 100%;
  align-items: center;
  position: absolute;
  left: 0px;
  top: 0px;
}

</style>



