<template>
  <v-dialog title="Add Source of Funding"  max-width="500px" v-model="dialogVisible">
    <el-button slot="activator" size="mini" type="primary" icon="el-icon-plus" plain>add Source of Funding</el-button>
    <v-card>
      <v-card-title>
        <span class="headline">Add Source of Funding</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-lg>
          <v-layout wrap>
            <v-flex xs12>
              <v-select
                v-model="form.group_id"
                :items="fundingSources"
                :messages="errors.group_id"
                label="Select Funding Group"
                item-value="id"
                item-text="name"/>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                v-model="form.name"
                :messages="errors.name"
                label="Name"/>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                v-model="form.full_name"
                :messages="errors.full_name"
                label="Full Name"/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" flat @click="dialogVisible = false">Cancel</v-btn>
        <v-btn
          color="blue darken-1" flat
          :loading="loading"
          @click="submit">
          Submit
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  import api from '@/api'

  export default {
    name: 'add-funding-source',

    data () {
      return {
        loading: false,
        dialogVisible: false,
        errors: {},
        form: {
          group_id: undefined,
          name: '',
          full_name: '',
          src: 'create_by_user'
        }
      }
    },
    props: {
      fundingSources: {
        type: Array,
        required: true
      }
    },
    methods: {
      submit () {
        this.loading = true
        this.errors = {}
        api.addFundingAgency(this.form)
          .then(res => {
            this.$notify.success({
              title: 'New source of funding has been successfully created'
            })
            this.loading = false
            this.form = {
              group_id: undefined,
              name: '',
              full_name: '',
              src: 'create_by_user'
            }
            this.dialogVisible = false
            this.$emit('onUpdate', res)
          })
          .catch(error => {
            this.$notify.error({
              title: error.message
            })
            if (error.status_code === 422) {
              this.errors = error.errors
            }
            this.loading = false
          })
      }
    }
  }
</script>

<style scoped>
</style>



