<template>
  <div class="image-container">
    <div>
      <circle-checkbox
          :intermediate="hasCompareMode"
          :text="image.side"
          :value="image.isActive"
          :highlight="highlight"
          @click="handleLoadGroupBtnClick">
      </circle-checkbox>
    </div>
    <div style="margin-left: 6px; flex: 1">
      <div style="color: darkgrey; font-size: x-small">
        {{ image.date }}
      </div>
      <div style="color: whitesmoke">
        {{ image.name }}
        <el-button type="text" @click="$emit('flyCameraClick')" class="el-icon-location-outline"></el-button>
      </div>
    </div>
    <div v-if="hasCompareMode" class="check-box-group">
      <el-checkbox @change="val => $emit('activeLeftChanged', val)" style="flex: 0" label="L" :checked="image.isLeft" size="mini"/>
      <el-checkbox @change="val => $emit('activeRightChanged', val)" style="margin: 0" label="R" :checked="image.isRight" size="mini"/>
    </div>
  </div>
</template>

<script>
  import CircleCheckbox from '../../../components/CircleCheckbox'

  export default {
    components: { CircleCheckbox },
    name: 'image-item',

    data () {
      return {
        checkboxLeft: false,
        checkboxRight: false,
        divider: {
          element: undefined
        },
        containerRect: {
          left: 0,
          width: 0
        }
      }
    },

    props: {
      hasCompareMode: {
        type: Boolean,
        default: false
      },
      image: {
        type: Object
      },
      highlight: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      isActive: {
        set (val) {
          this.$emit('activeChanged', val)
        },
        get () {
          return this.image.isActive | false
        }
      }
    },
    methods: {
      handleLoadGroupBtnClick () {
        if (this.hasCompareMode && this.isActive) {
          this.isActive = false
        } else {
          this.isActive = !this.isActive
        }
      }
    },

    mounted () {
    }
  }
</script>

<style lang="css" scoped>
  .image-container {
    padding: 16px;
    display: inline-flex;
    align-items: center;
    width: 100%;
    border-bottom: 1px ridge #283446;
  }

  .check-box-group {
    display: flex;
    flex-flow: column;
    align-items: center;
  }
</style>



