<template>
  <v-dialog title="Create User"  max-width="800px" v-model="dialogVisible">
    <v-card>
      <v-card-title>
        <span class="headline">SET USER PASSWORD</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-lg>
          <v-layout wrap>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="lock"
                type="password"
                v-model="form.password"
                :messages="errors.password"
                :counter="64"
                label="Password"/>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="lock"
                type="password"
                v-model="form.password_confirmation"
                label="Password confirmation"/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" flat @click="dialogVisible = false">Cancel</v-btn>
        <v-btn
          color="blue darken-1" flat
          :loading="loading"
          @click="submit">
          Update
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  import api from '@/api'
  export default {
    data () {
      return {
        loading: false,
        dialogVisible: false,
        errors: {},
        form: {
          password: '',
          password_confirmation: ''
        }
      }
    },
    methods: {
      setUser (user) {
        this.form.id = user.id
        this.dialogVisible = true
      },
      submit () {
        this.loading = true
        this.errors = {}
        api.setPassword(this.form)
          .then(res => {
            this.$notify.success({
              title: 'User password has been successfully updated'
            })
            this.loading = false
            this.form = {
              id: -1,
              password: ''
            }
            this.dialogVisible = false
          })
          .catch(error => {
            console.log(error)
            this.$notify.error({
              title: error.message
            })
            if (error.status_code === 422) {
              this.errors = error.errors
            }
            this.loading = false
          })
      }
    }
  }
</script>

<style scoped>
</style>



