<template>
  <base-layer>
    <Dashboard></Dashboard>
  </base-layer>
</template>

<script>
import BaseLayer from '@/containers/BaseLayer'
import Dashboard from './Dashboard'

export default {
  components: {
    BaseLayer,
    Dashboard
  },

  created () {
    this.$store.dispatch('changeIndex', '1')
  }
}
</script>



