import L from 'leaflet'

export default class GeometryHelper {
  /**
   * Calculate Orthogonal Projection of point O onto line that perpendicular with line AB in geographical coordinate
   *
   * O
   * |
   * O'--------B---A
   *
   * @param {L.LatLng} pA point A
   * @param {L.LatLng} pB point B
   * @param {L.LatLng} pO point O
   *
   * @return {L.LatLng} point pO'
   */
  static caculateOrthogonalProjectionGeographicalCoordinate (pA, pB, pO, map) {
    const zoom = map.getZoom()

    pA = map.project(pA, zoom)
    pB = map.project(pB, zoom)
    pO = map.project(pO, zoom)

    const p = this.caculateOrthogonalProjection(pA, pB, pO)

    const latLng = map.unproject(L.point(p.x, p.y), zoom)

    return latLng
  }

  /**
   * Calculate Orthogonal Projection of point O onto line AB
   * O
   * |
   * O'--------B---A
   *
   * @param {L.Point} pA point A
   * @param {L.Point} pB point B
   * @param {L.Point} pO point O
   *
   * @return {L.LatLng} point pO'
   */
  static caculateOrthogonalProjection (pA, pB, pO) {
    const denominatorOfT = Math.pow(pB.x - pA.x, 2) + Math.pow(pB.y - pA.y, 2)
    if (denominatorOfT === 0) return pA

    const t = ((pB.x - pA.x) * (pO.x - pA.x) + (pB.y - pA.y) * (pO.y - pA.y)) / denominatorOfT

    const y = pA.y + (pB.y - pA.y) * t
    const x = pA.x + (pB.x - pA.x) * t

    return { x, y }
  }

  /**
   * Calculate Orthogonal Projection of point O onto line d' that perpendicular with line AB in geographical coordinate
   *
   * O         A
   * |         |
   * O'--------B---d'
   *
   * @param {L.LatLng} pA point A
   * @param {L.LatLng} pB point B
   * @param {L.LatLng} pO point O
   *
   * @return {L.LatLng} point pO'
   */
  static caculateOrthogonalProjectionWithPerpendicularGeographicalCoordinate (pA, pB, pO, map) {
    const zoom = map.getZoom()

    pA = map.project(pA, zoom)
    pB = map.project(pB, zoom)
    pO = map.project(pO, zoom)

    const p = this.caculateOrthogonalProjectionWithPerpendicular(pA, pB, pO)

    const latLng = map.unproject(L.point(p.x, p.y), zoom)

    return latLng
  }

  /**
   * Calculate Orthogonal Projection of point O onto line d' that perpendicular with line AB
   *
   * O         A
   * |         |
   * O'--------B---d'
   *
   * @param {L.Point} pA point A
   * @param {L.Point} pB point B
   * @param {L.Point} pO point O
   *
   * @return {L.LatLng} point pO'
   */
  static caculateOrthogonalProjectionWithPerpendicular (pA, pB, pO) {
    const denominatorOfT = Math.pow(pB.x - pA.x, 2) + Math.pow(pB.y - pA.y, 2)
    if (denominatorOfT === 0) return pA

    const t = ((pB.x - pA.x) * (pB.x - pO.x) + (pB.y - pA.y) * (pB.y - pO.y)) / denominatorOfT

    const y = pO.y + (pB.y - pA.y) * t
    const x = pO.x + (pB.x - pA.x) * t

    return { x, y }
  }

  static neswBoundsToLatLngBounds (bounds) {
    const ne = [bounds.northeast.latitude, bounds.northeast.longitude]
    const sw = [bounds.southwest.latitude, bounds.southwest.longitude]
    const nw = [bounds.northeast.latitude, bounds.southwest.longitude]
    const se = [bounds.southwest.latitude, bounds.northeast.longitude]

    const latLngBounds = [nw, ne, se, sw]

    return latLngBounds
  }

  static bboxToLatLngBounds (bbox) {
    const minLng = bbox[0]
    const minLat = bbox[1]
    const maxLng = bbox[2]
    const maxLat = bbox[3]

    return [
      [minLat, minLng],
      [minLat, maxLng],
      [maxLat, maxLng],
      [maxLat, minLng]
    ]
  }
}



