            :nudge-right="40"
            lazy
            transition="scale-transition"
            offset-y
            >
              <v-text-field
                light
                slot="activator"
                outline
                height="30"
                background-color="#fff"
                hide-details
                dense
                label="To Date"
                style="font-size: smaller; max-height: 55px"
                v-model="form.to_date"
                prepend-inner-icon="event"
                readonly
                @change="submitFilter"
              ></v-text-field>
            <v-date-picker v-model="form.to_date" @change="submitFilter"></v-date-picker>
          </v-menu>
      </v-layout>
      </v-layout>
      <v-layout align-center class="pa-2 mb-4 " style="background-color: #324157; width: 100%; flex-wrap: wrap">
        <v-card flat style="flex: 1;" class="mx-1">
          <v-card-text>
            <div class="display-1" style="text-align: center; color: #036bfc">Projects: {{numberWithCommas(totalProject)}}</div>
          </v-card-text>
        </v-card>
        <v-card style="flex: 1;" hover :color="color1"class="mx-1">
          <v-card-text>
            <div class="display-1" style="text-align: center; color: #eec722" @click="filterStatus(100)">Completed: {{numberWithCommas(totalCompleted)}}</div>
          </v-card-text>
        </v-card>
        <v-card style="flex: 1;" hover :color="color2" class="mx-1">
          <v-card-text>
            <div class="display-1" style="text-align: center; color: #22EE22" @click="filterStatus(10)">OnGoing: {{numberWithCommas(totalOngoing)}}</div>
          </v-card-text>
        </v-card>
        <v-card style="flex: 1;" hover :color="color3" class="mx-1">
          <v-card-text>
            <div class="display-1" style="text-align: center; color: #88AACC" @click="filterStatus(1)">NotStarted: {{numberWithCommas(totalNotStarted)}}</div>
          </v-card-text>
        </v-card>
        <v-btn color="#036bfc" fab small dark title="Export Excel" @click="exportExcel">
          <v-icon>download</v-icon>
        </v-btn>
      </v-layout>
      <v-layout style="height: 100%; width: 100%" class="pa-2">
        <v-layout style="width: 50%; height: 100%; border: 1px solid #036bfc; border-radius: 5px" justify-center column align-end class="px-2 mx-1">
          <div style="width: 100%; max-height: 50%; height: 50%">
            <div style="height: 50px; width: 100%; text-align: center">
              <span style="font-size: 18px; font-weight: bold; color: #036bfc">Scheme Wise</span>
            </div>
            <canvas id="myChart" style="height: calc(100% - 50px); width: 100%"></canvas>
          </div>
          <div style="width: 100%; height: 50%; border-top: 1px solid #036bfc">
            <v-layout style="width: 100%; height: 100%">
              <div style="width: 50%" class="mr-1 py-2">
                <v-layout justify-center align-center style="height: 50px; width: 100%" column>
                  <span style="font-size: 18px; font-weight: bold; color: #036bfc">Cost Wise</span>
                  <span style="font-size: 12px; font-weight: bold; color: #036bfc;">Estimate cost:</span>
                </v-layout>
                <canvas id="myChartPie" style="height: calc(100% - 70px); width: 100%"></canvas>
              </div>
              <div style="width: 50%" class="ml-1 py-2" >
                <v-layout justify-center align-center style="height: 50px; width: 100%">
                  <span style="font-size: 18px; font-weight: bold; color: #036bfc">Physical Progress Wise</span>
                </v-layout>
                <canvas id="myChartBar2" style="height: calc(100% - 50px); width: 100%"></canvas>
              </div>
            </v-layout>
          </div>
        </v-layout>
        <v-layout column style="position: relative; width: 50%; height: 100%; border: 1px solid #036bfc; border-radius: 5px" class="mx-1">
          <Map ref="mapView" style="width: 100%; height: 100%" @setPopup="setPopup" v-on:mapLoaded="getMapdata"/>
          <div v-if="isPopup" class="px-2 elevation-1" style="min-width: 150px; height: 85px; position: absolute; top: 10px; left: 210px; background-color: white; border-radius: 4px">
            <v-layout column style="width: 100%; height: 100%">
              <v-layout style="width: 100%; max-height: 25px;" justify-end>
               <v-icon style="height: 20px" small @click="isPopup = false">close</v-icon>
              </v-layout>
              <v-layout column>
                <span>District: {{popupDescription.dist_name}}</span>
                <span>Circle: {{popupDescription.name}}</span>
                <span>Total project: {{popupDescription.value}}</span>
              </v-layout>
            </v-layout>
          </div>
          <div class="pa-2 elevation-1" style="background-color: white; position: absolute; top: 10px; left: 10px; width: 160px; height: 165px; border: 1px solid lightgrey; border-radius: 5px">
            <v-layout class="my-1" style="height: 20px" justify-center align-center>
              <span class="ml-2" style="font-weight: bold">Total Project</span>
            </v-layout>
            <v-layout class="my-1" style="height: 20px">
              <div style="width: 20px; height: 20px" :style="{'background-color': '#ffcdd2'}"></div>
              <span class="ml-4"> 0 - 99</span>
            </v-layout>
            <v-layout class="my-1" style="height: 20px">
              <div style="width: 20px; height: 20px" :style="{'background-color': '#e57373'}"></div>
              <span class="ml-4">100 - 199</span>
            </v-layout>
            <v-layout class="my-1" style="height: 20px">
              <div style="width: 20px; height: 20px" :style="{'background-color': '#f44336'}"></div>
              <span class="ml-4">200 - 299</span>
            </v-layout>
            <v-layout class="my-1" style="height: 20px">
              <div style="width: 20px; height: 20px" :style="{'background-color': '#d32f2f'}"></div>
              <span class="ml-4">300 - 399</span>
            </v-layout>
            <v-layout class="my-1" style="height: 20px">
              <div style="width: 20px; height: 20px" :style="{'background-color': '#7f0000'}"></div>
              <span class="ml-4"> > 400 </span>
            </v-layout>
          </div>
        </v-layout>
