</template>
<script>
import api from '@/api'
import {mapActions, mapGetters} from 'vuex'
import Chart from 'chart.js'
import Map from './map.vue'
import MainNavigationBar from '../../containers/MainNavigationBar'
export default {
  components: {
    MainNavigationBar,
    Map
  },
  created () {
    this.changeIndex('9')
    this.initData()
  },
  data () {
    return {
      color1: '',
      color2: '',
      color3: '',
      loading: true,
      isPopup: false,
      popupDescription: undefined,
      circlesData: [],
      totalProject: 0,
      totalCompleted: 0,
      totalOngoing: 0,
      totalNotStarted: 0,
      departments: [],
      districts: [],
      circles: [],
      types: [],
      agencies: [],
      structures: [],
      form: {
        from_date: undefined,
        to_date: undefined,
        department_id: undefined,
        structure_id: undefined,
        funding_agency_id: undefined,
        district: undefined,
        circle: undefined,
        status: undefined
      },
      costWise: {
        data: [],
        labels: [],
        colors: ['#05fa2a', '#ff3333', '#eefa05', '#05a4fa']
      },
      physicalWise: {
        data: [],
        labels: [],
        colors: []
      },
      schemeWise: {
        data: [],
        labels: [],
        colors: []
      },
      myChart: undefined,
      myChartPie: undefined,
      myChartBar2: undefined,
      detail: {
        show: false,
        item: undefined
      },
      menu2: false,
      menu3: false
    }
  },
  computed: {
    ...mapGetters(['token', 'me']), isDistrictUser () { return this.me.hasRole(4) }
  },
  methods: {
    ...mapActions([
      'changeIndex'
    ]),
    exportExcel () {
      let filters = this.form
      let form = {
        department_id: filters.department_id ? filters.department_id : '',
        district: filters.district ? filters.district : '',
        circle: filters.circle ? filters.circle : '',
        status: filters.status ? filters.status : '',
        funding_agency_id: filters.funding_agency_id ? filters.funding_agency_id : '',
        structure_id: filters.structure_id
      }
      if (filters.from_date !== undefined && filters.from_date !== null) {
        form.from_date = new Date(filters.from_date)
        form.from_date.setHours(0, 0, 0)
        form.from_date = form.from_date.toUTCString()
      }
      if (filters.to_date !== undefined && filters.to_date !== null) {
        form.to_date = new Date(filters.to_date)
        form.to_date.setHours(23, 59, 59)
        form.to_date = form.to_date.toUTCString()
      }
      const token = this.token
      let fil2 = ''
      Object.keys(form).forEach(val => {
        if (form[val] && form[val] !== '') {
          fil2 += `&${val}=${form[val]}`
        }
      })
      window.open(`/api/exports/projects?v=${new Date().getTime()}&token=${token}${fil2}`, '_blank')
    },
    filterStatus (status) {
      console.log('colorChange', status)
      if (status === 100) {
        if (this.color1 === '#036bfc') {
          this.color1 = ''
          this.form.status = undefined
        } else {
          this.color1 = '#036bfc'
          this.form.status = 'Completed'
        }
        this.color2 = ''
        this.color3 = ''
      } else if (status === 10) {
        if (this.color2 === '#036bfc') {
          this.color2 = ''
          this.form.status = undefined
        } else {
          this.color2 = '#036bfc'
          this.form.status = 'Ongoing'
        }
        this.color1 = ''
        this.color3 = ''
      } else if (status === 1) {
        if (this.color3 === '#036bfc') {
          this.color3 = ''
          this.form.status = undefined
        } else {
          this.color3 = '#036bfc'
          this.form.status = 'Not Started'
        }
        this.color2 = ''
        this.color1 = ''
      }
      this.submitFilter()
    },
    setPopup (data) {
      this.isPopup = data.status
      if (data.status) this.popupDescription = data.description
    },
    numberWithCommas (nStr) {
      nStr += ''
      var x = nStr.split('.')
      var x1 = x[0]
      var x2 = x.length > 1 ? '.' + x[1] : ''
      var rgx = /(\d+)(\d{3})/
      while (rgx.test(x1)) {
        x1 = x1.replace(rgx, '$1' + ',' + '$2')
      }
      return x1 + x2
    },
    initData () {
      if (this.isDistrictUser) {
        this.districts = [this.me.district]
        this.form.district = this.me.district
        this.getCircles()
      } else {
        api.getDistricts()
          .then(res => {
            this.districts = res.data
            this._districts = res.data
          })
      }
      api.getFundingAgencies()
        .then(res => {
          let agenciesData = res.data
          agenciesData.sort(function (a, b) {
            var nameA = a.name.toUpperCase()
            var nameB = b.name.toUpperCase()
            if (nameA < nameB) {
              return -1
            }
            if (nameA > nameB) {
              return 1
            }
            return 0
          })
          this.agencies = agenciesData
        })
      if (this.me.hasRole(2) || this.me.hasRole(6)) {
        this.departments = [this.me.department]
        this.form.department_id = this.me.department.id
      } else {
        api.getDepartments()
          .then(res => {
            this.departments = res.data
          })
      }
      api.getStructures()
        .then(res => {
          this.structures = res.data
        })
    },
    async getMapdata () {
      let res = await api.getGeometryDistrict()
      let geoDistricts = res.data.map(val => {
        return {
          type: 'Feature',
          properties: {name: val.name},
          geometry: JSON.parse(val.geometry)
        }
      })
      let res2 = await api.getGeometryCircle()
      let geoCircles = res2.data.map(val => {
        return {
          type: 'Feature',
          properties: {name: val.name, dist_name: val.dist_name, value: '', color: '#ffebee', opacity: 0.4},
          geometry: JSON.parse(val.geometry)
        }
      })
      this.$refs.mapView.addCircles(geoCircles)
      this.$refs.mapView.addDistricts(geoDistricts)
      this.loading = false
      this.submitFilter()
    },
    getCircles () {
      this.form.circle = undefined
      this.circles = undefined
      if (this.form.district && this.form.district !== '') {
        api.getCircles(this.form.district)
          .then(res => {
            this.circles = res.data
          })
      }
      this.submitFilter()
    },
    createChartBarRight () {
      var ctx = document.getElementById('myChartBar2').getContext('2d')
      this.myChart = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: this.physicalWise.labels,
          datasets: [{
            label: '',
            data: this.physicalWise.data,
            backgroundColor: this.physicalWise.colors,
            borderColor: this.physicalWise.colors,
            borderWidth: 1
          }]
        },
        options: {
          legend: {display: false},
          scales: {
            yAxes: [{
              scaleLabel: {
                display: true,
                labelString: 'Total project'
              }
            }],
            xAxes: [{
              scaleLabel: {
                display: true,
                labelString: ''
              }
            }]
          }
        }
      })
    },
    createChartBar () {
      var ctx = document.getElementById('myChart').getContext('2d')
      this.myChart = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: this.schemeWise.labels,
          datasets: [{
            label: '',
            data: this.schemeWise.data,
            backgroundColor: this.schemeWise.colors,
            borderColor: this.schemeWise.colors,
            borderWidth: 1
          }]
        },
        options: {
          legend: {display: false},
          scales: {
            yAxes: [{
              scaleLabel: {
                display: true,
                labelString: 'Total project'
              }
            }],
            xAxes: [{
              scaleLabel: {
                display: true,
                labelString: ''
              }
            }]
          }
        }
      })
    },
    createChartPie () {
      var ctx = document.getElementById('myChartPie').getContext('2d')
      this.myChartPie = new Chart(ctx, {
        type: 'doughnut',
        data: {
          labels: this.costWise.labels,
          datasets: [{
            label: '',
            data: this.costWise.data,
            backgroundColor: this.costWise.colors,
            borderColor: this.costWise.colors,
            borderWidth: 1
          }]
        },
        options: {
          scales: {
            y: {
              beginAtZero: true
            }
          }
        }
      })
    },
    resetData () {
      this.isPopup = false
      this.costWise.labels = []
      this.costWise.data = []
      this.schemeWise.labels = []
      this.schemeWise.data = []
      this.schemeWise.colors = []
      this.physicalWise.labels = []
      this.physicalWise.data = []
      this.physicalWise.colors = []
    },
    async submitFilter () {
      try {
        this.loading = true
        const res = await api.filterData(this.form)
        if (res.data.dataTotal.Completed) this.totalCompleted = res.data.dataTotal.Completed
        else this.totalCompleted = 0
        if (res.data.dataTotal.Ongoing) this.totalOngoing = res.data.dataTotal.Ongoing
        else this.totalOngoing = 0
        if (res.data.dataTotal['Not Started']) this.totalNotStarted = res.data.dataTotal['Not Started']
        else this.totalNotStarted = 0
        this.totalProject = this.totalCompleted + this.totalOngoing + this.totalNotStarted
        this.resetData()
        if (!Array.isArray(res.data.dataCircle)) {
          for (const property in res.data.dataCircle) {
            this.circlesData.push({
              name: property,
              value: res.data.dataCircle[property]
            })
          }
        }
        if (!Array.isArray(res.data.dataSchemeWise)) {
          let schemeData = Object.keys(res.data.dataSchemeWise).map((key) => [key, res.data.dataSchemeWise[key]])
          schemeData.sort((firstEl, secondEl) => {
            return firstEl[1] <= secondEl[1] ? 1 : -1
          })
          for (const item of schemeData) {
            this.schemeWise.labels.push(item[0])
            this.schemeWise.data.push(item[1])
            this.schemeWise.colors.push('#' + Math.floor(Math.random() * 16777215).toString(16))
          }
        }
        if (!Array.isArray(res.data.dataPhysicalWise)) {
          this.physicalWise.labels = ['0-25 %', '25-50 %', '50-75 %', '75-100 %']
          this.physicalWise.data = [
            res.data.dataPhysicalWise['0-25'],
            res.data.dataPhysicalWise['25-50'],
            res.data.dataPhysicalWise['50-75'],
            res.data.dataPhysicalWise['75-100']
          ]
          this.physicalWise.colors = ['#2957f0', '#2957f0', '#2957f0', '#2957f0']
        }
        if (!Array.isArray(res.data.dataCostWise)) {
          for (const property in res.data.dataCostWise) {
            this.costWise.labels.push(property)
            this.costWise.data.push(res.data.dataCostWise[property])
          }
        }
        if (this.myChart) {
          this.myChart.destroy()
          this.myChart = null
        }
        if (this.myChartBar2) {
          this.myChartBar2.destroy()
          this.myChartBar2 = null
        }
        if (this.myChartPie) {
          this.myChartPie.destroy()
          this.myChartPie = null
        }
        this.createChartBar()
        this.createChartBarRight()
        this.createChartPie()
        this.$refs.mapView.updateData(res)
      } catch (e) {
        console.log(e)
      } finally {
        this.loading = false
      }
    }
  }
}
</script>
<style lang="css" scoped>
  .logo {
    height: 48px;
    margin: 5px 15px;
  }
  ::-webkit-scrollbar {
    width: 12px;
    height: 12px;
    background-color: #747474;
  }

  ::-webkit-scrollbar-thumb {
    background-color: #4d4d4d;
    -webkit-box-sizing: border-box;
  }
  .main-content {
    background-color: var(--secondary-light);
    overflow-y: auto;
  }
  .nav-bar {
    padding-left: 20px;
    background-color: var(--secondary);
  }

  .card-content{
    background-color: white;
    margin-top: 24px;
  }

  .card-content:hover{
    background-color: #278D71;
    margin-top: 24px;
  }

  .title-two-lines {
    line-height: 1.5em;
    font-size: medium;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .detail-form {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #919191;
  }

  /deep/
  .v-select__selection--comma {
    white-space: nowrap !important;
    overflow: hidden !important;
    text-overflow: ellipsis !important;
    max-width: 70px;
  }

</style>



