<template>
  <base-layer>
    <div class="page-container">
      <div ref="physical" style="min-width: 230px">
        <div class="chart-item">
          <div class="chart-title">Physical Progress Wise</div>
          <div id="phisicalChart"></div>
        </div>
      </div>

      <div ref="type" style="min-width: 230px">
        <div class="chart-item">
          <div class="chart-title">Type of Projects</div>
          <div class="absolute-chart-center">
            <div id="typeNumber"></div>
            <div>Total Projects</div>
          </div>
          <div id="typeChart"></div>
        </div>
      </div>
      <div ref="department" style="min-width: 230px">
        <div class="chart-item">
          <div class="chart-title">Departments</div>
          <div id="departmentChart"></div>
        </div>
      </div>
      <div ref="year" style="flex: 3; min-width: 600px">
        <div class="chart-item">
          <div class="chart-title">Year Wise</div>
          <div id="yearChart"></div>
        </div>
      </div>
      <div ref="district" style="flex: 3; min-width: 550px">
        <div class="chart-item">
          <div class="chart-title">District</div>
          <div id="districtChart"></div>
        </div>
      </div>
      <div ref="source" style="flex: 3; min-width: 500px">
        <div class="chart-item">
          <div class="chart-title">Source of Funding</div>
          <div id="sourceSelect" class="absolute-chart"></div>
          <div id="sourceChart"></div>
          <div id="timeChart"></div>
        </div>
      </div>
      <div ref="cost" style="min-width: 230px">
        <div class="chart-item">
          <div class="chart-title">Cost Wise</div>
          <div id="costChart"></div>
        </div>
      </div>
    </div>
  </base-layer>
</template>

<script>
import BaseLayer from '@/containers/BaseLayer'
import dc from 'dc'
import crossfilter from 'crossfilter'
import api from '@/api'
import * as d3 from 'd3'
import 'dc/dc.min.css'
import 'd3-time'

export default {
  components: {
    BaseLayer
  },

  created () {
    this.$store.dispatch('changeIndex', '8')
  },

  mounted () {
    api.authSubmit('get', '/projects_kpi')
      .then(e => {
        this.renderChart(e.data)
      })
  },
  methods: {
    render () {
      dc.redrawAll()
    },
    renderChart (data) {
      dc.config.defaultColors(d3.schemePaired)
      let dateFormatSpecifier = '%Y-%m-%d'
      let dateFormatParser = d3.timeParse(dateFormatSpecifier)
      data.forEach(function (d) {
        d.dd = dateFormatParser(d.financial_sanction_date)
        d.month = d3.timeMonth(d.dd)
      })
      let physicalChart = dc.pieChart('#phisicalChart')
      let typeChart = dc.pieChart('#typeChart')
      let typeNumber = dc.numberDisplay('#typeNumber')
      let departmentChart = dc.pieChart('#departmentChart')
      let yearChart = dc.compositeChart('#yearChart')
      let districtChart = dc.barChart('#districtChart')
      let sourceChart = dc.barChart('#sourceChart')
      let sourceSelect = dc.selectMenu('#sourceSelect')
      let timeChart = dc.barChart('#timeChart')
      let costChart = dc.pieChart('#costChart')
      let height = this.$refs.physical.clientHeight - 61
      let getWidth = name => {
        return this.$refs[name].clientWidth - 40
      }

      let getGroupReduce = (dimension, keys, condition, test = false) => {
        let getVal = (v) => {
          let str = ''
          keys.forEach(e => {
            str += `${v[e]}.`
          })
          return str
        }
        return dimension.group().reduce((p, v) => {
          if (condition && !condition(v)) return p
          let val = getVal(v)
          if (!p[val]) p[val] = 0
          p[val] = p[val] + 1
          if (test) {
            console.log(p)
          }
          return p
        }, (p, v) => {
          if (condition && !condition(v)) return p
          let val = getVal(v)
          p[val] = p[val] - 1
          if (p[val] === 0) delete p[val]
          if (test) {
            console.log(p)
          }
          return p
        }, function () {
          return {}
        })
      }
      let valueGrouped = function (p) {
        return Object.keys(p.value).length
      }
      let monthDiff = (d1, d2) => {
        let months
        months = (d2.getFullYear() - d1.getFullYear()) * 12
        months -= d1.getMonth() + 1
        months += d2.getMonth()
        return months <= 0 ? 0 : months
      }
      let projectIDs = [...new Set(data.map(x => x.id))]
      projectIDs.forEach(ele => {
        let tmp = data.filter(v => {
          return v.id === ele
        })
        tmp.sort((a, b) => {
          return (a.physical_year + a.physical_quarter) < (b.physical_year + b.physical_quarter)
        })
        for (let i = 0; i < data.length; i++) {
          if (data[i].id === ele) {
            data[i]['last_physical_status'] = tmp[0].physical_status
          }
        }
      })
      let ndx = crossfilter(data)
      let physicalDimension = ndx.dimension(function (d) {
        return d.last_physical_status
      })
      let physicalGroup = getGroupReduce(physicalDimension, ['id'])
      physicalChart.width(getWidth('physical')).height(height)
        .dimension(physicalDimension)
        .group(physicalGroup)
        .legend(dc.legend().x(0).y(5).itemHeight(13).gap(5))
        .radius(Math.min(106, getWidth('physical') / 2))
        .renderLabel(true)
        .label(valueGrouped)
        .ordinalColors(['#9CAA00', '#C62A00', '#00ADD7'])
        .ordering(function (d) {
          return d.key
        })
        .innerRadius(20)
        .transitionDuration(500)
        .externalRadiusPadding(22)
        .on('pretransition', function (c) {
          // c.svg().attr('width', 300)
          // c.svg().select('g').attr('transform', 'translate(150,100)')
        }).valueAccessor(valueGrouped)

      let typeDimension = ndx.dimension(function (d) {
        return d.type
      })
      let typeGroup = getGroupReduce(typeDimension, ['id'])
      typeChart.width(getWidth('type')).height(height)
        .dimension(typeDimension)
        .group(typeGroup)
        .radius(Math.min(106, getWidth('type') / 2))
        .label(valueGrouped)
        .renderLabel(true)
        .innerRadius(20)
        .transitionDuration(500)
        .valueAccessor(valueGrouped)
        .ordinalColors(['#F15C80', '#E4D254', '#00ADD7', '#90ED7D', '#8085E9', '#F7A25D'])
        .externalLabels(16)
        .drawPaths(true)
        .minAngleForLabel(0)
        .externalRadiusPadding(22)
      let departmentDimension = ndx.dimension(function (d) {
        return d.department
      })
      let typeNumberGroup = ndx.groupAll().reduce((p, v) => {
        let val = v.id
        if (!p[val]) p[val] = 0
        p[val] = p[val] + 1
        return p
      }, (p, v) => {
        let val = v.id
        p[val] = p[val] - 1
        if (p[val] === 0) delete p[val]
        return p
      }, function () {
        return {}
      })
      typeNumber
        .group(typeNumberGroup)
        .valueAccessor(e => Object.keys(e).length)
        .label()
      let departmentGroup = getGroupReduce(departmentDimension, ['id'])
      departmentChart.width(getWidth('department')).height(height)
        .dimension(departmentDimension)
        .group(departmentGroup)
        .label(valueGrouped)
        .radius(Math.min(106, getWidth('department') / 2))
        .transitionDuration(500)
        .renderLabel(true)
        .externalLabels(20)
        .drawPaths(true)
        .valueAccessor(valueGrouped)
        .minAngleForLabel(0)
        .externalRadiusPadding(22)
      let yearDimension = ndx.dimension(e => Number(e.financial_sanction_year))
      let sourceDimension = ndx.dimension(function (d) {
        return d.month
      })
      let minDate = new Date(sourceDimension.bottom(1)[0].financial_sanction_date)
      let maxDate = new Date(sourceDimension.top(1)[0].financial_sanction_date)
      maxDate.setDate(maxDate.getDate() + 30)
      minDate.setDate(minDate.getDate() - 30)
      let timeGroup = getGroupReduce(sourceDimension, ['id'])
      timeChart.width(getWidth('source')).height(40)
        .margins({top: 0, right: 18, bottom: 20, left: 52})
        .dimension(sourceDimension)
        .group(timeGroup, 'time', valueGrouped)
        .centerBar(true)
        .gap(1)
        // .elasticX(true)
        // .elasticY(true)
        .x(d3.scaleTime().domain([minDate, maxDate]))
        .round(d3.timeMonth.round)
        .alwaysUseRounding(true)
        .xUnits(d3.timeMonths)
      timeChart.yAxis().ticks(0)
      let yearCompleteGroup = getGroupReduce(yearDimension, ['id'], e => e.physical_status === 'Completed')
      let yearOngoingGroup = getGroupReduce(yearDimension, ['id'], e => e.physical_status === 'Ongoing')
      let yearNotYetGroup = getGroupReduce(yearDimension, ['id'], e => e.physical_status === 'Not Started')
      yearChart
        .width(getWidth('year'))
        .height(height)
        .dimension(yearDimension)
        .compose([
          dc.lineChart(yearChart)
            .group(yearCompleteGroup, 'Completed', valueGrouped)
            .ordinalColors(['#9CAA00']),
          dc.lineChart(yearChart)
            .group(yearOngoingGroup, 'Ongoing', valueGrouped)
            .ordinalColors(['#00ADD7']),
          dc.lineChart(yearChart)
            .group(yearNotYetGroup, 'Not Started', valueGrouped)
            .ordinalColors(['#C62A00'])
        ])
        .transitionDuration(500)
        .valueAccessor(valueGrouped)
        .x(d3.scaleLinear())
        // .xUnits(dc.units.ordinal)
        .yAxisLabel('No.of Projects')
        .brushOn(false)
        // .legend(dc.legend().x(0).y(0).itemHeight(13).gap(5))
        .elasticX(true)
        .elasticY(true)
        .renderHorizontalGridLines(true)
        .xAxis().tickFormat(d3.format('d'))

      let districtDimension = ndx.dimension(function (d) {
        return d.district
      })
      districtChart.width(getWidth('district'))
        .height(height)
        .x(d3.scaleBand())
        .xUnits(dc.units.ordinal)
        // .margins({ left: 30, top: 0, right: 0, bottom: 100 })
        // .clipPadding(10)
        // .centerBar(true)
        .dimension(districtDimension)
        .group(getGroupReduce(districtDimension, ['id']), 'District', valueGrouped)
        // .legend(dc.legend().x(0).y(0).itemHeight(13).gap(5))
        // .stack(amountSP, 'Revenue')
        .elasticX(true)
        .yAxisLabel('No.of Projects')
        .ordinalColors(['#E4D254'])
        .transitionDuration(500)
        .on('pretransition', function (c) {
          c.svg().selectAll('g.x text').attr('transform', 'translate(-12, -5) rotate(270)')
          c.svg().selectAll('g.x text').attr('style', 'text-anchor: start')
          // c.svg().selectAll('text.y-axis-label').attr('fill', 'rgba(0,0,0,0.6)')
          // c.svg().selectAll('text.y-axis-label').attr('font-size', '12')
        })

      // let stateSourceGroup = getGroupReduce(sourceDimension, ['id'], e => e.fund_source === 'State Funding Agency')
      // let centralSourceGroup = getGroupReduce(sourceDimension, ['id'], e => e.fund_source === 'Central Funding Agency')
      let stateSourceGroup = sourceDimension.group().reduceSum(e =>
        e.fund_source === 'State Funding Agency' ? e.financial_amount : 0)
      let centralSourceGroup = sourceDimension.group().reduceSum(e =>
        e.fund_source === 'Central Funding Agency' ? e.financial_amount : 0)
      let shortMonths = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      sourceChart.width(getWidth('source'))
        .height(height - 46)
        .margins({top: 50, right: 20, bottom: 25, left: 40})
        .transitionDuration(500)
        .x(d3.scaleTime().domain([minDate, maxDate]))
        .brushOn(false)
        .mouseZoomable(true)
        // .round(d3.timeFormat('%b'))
        // .alwaysUseRounding(true)
        .legend(dc.legend().x(20).y(0).itemHeight(13).gap(5))
        .yAxisLabel('Rupees (in million)')
        .xUnits((start, end) => {
          let diff = monthDiff(start, end)
          if (diff < 50) diff = 50
          return diff
        })
        .centerBar(true)
        .title(e => {
          let date = new Date(e.key)
          let year = date.getFullYear()
          let month = date.getMonth()
          return `${year}-${shortMonths[month]} : ${e.value}`
        })
        // .elasticX(true)
        .elasticY(true)
        .renderHorizontalGridLines(true)
        .rangeChart(timeChart)
        .dimension(sourceDimension)
        .gap(1)
        .group(stateSourceGroup, 'State Funding Agency')
        .stack(centralSourceGroup, 'Central Funding Agency')
      sourceChart.xAxis()
        .tickFormat((e) => {
          let date = new Date(e)
          let year = date.getFullYear()
          let month = date.getMonth()
          let day = date.getDate()
          if (month === 0 && day === 1) {
            return year
          } else {
            return shortMonths[month]
          }
        })

      let sourceSelectDimension = ndx.dimension(e => e.fund_source)
      sourceSelect.dimension(sourceSelectDimension)
        .group(sourceSelectDimension.group())
        .controlsUseVisibility(true)
      let costDimension = ndx.dimension(function (d) {
        return d.cost_group
      })

      costChart.width(getWidth('cost')).height(height)
        .dimension(costDimension)
        .group(getGroupReduce(costDimension, ['id']))
        .legend(dc.legend().x(0).y(0).autoItemWidth(true).itemHeight(13).gap(5).horizontal(false))
        .label(function (p) {
          return Object.keys(p.value).length
        })
        .radius(Math.min(106, getWidth('cost') / 2))
        .transitionDuration(0)
        .renderLabel(true)
        .valueAccessor(valueGrouped)
        .ordering(function (d) {
          switch (d.key) {
            case '0 - 20 Lakhs': return 0
            case '20 - 50 Lakhs': return 1
            case '50 - 1 Crores': return 2
            case '1 Crore & above': return 3
          }
        })
        .ordinalColors(['#7CB5EC', '#85AA00', '#EB7200', '#434348'])
        .externalLabels(16)
        .drawPaths(true)
        .minAngleForLabel(0)
        .externalRadiusPadding(22)
        // .on('pretransition', function (c) {
        //   // c.svg().select('g').attr('transform', 'translate(0, 50)')
        //   c.svg()._groups[0][0].childNodes[0].setAttribute('transform', 'translate(80, 150)')
        // })
        .on('renderlet', function (c) {
          let svg = c.svg()._groups[0][0]
          let w = svg.getAttribute('width')
          let h = svg.getAttribute('height')
          c.svg()._groups[0][0].childNodes[0].setAttribute('transform', `translate(${w / 2}, ${h / 2 + 26})`)
        })
      dc.renderAll()
    }
  }
}
</script>

<style scoped>
  .page-container {
    width: 100%;
    height: 100%;
    display: flex;
    overflow-y: auto;
    flex-flow: row wrap;
    padding: 20px 30px;
    align-content: start;
  }

  .page-container > div {
    flex: 1;
    height: 50%;
    max-height: 400px;
    min-height: 336px;
  }

  .chart-item {
    position: relative;
    background: white;
    border-radius: 2px;
    padding: 10px;
    margin: 10px;
    box-shadow: 1px 1px 20px rgba(0,0,0,0.1);
  }

  .chart-title {
    font-weight: bold;
  }

  .absolute-chart {
    position: absolute;
    right: 20px;
    border: solid 1px rgba(0,0,0,0.4);
    border-radius: 5px;
    padding: 2px 10px;
  }

  .absolute-chart-center {
    position: absolute; display: flex; align-items: center; flex-flow: column; width: calc(100% - 20px);
    font-weight: bold;
    color: rgba(0,0,0,0.6);
  }

  .dc-chart {
    float: inherit
  }
</style>



