<template>
  <base-layer>
    <web-users></web-users>
  </base-layer>
</template>

<script>
  import BaseLayer from '@/containers/BaseLayer'
  import WebNotifications from './WebNotifications'

  export default {
    components: {
      BaseLayer,
      WebNotifications
    },
    created () {
      this.$store.dispatch('changeIndex', '3')
      this.$store.dispatch('getUsers')
    }
  }
</script>

<style lang="css" scoped>
</style>



