<template>
  <div class="user-management">
    <div style="width: 100%; display: flex; flex-flow: row;">
      <el-form size="mini" style="flex: 1" label-width="80px">
        <el-form-item label="Filters">
          <el-select
            style="width: 600px"
            v-model="filtersForm.departments"
            multiple
            filterable
            :filter-method="filterDep"
            @focus="resetDep"
            @change="getTableData"
            collapse-tags
            size="mini"
            placeholder="Select departments">
            <el-option
              v-for="item in departments"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-input
            size="mini"
            style="width: 600px"
            v-model="filtersForm.search"
            placeholder="Search user (name, mobile, email)"
            @keyup.enter.native.prevent.stop="getTableData"/>
          <el-button size="mini" :autofocus="true" type="primary" icon="el-icon-search" @click="getTableData" plain>Search</el-button>
          <el-button size="mini" type="primary" icon="el-icon-refresh" @click="resetSearch" plain>Reset</el-button>
        </el-form-item>
      </el-form>
      <create-user :departments="departments" :districts="districts" @userCreated="getTableData"/>
    </div>
    <el-pagination
      size="mini"
      @size-change="sizeChanged"
      @current-change="pageChanged"
      :page-sizes="[10, 15, 20, 50]"
      :page-size="filtersForm.page_size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalUser">
    </el-pagination>
    <el-table size="mini" :loading="loading" :data="tableData" style="overflow-y: auto">
      <el-table-column
          prop="created_at"
          label="Created at (UTC)"
          width="140">
      </el-table-column>
      <el-table-column
          prop="updated_at"
          label="Updated at (UTC)"
          width="150">
      </el-table-column>
      <el-table-column
          prop="name"
          label="Name"
          min-width="150">
      </el-table-column>
      <el-table-column
          prop="mobile"
          label="Mobile"
          min-width="140">
      </el-table-column>
      <el-table-column
          prop="email"
          label="Email"
          min-width="140">
      </el-table-column>
      <el-table-column
          label="Role"
          min-width="140">
        <template slot-scope="props">
          {{ props.row.roles[0] ? props.row.roles[0].name : 'No role' }}
        </template>
      </el-table-column>
      <el-table-column
          label="Department"
          min-width="200">
        <template slot-scope="props">
          {{ props.row.department ? props.row.department.name : '' }}
        </template>
      </el-table-column>
      <el-table-column
        label="District"
        prop="district"
        min-width="200">
      </el-table-column>
      <el-table-column
          label="Operators"
          width="170">
        <template slot-scope="props">
          <el-button-group>
            <el-tooltip content="Edit user" placement="top-start">
              <el-button size="mini" @click="showEditUserForm(props.row)" type="primary" icon="el-icon-edit"></el-button>
            </el-tooltip>
            <el-tooltip content="Set new password" placement="top-end">
              <el-button size="mini" @click="showSetPasswordForm(props.row)" type="primary" icon="el-icon-key"></el-button>
            </el-tooltip>
            <el-tooltip content="Delete user" placement="top-end">
              <el-button size="mini" @click="confirmDeleteUser(props.row)" type="danger" icon="el-icon-delete"></el-button>
            </el-tooltip>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <update-user ref="updateUserForm" :departments="departments" :districts="districts" @userUpdated="getTableData"/>
    <set-password-user ref="setPasswordForm"/>
  </div>
</template>

<script>
import api from '@/api'
import CreateUser from './CreateUser'
import UpdateUser from './UpdateUser'
import SetPasswordUser from './SetPasswordUser'
import { filterArr } from '@/utils/Array'

export default {
  name: 'web-users',
  components: {UpdateUser, CreateUser, SetPasswordUser},
  data () {
    return {
      departments: [],
      _departments: [],
      districts: [],
      loading: false,
      tableData: [],
      filtersForm: {
        departments: [],
        search: undefined,
        page_size: 10,
        page: 1,
        roles: [2, 4, 5, 6]
      },
      totalUser: null,
      profileUserForm: {
        name: '',
        email: '',
        mobile: '',
        department: ''
      },
      createdUserForm: {
        name: '',
        mobile: '',
        email: '',
        department_id: '',
        password: '',
        password_confirmation: ''
      }
    }
  },
  methods: {
    resetDep () {
      this.departments = this._departments
    },
    filterDep (value) {
      this.departments = filterArr(value, this._departments, 'name')
    },
    sizeChanged (val) {
      this.filtersForm.page_size = val
      this.filtersForm.page = 1
      this.getTableData()
    },
    pageChanged (val) {
      this.filtersForm.page = val
      this.getTableData()
    },
    resetSearch () {
      this.filtersForm = {
        departments: [],
        search: undefined,
        page_size: 10,
        page: 1,
        roles: [2, 4, 5, 6]
      }
      this.getTableData()
    },
    getTableData () {
      this.loading = true
      this.tableData = []
      api.getUsers(this.filtersForm)
        .then(res => {
          this.loading = false
          this.tableData = res.data.data
          this.totalUser = res.data.total
        })
        .catch(error => {
          this.loading = false
          console.log(error)
        })
    },
    confirmDeleteUser (user) {
      this.$confirm('Are you sure delete user?')
        .then(_ => {
          this.deleteUser(user)
        })
        .catch(_ => { })
    },
    deleteUser (user) {
      api.deleteUser(user.id)
        .then(res => {
          this.$notify.success('User has been successfully deleted')
          this.getTableData()
        })
        .catch(e => {
          this.getTableData()
          this.$notify.error('Something went wrong')
        })
    },
    showEditUserForm (user) {
      this.$refs.updateUserForm.setUser(user)
    },
    showSetPasswordForm (user) {
      this.$refs.setPasswordForm.setUser(user)
    },
    getMetaData () {
      api.getDepartments()
        .then(res => {
          this.departments = res.data
          this._departments = res.data
        })
        .catch(err => console.log(err))
      api.getDistricts()
        .then(res => {
          this.districts = res.data
        })
        .catch(err => console.log(err))
    }
  },

  mounted () {
    this.getTableData()
  },
  created () {
    this.getMetaData()
  },

  watch: {
    deleteUserStatus () {
      if (this.deleteUserRequest.isSuccess()) {
        this.$notify.success({
          title: 'User deleted '
        })
      } else if (this.deleteUserRequest.isFailure()) {
        this.$notify.error({
          title: 'Delete user failed',
          message: this.deleteUserRequest.error.message
        })
      }
    },
    updateUserStatus () {
      if (this.editUserRequest.isSuccess()) {
        this.$notify.success({
          title: 'Updated successfully'
        })
        this.dialogProfileVisible = false
      } else if (this.editUserRequest.isFailure()) {
        this.$notify.error({
          title: 'Updated failed',
          message: this.editUserRequest.error.message
        })
      }
    }
  }
}
</script>

<style scoped>
.user-management {
  display: flex;
  padding: 10px;
  flex-flow: column;
  height: 100%;
}
</style>



