import Cesium from 'cesium/Cesium'
import SatelliteImagesController from './SatelliteImagesController'

class CompareModeController {
  constructor (map, splitPosition = 0) {
    this._map = map
    this._splitPosition = splitPosition
    this._leftCompareGroupLayer = SatelliteImagesController.newInstance(map)
    this._rightCompareGroupLayer = SatelliteImagesController.newInstance(map)
  }

  addCompareLayers (side, layers) {
    const group = side === 'left' ? this._leftCompareGroupLayer : this._rightCompareGroupLayer
    for (let i = 0, len = layers.length; i < len; i++) {
      const layer = group._addImage(layers[i])
      if (layer instanceof Cesium.ImageryLayer) {
        layer.splitDirection = side === 'left' ? Cesium.ImagerySplitDirection.LEFT : Cesium.ImagerySplitDirection.RIGHT
      }
    }
  }

  removeCompareLayers (side, layers) {
    const group = side === 'left' ? this._leftCompareGroupLayer : this._rightCompareGroupLayer
    group.removeImages(layers)
  }

  active (splitPosition) {
    this.update(splitPosition)
  }

  deactive () {
    this._leftCompareGroupLayer.clear()
    this._rightCompareGroupLayer.clear()
  }

  update (splitPosition) {
    if (!isNaN(splitPosition)) { // Do not use Number.isNaN, it's bug
      this._splitPosition = splitPosition
    }

    const mapRect = this._map.container.getBoundingClientRect()
    this._map.scene.imagerySplitPosition = (this._splitPosition - mapRect.left) / mapRect.width
  }
}

export default CompareModeController



