<template>
  <div class="dashboard-map-container">
    <div id="dashboard-map"></div>
    <div class="legend-view">
      <v-btn v-if="!seen" icon color="#37474f" dark @click="seen = !seen">
        <v-icon>menu</v-icon>
      </v-btn>

      <v-card v-if="seen" >
        <v-toolbar color="#37474f" flat dense dark>
          <v-btn icon dark @click="seen = !seen">
            <v-icon>menu</v-icon>
          </v-btn>
          <v-toolbar-title>Schemes overview</v-toolbar-title>
        </v-toolbar>
        <v-list dense style="background-color: #bdbdbd">
          <v-list-group
            lazy
            v-for="item of fundingGroups"
            :key="item.id"
            no-action
          >
            <v-list-tile
              slot="activator">
              <v-list-tile-avatar>
                <v-icon>location_on</v-icon>
              </v-list-tile-avatar>
             <v-list-tile-content>
                <v-list-tile-title>{{ item.name }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile
              @click="changeFundingFilter(subItem.id)"
              v-for="subItem of item.funding_agencies"
              :key="subItem.id">
              <v-list-tile-avatar>
                <v-icon :color="getStyle(subItem.id).markerColor">location_on</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ `${subItem.name} (${subItem.constructions_count})`}}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list-group>
        </v-list>
      </v-card>
    </div>

  </div>
</template>

<script>
  import api from '@/api'
  import L from 'leaflet'
  import 'leaflet/dist/leaflet.css'
  import 'leaflet.gridlayer.googlemutant'

  L.Icon.Default.imagePath = '/static/img/leaflet/'

  // Marker Cluster
  import 'leaflet.markercluster'
  import 'leaflet.markercluster/dist/MarkerCluster.css'
  import 'leaflet.markercluster/dist/MarkerCluster.Default.css'
  import Vue from 'vue'
  import { Carousel, CarouselItem } from 'element-ui'
  import OrderPopup from './OrderPopup'
  import { mapGetters } from 'vuex'

  let map = {}
  let markerGroup = {}
  const orderMapper = {} // Map order with marker { [order.id]: L.Marker }

  Vue.use(Carousel)
  Vue.use(CarouselItem)
  export const popupComp = new Vue(OrderPopup).$mount()

  const orderPopup = L.popup().setContent(popupComp.$el)

  export default {
    name: 'MapView',
    created () {
      this.getFundingAgencies()
    },
    computed: {
      ...mapGetters([
        'me'
      ])
    },
    data () {
      return {
        showLegend: false,
        fundingGroups: undefined,
        markerColor: 'red',
        markerName: 'Jonny',
        agencies: [],
        seen: false,
        click_down: true,
        hex: ['#297afb', '#2898fb', '#01d8fd', '#297afb', '#2898fb', '#01d8fd']
      }
    },
    methods: {
      getStyle (fundingAgencyId) {
        let markerColor
        let markerName
        if (!fundingAgencyId) {
          markerColor = '#00000f'
          markerName = 'UNDEFINED'
          return {markerColor, markerName}
        }
        switch (fundingAgencyId) {
          case 1:
            markerColor = '#DD2C00'
            markerName = 'BADP'
            break
          case 2:
            markerColor = '#E64A19'
            markerName = 'MPLADS'
            break
          case 3:
            markerColor = '#EF6C00'
            markerName = 'SIDF'
            break
          case 4:
            markerColor = '#FF9800'
            markerName = 'NEC'
            break
          case 5:
            markerColor = '#FFB74D'
            markerName = 'NLCPR'
            break
          case 6:
            markerColor = '#FFCCBC'
            markerName = 'NEISIDS'
            break
          case 7:
            markerColor = '#1B5E20'
            markerName = 'MGNREGS'
            break
          case 8:
            markerColor = '#2E7D32'
            markerName = 'DdAY-NRLM'
            break
          case 9:
            markerColor = '#388E3C'
            markerName = 'DDU-GKY'
            break
          case 10:
            markerColor = '#43A047'
            markerName = 'PMGSY'
            break
          case 11:
            markerColor = '#4CAF50'
            markerName = 'NSAP'
            break
          case 12:
            markerColor = '#66BB6A'
            markerName = 'PMAY'
            break
          case 13:
            markerColor = '#81C784'
            markerName = 'PMAY-G'
            break
          case 14:
            markerColor = '#A5D6A7'
            markerName = 'SBM'
            break
          case 15:
            markerColor = '#C8E6C9'
            markerName = 'SBM-G'
            break
          case 16:
            markerColor = '#01579B'
            markerName = 'NRDWP'
            break
          case 17:
            markerColor = '#0277BD'
            markerName = 'PMKSY-(IWMP)'
            break
          case 18:
            markerColor = '#0288D1'
            markerName = 'DILRMP'
            break
          case 19:
            markerColor = '#039BE5'
            markerName = 'DDUGJY'
            break
          case 20:
            markerColor = '#03A9F4'
            markerName = 'SPMRM-(NRuM)'
            break
          case 21:
            markerColor = '#29B6F6'
            markerName = 'HRIDAY'
            break
          case 22:
            markerColor = '#4FC3F7'
            markerName = 'BADP'
            break
          case 23:
            markerColor = '#81D4FA'
            markerName = '"Smart City Mission"'
            break
          case 24:
            markerColor = '#B3E5FC'
            markerName = 'UDAY'
            break
          case 25:
            markerColor = '#004D40'
            markerName = 'PMFBY'
            break
          case 26:
            markerColor = '#00695C'
            markerName = 'NHM'
            break
          case 27:
            markerColor = '#00796B'
            markerName = 'SSA'
            break
          case 28:
            markerColor = '#00897B'
            markerName = 'ICDS'
            break
          case 29:
            markerColor = '#009688'
            markerName = 'Mid-Day Meal Scheme'
            break
          case 30:
            markerColor = '#26A69A'
            markerName = 'PMUY'
            break
          case 31:
            markerColor = '#4DB6AC'
            markerName = 'Jal Marg Vikas Project'
            break
          case 32:
            markerColor = '#80CBC4'
            markerName = 'PMKVY'
            break
          case 33:
            markerColor = '#FBE9E7'
            markerName = 'DADA'
            break
          case 34:
            markerColor = '#FFCCBC'
            markerName = 'SIDF'
            break
          case 35:
            markerColor = '#FFAB91'
            markerName = 'MLALADS'
            break
          case 36:
            markerColor = '#FF8A65'
            markerName = 'UNTIED Fund'
            break
          case 37:
            markerColor = '#FF7043'
            markerName = 'Budget Announcement'
            break
          case 38:
            markerColor = '#FF5722'
            markerName = 'Non-BADP Block'
            break
          case 39:
            markerColor = '#263238'
            markerName = 'RIDF'
            break
          case 40:
            markerColor = '#4A148C'
            markerName = 'SADA'
            break
          case 41:
            markerColor = '#6A1B9A'
            markerName = 'MSDP'
            break
          case 42:
            markerColor = '#7B1FA2'
            markerName = 'SPA'
            break
          case 43:
            markerColor = '#8E24AA'
            markerName = 'DoTCL'
            break
          case 44:
            markerColor = '#9C27B0'
            markerName = 'NRWP'
            break
          case 45:
            markerColor = '#AB47BC'
            markerName = 'ARTICLE-275'
            break
          case 46:
            markerColor = '#BA68C8'
            markerName = 'NWRD'
            break
          case 47:
            markerColor = '#CE93D8'
            markerName = 'SSF'
            break
          case 48:
            markerColor = '#D50000'
            markerName = 'NOT CONTACT'
            break
          default:
            markerColor = '#00000f'
            markerName = 'Other'
        }
        let tmp = this.agencies.find(val => val.id === fundingAgencyId)
        if (tmp) {
          markerName = tmp.name
        }
        return {markerColor, markerName}
      },
      changeFundingFilter (id) {
        this.$emit('changeFundingFilter', id)
      },
      async addConstructions (constructions) {
        if (this._marker) {
          this._marker.remove()
        }
        if (this.agencies.length === 0) {
          let res = await api.getFundingAgencies()
          this.agencies = res.data
        }
        markerGroup.clearLayers()
        let markers = constructions.map(construction => {
          const latLng = [+construction.latitude, +construction.longitude]
          const style = this.getStyle(construction.funding_agency_id)
          const marker = L.marker(latLng, {
            icon: L.divIcon({
              className: 'svg-marker',
              html: `<div style="margin: -16px 0px 16px -36px;color: white;"><textarea rows="1" cols="10" style="background-color:${style.markerColor}; font-weight: bold;overflow: auto;border-radius: 1px;border: 1px solid #ccc;white-space: normal;text-align: justify;text-align-last: center;-moz-text-align-last: center;">${style.markerName}</textarea></div><svg style="width: 24px; height: 48px; position: relative; top: -30px; left: -7px;" aria-hidden="true" data-prefix="fas" data-icon="map-marker-alt" class="svg-inline--fa fa-map-marker-alt fa-w-12" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512"><path style="fill: ${style.markerColor};" fill="currentColor" d="M172.268 501.67C26.97 291.031 0 269.413 0 192 0 85.961 85.961 0 192 0s192 85.961 192 192c0 77.413-26.97 99.031-172.268 309.67-9.535 13.774-29.93 13.773-39.464 0zM192 272c44.183 0 80-35.817 80-80s-35.817-80-80-80-80 35.817-80 80 35.817 80 80 80z"></path></svg>`,
              title: 'construction.name'
            })
          })
          marker.on('click', () => {
            api.showConstructions(construction.id).then(res => {
              this.$emit('constructionSelected', res.data)
              popupComp.construction = res.data
              popupComp.linkAble = this.me.hasRole(6) || this.me.hasRole(5)
              marker.bindPopup(orderPopup, {
                maxWidth: 370,
                minWidth: 370
              })
              this.$nextTick(() => {
                marker.openPopup()
              })
            })
          })
          orderMapper[construction.id] = marker
          return marker
        })
        markerGroup.addLayers(markers)
      },
      zoomToConstruction (construction) {
        if (construction) {
          const marker = orderMapper[construction.id]
          if (marker) {
            map.setView(marker.getLatLng(), 16)
          }
          if (this._marker) {
            this._marker.remove()
          }
          let myIcon = L.icon({
            iconUrl: '/static/img/leaflet/marker-icon-yellow.png',
            iconRetinaUrl: '/static/img/leaflet/marker-icon-yellow-2x.png',
            shadowUrl: '/static/img/leaflet/marker-shadow.png',
            iconSize: [25, 41],
            iconAnchor: [12, 41],
            popupAnchor: [1, -34],
            tooltipAnchor: [16, -28],
            shadowSize: [41, 41]
          })
          popupComp.construction = construction
          popupComp.linkAble = this.me.hasRole(6) || this.me.hasRole(5)
          this._marker = L.marker(marker.getLatLng(), {icon: myIcon})
          try {
            this._marker.bindPopup(orderPopup, {
              maxWidth: 370,
              minWidth: 370
            })
          } catch (error) {
            console.error(error)
          }
          map.addLayer(this._marker)
        }
      },
      _createStreetLayer () {
        // const url = 'https://api.mapbox.com/styles/v1/vthai5292/cj0dmhfrt001b2smng55mxqlv/tiles/256/{z}/{x}/{y}?access_token={token}'
        // const token = 'pk.eyJ1IjoidnRoYWk1MjkyIiwiYSI6ImNpeGJhYnhwajAwOW4yb2xmNmZrZWttZXgifQ.iJQElgOme8b2Mpc6vJKyHg'
        const attribution = ''
        const url = 'https://{s}.tiles.mapbox.com/v3/planet.j92p3g27/{z}/{x}/{y}.png'
        const subdomains = ['a', 'b', 'c', 'd']
        const pane = 'tilePane'

        return L.tileLayer(url, {
          attribution,
          // token,
          pane,
          subdomains,
          updateWhenZooming: false,
          updateInterval: 1000,
          maxZoom: 19,
          maxNativeZoom: 15
        })
      },
      _createLabelLayerWMS () {
        const attribution = ''
        const pane = 'tilePane'
        // const url = 'https://{s}.tiles.mapbox.com/v3/planet.jh0b3oee/{z}/{x}/{y}.png'
        const url = 'https://arunachalmonitoring.com/geoserver/construction/wms?'
        return L.tileLayer.wms(url, {
          layers: 'arunachal',
          attribution,
          pane,
          maxZoom: 19,
          format: 'image/png',
          tileSize: 2048,
          transparent: true,
          maxNativeZoom: 18

        })
      },
      _createLabelLayer () {
        const attribution = ''
        const pane = 'tilePane'
        const subdomains = ['1', '2', '3', '0']
        const url = 'https://tile{s}.imagetrekk.com/gee/query?request=VectorMapsRaster&channel=1000&version=7&x={x}&y={y}&z={z}'
        return L.tileLayer(url, {
          attribution,
          // token,
          pane,
          subdomains,
          updateWhenZooming: false,
          updateInterval: 1000,
          maxZoom: 19,
          maxNativeZoom: 16
        })
      },
      _createGoogleLayer () {
        return L.gridLayer.googleMutant({
          type: 'satellite'
        })
      },
      _createGoogleStreetLayer () {
        return L.gridLayer.googleMutant({
          type: 'roadmap'
        })
      },
      getFundingAgencies () {
        api.getLegends()
          .then(res => {
            this.fundingGroups = res.data
          })
      },
      getColor (index) {
        return 'color:red;'
      }
    },

    mounted () {
      // Init map
      this._streetLayer = this._createStreetLayer()
      this._labelLayer = this._createLabelLayerWMS()
      this._googleLayer = this._createGoogleLayer()
      this._googleStreet = this._createGoogleStreetLayer()

      map = L.map('dashboard-map', {
        maxZoom: 19,
        attributionControl: false,
        layers: [
          this._googleStreet, this._labelLayer
        ]
      })
      this._baseMapLayers = {
        'Base Layer': this._streetLayer,
        'Google Street Layer': this._googleStreet,
        'Google Satellite Layer': this._googleLayer
      }

      this._overlayMapLayers = {
        'Label Layer': this._labelLayer
      }

      L.control.layers(this._baseMapLayers, this._overlayMapLayers, {
        position: 'bottomright'
      }).addTo(map)

      markerGroup = L.markerClusterGroup(
        {
          iconCreateFunction: function (cluster) {
            let childCount = cluster.getChildCount()
            let c = ' marker-cluster-'
            if (childCount < 10) {
              c += 'small'
            } else if (childCount < 100) {
              c += 'medium'
            } else {
              c += 'large'
            }
            return new L.DivIcon({ html: '<div style="background-color:#757575;border:2px solid #BDBDBD; height:40px; border-radius:50%; -moz-border-radius:50%; -webkit-border-radius:50%; width:40px;text-align: center;padding: 10px 0px 0px 0px;"><span style="color:white">' + childCount + '</span></div>', className: 'Jmarker-cluster' + c })
          }
        }
      ).addTo(map)
      map.fitBounds([
        [26.6311, 91.5754],
        [29.4833, 97.40909]
      ])
      map.on('click', () => {
        this.$emit('constructionUnSelected')
        if (this._marker) {
          this._marker.remove()
        }
      })
    },
    components: {
      OrderPopup
    }
  }
</script>

<style>
.svg-marker {
  background: none;
  border: none;
}
</style>

<style scoped>
  .dashboard-map-container, #dashboard-map {
    width: 100%;
    height: 100%;
    position: relative;
  }
  #dashboard-map {
    z-index: 1000;
  }

  .legend-view {
    z-index: 1001;
    max-height: 600px;
    overflow-y: auto;
    position: absolute;
    right: 10px;
    top: 10px;
  }
  .Jmarker-cluster-small {
    background-color: black !important;
  }
  .Jmarker-cluster-small div {
    background-color: black !important;
  }
  .Jmarker-cluster-medium {
    background-color: black !important;
  }
  .Jmarker-cluster-medium div {
    background-color: black !important;
  }

  .Jmarker-cluster-large {
    background-color: black !important;
  }
  .Jmarker-cluster-large div {
    background-color: black !important;
  }
</style>



