<template>
  <div class="order-item-container">
    <div class="order-item" @click.stop="moreDetail = !moreDetail" v-bind:class="{'active': construction.highlight}">
      <div style="display: flex; flex-flow: column">
      </div>
      <i class="material-icons construction-icon">domain</i>
      <div class="order-info">
        <div class="title-text-id">{{ genUuid(construction) }}</div>
        <div class="title-text">{{ construction.name }}</div>
        <div class="info-text">{{ construction.orders[0].name }}</div>
      </div>
      <i class="material-icons collapse-icon" v-if="!construction.highlight">{{ moreDetail ? 'keyboard_arrow_down' : 'keyboard_arrow_up' }}</i>
    </div>
    <div v-if="moreDetail || construction.highlight" class="order-item-more-detail">
      <div class="title-text">No. of Visit - {{ construction.orders[0].reports.length }}</div>
      <div class="title-text">No. of Images - {{ imagesCount(construction.orders[0]) }}</div>
      <el-button-group>
        <el-button type="primary" icon="el-icon-download" title="Download report" size="mini" @click="downloadReport(construction.orders[0])"/>
        <el-button type="primary" icon="el-icon-location-outline" title="View in map" size="mini" @click="$emit('viewMap')"/>
        <el-button
          type="primary"
          size="mini"
          title="Link to MIS"
          icon="el-icon-share"
          v-if="!(me.hasRole(6) || me.hasRole(5))"
          @click="link(construction.orders[0])"></el-button>
        <el-button v-if="me.isAdmin" type="danger" icon="el-icon-delete" size="mini" @click="$emit('onDelete', construction.orders[0])"></el-button>
      </el-button-group>
      <!--<OrderEdit
        :orderId="order.id"
        v-if="dialogVisible"
        v-model="dialogVisible"
        :value="dialogVisible"
        @updatedOrder="val => $emit('updatedOrder', val)"
      />-->
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import OrderEdit from './OrderEdit'
  import api from '@/api'

  export default {
    name: 'ConstructionItem',
    components: {OrderEdit},
    data () {
      return {
        moreDetail: false,
        dialogVisible: false,
        imagePre: undefined,
        tmp_order: undefined
      }
    },

    props: {
      temp: false,
      moredetail: false,
      construction: {
        type: Object,
        required: true
      }
    },

    methods: {
      link (order) {
        let form = {
          order_id: order.id
        }
        api.checkExistMIS(form)
          .then(res => {
            let total = res.data.total
            if (total === 0) {
              if (!(this.me.hasRole(6) || this.me.hasRole(5) || this.me.hasRole(4))) {
                window.location.href = '/#/add-project?order_id=' + order.id
              } else {
                this.$notify.warning({
                  title: 'MIS not found',
                  message: ''
                })
              }
            } else {
              this.$notify.warning({
                title: 'MIS has already been created',
                message: ''
              })
              this.$router.push({ path: `projects?order_id=${order.id}` })
            }
//                 this.projects = res.data.data
//                 this.totalProject = res.data.total
          })
          .catch(error => {
            console.log(error)
          })
      },
      genUuid (construction) {
        if (construction.funding_agency) {
          return construction.funding_agency.name + '/' + construction.department.name + '/' + construction.id
        }
        return 'undefined/' + construction.department.name + '/' + construction.id
      },
      downloadReport (order) {
        const id = order.id
        const token = this.token
        window.open(`/api/work_orders/report/${id}?v=${new Date().getTime()}&token=${token}`, '_blank')
      },
      imagesCount (order) {
        let count = 0
        for (const report of order.reports) {
          if (report.image1) count++
          if (report.image2) count++
          if (report.image3) count++
          if (report.image4) count++
        }
        return count
      }
    },

    computed: {
      ...mapGetters(['token', 'me'])
    },
    watch: {
      temp () {
        if (this.temp !== undefined) {
          this.tmp_order = this.temp
        }
      }
    }
  }
</script>

<style scoped>
  .order-item-container {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }

  .order-item {
    display: flex;
    padding: 5px 0;
    align-items: center;
    cursor: pointer;
  }

  .order-item:hover, .order-item:focus {
    background: rgba(150, 150, 150, 0.3);
  }

  .construction-icon {
    font-size: 38px;
    line-height: 42px;
    margin: 0 5px;
    flex: none;
    color: #cecece;
  }

  .order-info {
    flex: 1;
    min-width: 0;
  }

  .title-text {
    font-weight: bold;
    color: #cecece;
    font-size: small;
  }

  .title-text-id {
    color: #cecece;
    font-size: smaller;
  }

  .info-text {
    color: #afafaf;
    font-size: small;
  }

  .collapse-icon {
    color: #cecece;
  }

  .order-item-more-detail {
    padding: 10px 25px;
    background: rgba(0, 0, 0, 0.3);
  }

  .image-view {
    height: 200px;
    width: auto;
    margin: auto;
    padding: 10px;
  }

  .item-view {
    display: flex;
    flex-flow: column
  }
  .active{
    background-color: #007ac1;
  }

  .un-active{
    background-color: transparent;
  }
</style>



