<template>
  <base-layer>
    <div  style="width:100%; height: 100%; overflow-y: auto; padding: 10px">
      <div style=" min-width:1200px; max-width: 1200px; height: 100% ;margin: auto;">
        <el-form ref="form" :model="form" label-position="top" label-width="160px">
          <div class="caption">{{'Project-ID: '+ form.uuid}}</div>
          <el-form-item label="Project Name (*)">
            <el-input :disabled="!me.isAdmin" placeholder="Project Name" :maxlength="500" v-model="form.name">
            </el-input>
          </el-form-item>
          <el-form-item label="Allocation Order No">
            <el-input placeholder="Allocation Order No" v-model="form.order.name">
            </el-input>
          </el-form-item>
          <el-form-item label="Type (*)">
            <el-select :disabled="!me.isAdmin" style="width: 100%" v-model="form.structure.id" placeholder="Project Type">
              <el-option
                v-for="item of structures"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-row type="flex" class="row-bg form-row" justify="space-between">
              <el-col :span="11">
                <label>Department<br></label>
                <el-input placeholder="Department" disabled v-model="form.department.name">
                </el-input>
              </el-col>
              <el-col :span="11">
                <el-row type="flex" class="row-bg " justify="space-between">
                  <el-col :span="11">
                    <label>District<br></label>
                    <el-input placeholder="District" disabled v-model="form.area.dist_name">
                    </el-input>
                  </el-col>
                  <el-col :span="11">
                    <label>Circle<br></label>
                    <el-input placeholder="District" disabled v-model="form.area.name">
                    </el-input>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <el-row type="flex" class="row-bg form-row" justify="space-between">
              <el-col :span="11">
                <el-row  type="flex" class="row-bg " justify="space-between">
                  <el-col :span="11">
                    <label>Estimated Cost of the project (In Lakhs)</label>
                    <el-input :controls="false" placeholder="Estimated Cost" @change="onCostChange" v-model="form.estimated_cost">
                      <template slot="prepend">
                        ₹
                      </template>
                    </el-input>
                  </el-col>
                  <el-col :span="11">
                    <label>Share Ratio (Central/State)(%) (*)<br></label>
                    <el-row  type="flex" class="row-bg " justify="space-between">
                      <el-col :span="10">
                        <el-input-number style="width: 90px" :controls="false" :precision="0" placeholder="Central Share" v-model="form.central_share" @change="handleChangeCentralShare" :min="0" :max="100">
                        </el-input-number>
                      </el-col>
                      <el-col :span="1">:</el-col>
                      <el-col :span="10">
                        <el-input-number style="width: 90px" :controls="false" :precision="0" placeholder="State Share" v-model="form.state_share" @change="handleChangeStateShare" :min="0" :max="100">
                        </el-input-number>
                      </el-col>
                    </el-row>
                  </el-col>
                </el-row>
              </el-col>
              <el-col :span="11">
                <label>CD Block<br></label>
                <el-autocomplete
                  style="width: 100%"
                  v-model="form.block"
                  :fetch-suggestions="querySearchBlock"
                  placeholder="CD Block"
                  value-key="block_name"
                  @select="val => form.block = val.block_name">
                </el-autocomplete>
              </el-col>
            </el-row>
            <el-row type="flex" class="row-bg form-row" justify="space-between">
              <el-col :span="11">
                <label>Share Amount (₹)<br></label>
                <el-row type="flex" class="row-bg" justify="space-between">
                  <el-col :span="11">
                    <el-input :disabled="true" :controls="false"  placeholder="Central Share" :value="form.central_share*form.estimated_cost/100">
                      <template slot="prepend">
                        Center share
                      </template>
                    </el-input>
                  </el-col>
                  <el-col :span="11">
                    <el-input :disabled="true" :controls="false" placeholder="State Share" :value="form.state_share*form.estimated_cost/100">
                      <template slot="prepend">
                        State share
                      </template>
                    </el-input>
                  </el-col>
                </el-row>
              </el-col>
              <el-col :span="11">
                <el-row type="flex" class="row-bg " justify="space-between">
                  <el-col :span="5">
                    <label>Latitude<br></label>
                    <el-input-number disabled style="width: 100%" :controls="false" :min="-90" :max="90" placeholder="Latitude" v-model="lat">
                    </el-input-number>
                  </el-col>
                  <el-col :span="18">
                    <label>In degrees minutes seconds<br></label>
                    <el-row type="flex" class="row-bg " justify="space-between">
                      <el-col :span="5">
                        <el-input-number disabled v-model="latDeg" :min="0" :max="90" placeholder="Degrees" style="width: 100%" :controls="false">
                        </el-input-number>
                      </el-col>
                      <el-col :span="5">
                        <el-input-number disabled v-model="latMin" :min="0" :max="60" placeholder="Minutes" style="width: 100%" :controls="false">
                        </el-input-number>
                      </el-col>
                      <el-col :span="5">
                        <el-input-number disabled v-model="latSec" :min="0" :max="60" placeholder="Seconds" style="width: 100%" :controls="false">
                        </el-input-number>
                      </el-col>
                      <el-col :span="8">
                        <el-radio-group disabled size="mini" v-model="latDir">
                          <el-radio size="mini" label="S">S</el-radio>
                          <el-radio size="mini" label="N">N</el-radio>
                        </el-radio-group>
                      </el-col>
                    </el-row>
                  </el-col>

                </el-row>
              </el-col>
            </el-row>
            <el-row type="flex" class="row-bg form-row" justify="space-between">
              <el-col :span="11">
                <el-row  type="flex" class="row-bg " justify="space-between">
                  <el-col :span="11" v-if="fundingSources.length >= 5">
                    <label>Source of Funding - Central (*)</label>
                    <el-select
                      style="width: 100%"
                      multiple
                      filterable
                      v-model="form.central_funding_agencies"
                      placeholder="Source Of Funding - Central">
                      <el-option-group
                        v-for="source of fundingSources"
                        :key="source.id"
                        :label="source.name">
                        <el-option
                          v-for="funding of source.funding_agencies"
                          :key="funding.id"
                          :label="funding.name"
                          :value="funding.id">
                        </el-option>
                      </el-option-group>
                    </el-select>

                  </el-col>
                  <el-col :span="11">
                    <label>Source of Funding - State (*)</label>
                    <el-select
                      style="width: 100%"
                      multiple
                      filterable
                      v-model="form.state_funding_agencies"
                      placeholder="Source Of Funding - State">
                      <el-option-group
                        v-for="source of fundingSources"
                        :key="source.id"
                        :label="source.name">
                        <el-option
                          v-for="funding of source.funding_agencies"
                          :key="funding.id"
                          :label="funding.name"
                          :value="funding.id">
                        </el-option>
                      </el-option-group>
                    </el-select>
                  </el-col>
                </el-row>
              </el-col>
              <el-col :span="11">
                <el-row type="flex" class="row-bg " justify="space-between">
                  <el-col :span="5">
                    <label>Longitude<br></label>
                    <el-input-number disabled style="width: 100%" :controls="false" :min="-180" :max="180" placeholder="Longitude" v-model="lng">
                    </el-input-number>
                  </el-col>
                  <el-col :span="18">
                    <label>In degrees minutes seconds<br></label>
                    <el-row type="flex" class="row-bg " justify="space-between">
                      <el-col :span="5">
                        <el-input-number disabled v-model="lngDeg" :min="0" :max="180" placeholder="Degrees" style="width: 100%" :controls="false">
                        </el-input-number>
                      </el-col>
                      <el-col :span="5">
                        <el-input-number disabled v-model="lngMin" :min="0" :max="60" placeholder="Minutes" style="width: 100%" :controls="false">
                        </el-input-number>
                      </el-col>
                      <el-col :span="5">
                        <el-input-number disabled v-model="lngSec" :min="0" :max="60" placeholder="Seconds" style="width: 100%" :controls="false">
                        </el-input-number>
                      </el-col>
                      <el-col :span="8">
                        <el-radio-group disabled size="mini" v-model="lngDir">
                          <el-radio size="mini" label="W">W</el-radio>
                          <el-radio size="mini" label="E">E</el-radio>
                        </el-radio-group>
                      </el-col>
                    </el-row>
                  </el-col>

                </el-row>
              </el-col>
            </el-row>

            <add-funding-source v-if="me.isAdmin" :fundingSources="fundingSources" @onUpdate="getFundingSources"/>
          </el-form-item>
          <el-form-item label="Date of Actual Commencement of Work">
              <el-date-picker :disabled="!me.isAdmin && !!form.date_of_actual" placeholder="Pick the date" v-model="form.date_of_actual" type="date" format="yyyy-MM-dd"
                              value-format="yyyy-MM-dd" ></el-date-picker>

          </el-form-item>
          <el-form-item label="Date of Completion as per Agreement">
            <el-date-picker :disabled="!me.isAdmin && !!form.date_of_completion" placeholder="Pick the date" v-model="form.date_of_completion" type="date" format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd" ></el-date-picker>
          </el-form-item>
          <el-form-item>
            Number of days to complete the work: <b>{{totalWorkday}} days</b>. Percent of work complete as on today: <b>{{percentWork}} %</b>
          </el-form-item>
          <el-form-item>
            <label style="font-weight: bold">Financial Progress Summary<br></label>
            <label v-if="form.central_share > 0">Central Share</label>
            <financial-progress v-if="form.central_share > 0" :shared="'central_share'" :financial-progress="form.financial_progresses_central_share"
                                :percent="form.central_share" :estimated_cost="form.estimated_cost"
                                @onAddProgress="val => form.financial_progresses_central_share.push(val)"
                                @onEditProgress="val => onEdit(form.financial_progresses_central_share, val.index, val)"
                                @onDeleteProgress="index => form.financial_progresses_central_share.splice(index, 1)">
            </financial-progress>
            <label v-if="form.state_share > 0">State Share</label>
            <financial-progress v-if="form.state_share > 0" :shared="'state_share'" :financial-progress="form.financial_progresses_state_share"
                                :percent="form.state_share" :estimated_cost="form.estimated_cost"
                                @onAddProgress="val => form.financial_progresses_state_share.push(val)"
                                @onEditProgress="val => onEdit(form.financial_progresses_state_share, val.index, val)"
                                @onDeleteProgress="index => form.financial_progresses_state_share.splice(index, 1)">
            </financial-progress>
          </el-form-item>
          <el-form-item>
            <label style="font-weight: bold">Physical Progress Summary</label>
            <physical-progress :physical-progress="form.physical_progresses"
                               @onAddProgress="val => form.physical_progresses.push(val)"
                               @onEditProgress="val => onEdit(form.physical_progresses, val.index, val)"
                               @onDeleteProgress="index => form.physical_progresses.splice(index, 1)"
                               :edit="false">
            </physical-progress>
          </el-form-item>
          <el-form-item label="Project Gist">
            <el-input placeholder="Project Gist" :rows="3" v-model="form.project_gist" type="textarea">
            </el-input>
          </el-form-item>
          <el-form-item label="Remarks">
            <el-input placeholder="Remarks" :rows="3" v-model="form.remarks" type="textarea">
            </el-input>
          </el-form-item>
          <el-form-item style="float: right">
            <el-button type="primary" @click="onSubmit">Submit</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </base-layer>
</template>
<script>
  import BaseLayer from '../../containers/BaseLayer'
  import api from '@/api'
  import Projects from '../../containers/Projects/Projects'
  import FinancialProgress from '../../containers/Projects/FinancialProgress'
  import PhysicalProgress from '../../containers/Projects/PhysicalProgress'
  import { cloneDeep, pick } from 'lodash'
  import AddFundingSource from './AddFundingSource'
  import {dcToDeg, dcToMin, dcToSec, getError, getQuarter} from '@/utils/projectUtils'
  import { mapGetters } from 'vuex'

  export default {
    data () {
      return {
        structures: [],
        blocks: [],
        fundingSources: [],
        currencies: ['₹', 'Lakh', 'Crore'],
        errors: {},
        form: {
          date_of_actual: undefined,
          date_of_completion: undefined,
          order_name: '',
          order_id: undefined,
          name: '',
          structure_id: undefined,
          department: {},
          area: {},
          structure: {},
          block: '',
          estimated_cost: 0,
          currency: '₹',
          central_share: 0,
          state_share: 0,
          financial_progresses_central_share: [],
          financial_progresses_state_share: [],
          physical_progresses: [],
          central_funding_agencies: [],
          state_funding_agencies: [],
          latitude: 0,
          longitude: 0,
          order: {},
          project_gist: '',
          remarks: ''
        }
      }
    },
    computed: {
      ...mapGetters(['token', 'me']),
      totalWorkday: {
        get () {
          if (this.form.date_of_completion && this.form.date_of_actual) {
            let difTime = new Date(this.form.date_of_completion).getTime() - new Date(this.form.date_of_actual).getTime()
            let days = difTime / (1000 * 3600 * 24)
            return days.toFixed(0)
          }
          return '-'
        }
      },
      percentWork: {
        get () {
          if (this.form.date_of_completion && this.form.date_of_actual) {
            let difTime = new Date().getTime() - new Date(this.form.date_of_actual).getTime()
            let days = difTime / (1000 * 3600 * 24)
            if (days > 0) {
              let percent = (100 * days.toFixed(0) / this.totalWorkday).toFixed(0)
              return percent < 100 ? percent : 100
            } else {
              return '0'
            }
          }
          return '-'
        }
      },
      lat: {
        get () {
          return parseFloat(this.form.latitude.toFixed(6))
        },
        set (val) {
          if (val === undefined || val === '') {
            val = 0
          }
          this.form.latitude = parseFloat(val.toFixed(6))
        }
      },
      latDir: {
        get () {
          return this.form.latitude > 0 ? 'N' : 'S'
        },
        set (val) {
          this.form.latitude = val === 'N' ? Math.abs(this.form.latitude) : -Math.abs(this.form.latitude)
        }
      },
      latDeg: {
        get () {
          return dcToDeg(this.form.latitude)
        },
        set (val) {
          if (val === undefined || val === '') {
            val = 0
          }
          let lat = val + Math.abs(this.form.latitude) - this.latDeg
          lat = parseFloat(lat.toFixed(6))
          this.form.latitude = this.form.latitude < 0 ? -lat : lat
        }
      },
      latMin: {
        get () {
          return dcToMin(this.form.latitude)
        },
        set (val) {
          if (val === undefined || val === '') {
            val = 0
          }
          let lat = val / 60 + this.latDeg + this.latSec / 3600
          lat = parseFloat(lat.toFixed(6))
          this.form.latitude = this.form.latitude < 0 ? -lat : lat
        }
      },
      latSec: {
        get () {
          let val = dcToSec(this.form.latitude)
          return parseFloat(val.toFixed(3))
        },
        set (val) {
          if (val === undefined || val === '') {
            val = 0
          }
          let lat = this.latDeg + this.latMin / 60 + val / 3600
          lat = parseFloat(lat.toFixed(6))
          this.form.latitude = this.form.latitude < 0 ? -lat : lat
        }
      },
      lng: {
        get () {
          return parseFloat(this.form.longitude.toFixed(6))
        },
        set (val) {
          if (!val || val === '') {
            val = 0
          }
          this.form.longitude = parseFloat(val.toFixed(6))
        }
      },
      lngDir: {
        get () {
          return this.form.longitude > 0 ? 'E' : 'W'
        },
        set (val) {
          this.form.longitude = val === 'E' ? Math.abs(this.form.longitude) : -Math.abs(this.form.longitude)
        }
      },
      lngDeg: {
        get () {
          return dcToDeg(this.form.longitude)
        },
        set (val) {
          if (val === undefined || val === '') {
            val = 0
          }
          let lng = val + Math.abs(this.form.longitude) - this.lngDeg
          lng = parseFloat(lng.toFixed(6))
          this.form.longitude = this.form.longitude < 0 ? -lng : lng
        }
      },
      lngMin: {
        get () {
          return dcToMin(this.form.longitude)
        },
        set (val) {
          if (val === undefined || val === '') {
            val = 0
          }
          let lng = val / 60 + this.lngDeg + this.lngSec / 3600
          lng = parseFloat(lng.toFixed(6))
          this.form.longitude = this.form.longitude < 0 ? -lng : lng
        }
      },
      lngSec: {
        get () {
          let val = dcToSec(this.form.longitude)
          return parseFloat(val.toFixed(3))
        },
        set (val) {
          if (val === undefined || val === '') {
            val = 0
          }
          let lng = this.lngDeg + this.lngMin / 60 + val / 3600
          lng = parseFloat(lng.toFixed(6))
          this.form.longitude = this.form.longitude < 0 ? -lng : lng
        }
      }
    },
    components: {
      AddFundingSource,
      PhysicalProgress,
      FinancialProgress,
      Projects,
      BaseLayer
    },
    methods: {
      handleChangeCentralShare (val) {
        this.form.state_share = 100 - val
      },
      handleChangeStateShare (val) {
        this.form.central_share = 100 - val
      },
      querySearchBlock (queryString, cb) {
        let results = queryString ? this.blocks.filter(item => item.block_name.toLowerCase().indexOf(queryString.toLowerCase()) !== -1) : this.blocks
        cb(results)
      },
      onCostChange (str) {
        try {
          this.form.estimated_cost = parseFloat(str)
        } catch (e) {
          this.form.estimated_cost = 0
        }
      },
      onSubmit () {
        let form = cloneDeep(this.form)
        form = {
          id: form.id,
          name: form.name,
          structure_id: form.structure.id,
          central_funding_agencies: form.central_funding_agencies,
          state_funding_agencies: form.state_funding_agencies,
          financial_progresses: form.financial_progresses_central_share.concat(form.financial_progresses_state_share),
          physical_progresses: form.physical_progresses.map(val => pick(val, ['report_id', 'status', 'physical_percent', 'financial_percent', 'cc_status', 'cc_documents', 'cc_date'])),
          project_gist: form.project_gist,
          remarks: form.remarks,
          block: form.block,
          estimated_cost: form.estimated_cost,
          currency: form.currency,
          central_share: form.central_share,
          order_id: form.order.id,
          order_name: form.order.name,
          state_share: form.state_share,
          date_of_actual: form.date_of_actual,
          date_of_completion: form.date_of_completion
        }
        this.errors = getError(form)
        if (!this.errors.valid) {
          delete this.errors.valid
          delete this.errors.alert
          this.$notify({
            title: 'Invalid',
            message: Object.values(this.errors).join(', '),
            type: 'error'
          })
          return
        }
        this.errors = {}
        api.updateProjects(form)
          .then(res => {
            this.$notify({
              title: 'Success',
              type: 'success'
            })
            this.$router.back()
          })
          .catch(error => {
            this.$notify.error({
              title: error.message
            })
            if (error.status_code === 422) {
              this.errors = error.errors
              this.$notify({
                title: 'error',
                message: JSON.stringify(this.errors),
                type: 'error'
              })
            }
          })
      },
      onEdit (items, index, form) {
        items.splice(index, 1, form)
      },
      getBlocks () {
        api.getBlocks(this.form.area.dist_name)
          .then(res => {
            this.blocks = res.data
          })
      },
      getFundingSources () {
        api.getFundingSources()
          .then(res => {
            this.fundingSources = res.data
          })
      }
    },
    mounted () {
      api.showProjects(this.$route.params.id)
        .then(res => {
          let form = res.data
          form.name = form.order.construction.name
          form.area = form.order.construction.area
          form.department = form.order.construction.department
          form.structure = form.order.construction.structure
          form.financial_progresses_central_share = form.financial_progresses.filter(item => item.shared_by === 'central_share')
          form.financial_progresses_state_share = form.financial_progresses.filter(item => item.shared_by === 'state_share')
          form.central_funding_agencies = form.central_funding_agencies.map(item => item.id)
          form.state_funding_agencies = form.state_funding_agencies.map(item => item.id)
          form.physical_progresses = form.order.reports.map(val => {
            let date = new Date(val.local_time)
            return {
              report_id: val.id,
              photos: [{'url': val.image1}, {'url': val.image2}, {'url': val.image3}, {'url': val.image4}],
              year: date,
              quarter: getQuarter(date),
              status: val.physical_progress.status,
              physical_percent: val.physical_progress.physical_percent,
              cc_status: val.physical_progress.cc_status,
              cc_documents: val.physical_progress.cc_documents,
              cc_date: val.physical_progress.cc_date,
              financial_percent: val.physical_progress.financial_percent
            }
          })
          form.latitude = parseFloat(form.order.construction.latitude)
          form.longitude = parseFloat(form.order.construction.longitude)
          form.central_share = parseFloat(form.central_share)
          form.state_share = parseFloat(form.state_share)
          form.estimated_cost = parseFloat(form.estimated_cost)
          this.form = cloneDeep(form)
          this.getBlocks()
        })
      .catch(e => {
        console.log(e)
        this.$notify.error('Cannot load data, retry again!')
        this.$router.back()
      })
    },
    created () {
      api.getStructures()
        .then(res => {
          this.structures = res.data
        })
      this.getFundingSources()
      this.$store.dispatch('changeIndex', '5')
    }
  }
</script>

<style lang="css" scoped>
  .form-view {
    width: 100%;
    min-width: 500px;
    padding: 16px;
    height: 100%;
    overflow-y: scroll;
  }
  .form-row {
    margin-bottom: 20px;
  }
</style>



