<template>
  <div class="top-bar-container">
    <div class="logo-container">
      <img class="logo" src="../../assets/images/Arunachal_Pradesh_Seal.png">
      <div class="logo-title">
        <div class="logo-title-name">Department of Planning & Investment</div>
        <div class="logo-title-region">Government of Arunachal Pradesh</div>
      </div>
    </div>
    <div class="menu-container">
      <el-menu style="border-color: transparent" background-color="#273447" active-text-color="#409eff" class="el-menu-demo"  text-color="#f4f4f5" :default-active="activeIndex" mode="horizontal" :router="true">
        <el-menu-item :route="$router.match('/home')" class="top-bar-item" index="1">Dashboard</el-menu-item>
        <el-menu-item v-if="isAdmin" :route="$router.match('/dashboard')" class="top-bar-item" index="9">Monitoring</el-menu-item>
        <el-menu-item v-if="isAdmin" :route="$router.match('/users/registration')" class="top-bar-item" index="2">User Registration</el-menu-item>
        <el-menu-item v-if="isAdmin" :route="$router.match('/users/management')" class="top-bar-item" index="3">User Management</el-menu-item>
        <el-menu-item :route="$router.match('/projects')" class="top-bar-item" index="5">MIS</el-menu-item>
        <el-menu-item v-if="showKPI" :route="$router.match('/kpi')" class="top-bar-item" index="8">KPI</el-menu-item>
        <el-menu-item :route="$router.match('/imagery')" class="top-bar-item" index="4">Imagery</el-menu-item>
        <!-- <el-menu-item v-if="isAdmin" :route="$router.match('/funding')" class="top-bar-item" index="6">Funding</el-menu-item> -->
        <el-menu-item :route="$router.match('/help')" class="top-bar-item" index="7">Help</el-menu-item>
      </el-menu>
      <el-popover
          ref="popover2"
          placement="bottom"
          width="200"
          trigger="click">
             <div class="user-info">
               <img class="info-avatar" src="../../assets/images/avatar.png"/>
               <div class="info-title">{{ 'Hi, ' + me.name.toUpperCase()}}</div>
               <div class="info-subtitle">{{ me.mobile }}</div>
               <div class="info-bottom-actions"><logout-link type="button"></logout-link></div>
             </div>
      </el-popover>
      <Notification/>
      <el-button v-popover:popover2 type="text" class="top-bar-account"><i class="material-icons">account_circle</i></el-button>
    </div>
  </div>
</template>

<script>
import NavBar from '@/components/NavBar'
import NavBarItem from '@/components/NavBarItem'
import NavigationPopOver from '@/containers/Navigation/NavigationPopOver'
import LogoutLink from '@/containers/LogoutLink'
import { mapGetters, mapActions } from 'vuex'
import Notification from '../Notification'

export default {
  name: 'main-navigation-bar',

  data () {
    return {
      gridData: [{
        date: '2016-05-02',
        name: 'Jack',
        address: 'New York City'
      }, {
        date: '2016-05-04',
        name: 'Jack',
        address: 'New York City'
      }, {
        date: '2016-05-01',
        name: 'Jack',
        address: 'New York City'
      }, {
        date: '2016-05-03',
        name: 'Jack',
        address: 'New York City'
      }],
      defaultActive: '2',
      notification: undefined
    }
  },

  methods: {
    ...mapActions([
      'changeIndex'
    ])
  },

  computed: {
    ...mapGetters([
      'appName',
      'activeIndex',
      'me'
    ]),
    loggedIn () { return this.me.id >= 0 },
    isAdmin () { return this.me.hasRole(1) },
    isDepartmentUser () { return this.isAdmin || this.me.hasRole(2) || this.me.hasRole(5) || this.me.hasRole(6) },
    showKPI () { return this.isAdmin || this.me.hasRole(5) },
    isDistrictUser () { return this.isAdmin || this.me.hasRole(4) }
  },

  components: {
    Notification,
    NavBar,
    LogoutLink,
    NavigationPopOver,
    NavBarItem
  }

}
</script>

<style lang="css" scoped>
  .top-bar-container {
    width: 100%;
    background-color: #273447;
    display: inline-flex;
    flex-flow: row;
  }



  .logo-container {
    display: inline-flex;
    align-items: center;
    flex: 1;
  }

  .logo {
    height: 48px;
    margin: 5px 15px;
  }

  .logo-title-name {
     margin-left: 6px;
     font-size: 14px;
     font-weight: bold;
     color: #ffcc66;
     text-transform: capitalize;
   }

  .logo-title-region {
    margin-left: 6px;
    font-size: 15px;
    font-weight: bold;
    text-transform: capitalize;
    color: #ffcc66;
    text-transform: capitalize;
  }

  .menu-container {
    display: inline-flex;
    align-items: center;
    text-align: center;
  }

  .user-info {
    align-items: center;
    align-content: center;
    text-align: center;
  }
  .info-avatar {
    width: 60px;
    height: 60px;
    border-radius: 30px;
  }
  .info-title {
    font-size: 15px;
    font-weight: bold;
  }
  .info-subtitle {
    font-size: 14px;
  }
  .info-bottom-actions {
    width: 100%;
    padding-top: 16px;
    display: inline-flex;
    flex-flow: row-reverse;
  }
  .top-bar-item {
    justify-content: space-around;
    font-weight: bold;
  }
  .top-bar-account {
    color: white;
    padding: 10px;
  }
  .item {
  margin-top: 10px;
  margin-right: 40px;
  }
  .jonny {
    padding: 0px;
  }

</style>



