<template>
  <base-layer>
    ProjectDetails
  </base-layer>
</template>

<script>
  import BaseLayer from '../../containers/BaseLayer'
  import Projects from '../../containers/Projects/Projects'

  export default {
    components: {
      Projects,
      BaseLayer
    },
    props: {
      projectId: {
        type: String,
        default: 'link'
      }
    },
    created () {
      this.$store.dispatch('changeIndex', '5')
    }
  }
</script>

<style lang="css" scoped>
</style>



