      <el-row style="width: 100%; height: 100%" type="flex" class="row-bg" justify="space-around">
        <el-col class="login-header" :xs="{span: 24, offset: 0}" :sm="{span: 12, offset: 0}" :md="{span: 10, offset: 0}" :lg="{span: 10, offset: 7}" :xl="{span: 8, offset: 7}">
          SATELLITE BASED MONITORING & GEO-TAGGING PORTAL
        </el-col>
        <el-col class="login-view" :xs="{span: 24, offset: 0}" :sm="{span: 12, offset: 0}" :md="{span: 10, offset: 0}" :lg="{span: 10, offset: 12}" :xl="{span: 8, offset: 14}">
