                      :type="'password'"
                      v-model="loginForm.password"
                      single-line
                      @keyup.enter.native.prevent.stop="login(loginForm)"
                      label="Password"
                      hint="Password"
                      placeholder="Enter your Password">
                    </v-text-field>
                  </div>
                  <v-btn color="#0078ff" style="font-weight: bold; color: #FFFFFF" @click="login(loginForm)" :loading="loginRequest.isRequesting()">SIGN IN</v-btn>
                </v-layout>
              </v-container>
            </v-card>
          </div>
          <div class="login-footer-content">
            <div class="login-footer">
