    </div>
  </div>
</template>

<script>
import BaseLayer from '@/containers/BaseLayer'
import { mapGetters, mapActions } from 'vuex'
import { REMEMBER_ME } from '@/store/mutation-types'

export default {
  data () {
    return {
      redirectAfterLogin: '/home',

      loginForm: {
        name: '',
        password: ''
      }
    }
  },

  computed: {
    ...mapGetters(['loginRequest', 'appName', 'rememberMe']),

    status () { return this.loginRequest.status },

    rememberToken: {
      get () { return this.rememberMe },
      set (value) { this.$store.commit(REMEMBER_ME, value) }
    }
  },

  watch: {
    status (val, old) {
      if (this.loginRequest.isSuccess()) {
        this.$router.replace(this.$route.query.redirect || this.redirectAfterLogin)
      } else if (this.loginRequest.isFailure() && !this.loginRequest.error.isUnprocessableEntity()) {
        this.$notify.error({
          title: 'Login failed',
          message: this.loginRequest.error.message
        })
      }
    }
  },

  methods: {
    ...mapActions(['login'])
  },

  components: {
    BaseLayer
  }
}
</script>

<style lang="css" scoped>
  @media only screen and (max-width: 1535px) {
    .login-page-1 {
      zoom: 80%
    }
  }

  .login-page-1 {
    width: 100%;
    height: 100%;
    background: url('../assets/images/background.jpg') no-repeat center center fixed;
    background-size: cover;
  }

  .login-page {
    width: 100%;
    height: 100%;
    display: flex;
    background-image: -webkit-linear-gradient(160deg, #219179 50%, transparent 50%);
    min-height: 200px;
    text-align: center;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
  }

  .login-header {
    width: 100%;
    display: inline-flex;
    justify-content: center;
    position: absolute;
    top: 10px;
    color: #ffc800;
    font-weight: bold;
    font-size: 22px;
    text-shadow: 4px 2px #000000;
  }

  .login-logo {
    margin-bottom: 20px;
    height: 125px;
  }

  .login-title {
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    font-size: 20px;
  }

  .login-sub-title {
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: small;
    font-weight: bold;
    margin-bottom: 40px;
  }

  .login-view {
    max-width: 460px;
    height: 100%;
    position: relative;
    display: flex;
    border-radius: 10px;
    flex-flow: row;
    align-items: center;
  }

  .login-form {
    width: 100%;
    display: inline-flex;
    flex-flow: column;
    border-radius: 26px;
    margin-bottom: 70px;
  }

  .login-footer-content {
    width: 100%;
    display: inline-flex;
    justify-content: center;
    position: absolute;
    bottom: 30px;
  }
  .login-footer {
    font-weight: bold;
    text-align: center;
    color: #FFFFFF;
    font-family: 'Roboto', sans-serif;
    font-size: small;
  }

  .margin-icon {
    margin-top: 20px;
    margin-right:6px;
  }

  @keyframes marquee {
    0% { left: 0; }
    100% { left: -100%; }
  }
</style>



