<template>
<base-layer
  :show-header="false"
  :show-footer="false">
  <div class="register-page">
    <div class="register-background"></div>

    <el-card class="register-card">
      <div class="app-name">
        <router-link to="/"><img src="../assets/images/skymap-logo-black.png" :alt="appName"></router-link>
      </div>

      <el-form :model="registerForm" :rules="rules" ref="registerForm">
        <!-- Name -->
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="First Name" prop="first_name">
              <el-input v-model="registerForm.first_name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Last Name" prop="last_name">
              <el-input v-model="registerForm.last_name"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <!-- Email -->
        <el-form-item label="Email" prop="email" :error="registerRequest.error.get('email')">
          <el-input v-model="registerForm.email"></el-input>
        </el-form-item>

        <!-- Password -->
        <el-form-item label="Password" prop="password">
          <el-input type="password" v-model="registerForm.password"></el-input>
        </el-form-item>

        <!-- Confirm Password -->
        <el-form-item label="Confirm Password" prop="password_confirmation">
          <el-input type="password" v-model="registerForm.password_confirmation"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button class="register-button" :loading="registerRequest.isRequesting()" type="primary" @click="submitForm('registerForm')">Register</el-button>
        </el-form-item>

        <div class="access-features-group">
          <router-link to="/login">&larr; Back to Login</router-link>
        </div>
      </el-form>
    </el-card>
  </div>
</base-layer>
</template>

<script>
import BaseLayer from '@/containers/BaseLayer'
import { mapGetters } from 'vuex'

export default {
  data () {
    const validatePasswordConfirmation = (rule, value, callback) => {
      if (value !== this.registerForm.password) {
        callback(new Error('The password confirmation does not match.'))
      } else {
        callback()
      }
    }

    return {
      registerForm: {
        fist_name: '',
        last_name: '',
        email: '',
        password: '',
        password_confirmation: ''
      },

      rules: {
        first_name: [
          { required: true, message: 'Please input first name', trigger: 'blur' }
        ],

        last_name: [
          { required: true, message: 'Please input last name', trigger: 'blur' }
        ],

        email: [
          { required: true, message: 'Please input email address', trigger: 'blur' },
          { type: 'email', message: 'Please input correct email address', trigger: 'blur,change' }
        ],

        password: [
          { required: true, message: 'Please input password', trigger: 'blur' },
          { min: 6, message: 'Password must be at least 6 characters', trigger: 'blur' }
        ],

        password_confirmation: [
          { required: true, message: 'Please input confirm password', trigger: 'blur' },
          { validator: validatePasswordConfirmation, trigger: 'blur' }
        ]
      },

      message: ''
    }
  },

  computed: {
    ...mapGetters([
      'appName',
      'registerRequest'
    ]),

    status () { return this.registerRequest.status }
  },

  watch: {
    status (val, old) {
      if (this.registerRequest.isSuccess()) {
        this.handleRegisterSuccess()
      } else if (this.registerRequest.isFailure() && !this.registerRequest.error.isUnprocessableEntity()) {
        this.handleRegisterFailure()
      }
    }
  },

  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.register()
        } else {
          return false
        }
      })
    },

    register () {
      this.$store.dispatch('register', this.registerForm)
    },

    handleRegisterSuccess () {
      this.$confirm('Your account have been created successfully.', 'Success', {
        confirmButtonText: 'Goto Login Page',
        cancelButtonText: 'Cancel',
        type: 'success'
      }).then(() => {
        this.$router.replace('/login')
      }).catch(() => {})
    },

    handleRegisterFailure () {
      this.$notify.error({
        title: 'Register failed',
        message: this.registerRequest.error.message
      })
    }
  },

  components: {
    BaseLayer
  }
}
</script>

<style lang="css" scoped>
.app-name {
  font-weight: bold;
  font-size: 26px;
  text-align: center;
  margin: 10px 0;
}

.app-name img {
  height: 84px;
}

.register-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.title {
  font-size: 30px;
  font-weight: bold;
  text-align: center;
  padding: 10px 20px;
}

.register-card {
  width: 400px;
}

.register-title {
   font-size: 24px;
   font-weight: bold;
}

@media (max-width: 768px) {
  .register-card {
    width: 90%;
  }
}

.register-button {
  width: 100%;
}

.register-background {
  height: 50vh;
  width: 100vw;
  z-index: -1;
  background: var(--brand-primary-dark);
  position: absolute;
  top: 0;
  left: 0;
}

.access-features-group {
  display: flex;
  margin-top: 10px;
}

.access-features-group a {
  color: var(--text-color);
  text-decoration: none;
}

.access-features-group span {
  margin: 0 5px;
}
</style>



