<template>
  <div>
    <el-dialog
      :v-loading="loading"
      title="Edit Project"
      :visible.sync="showForm">
      <el-form label-width="120px">
        <el-form-item>
          <div style="float: right; margin-bottom: 10px">
            <el-button
              type="primary"
              size="mini"
              @click="updateOrder">Update Project</el-button>
<!--            <el-button-->
<!--              type="primary"-->
<!--              size="mini"-->
<!--              @click="$router.push({ path: `add-project?order_id=${orderId}` })">Link to MIS</el-button>-->
          </div>

        </el-form-item>
        <el-form-item label="Project Name">
          <el-input
            autosize
            placeholder="Please input Project Name"
            :value="form.project_name"
            v-model="form.project_name">
          </el-input>
        </el-form-item>
        <el-form-item label="Work Order">
          <el-input
            autosize
            placeholder="Please input Work Order No"
            :value="form.name"
            v-model="form.name">
          </el-input>
        </el-form-item>
        <el-form-item
          label="Source of Funding">
          <el-select
            autosize
            style="width: 100%"
            v-model="form.funding_agency_id"
            :value="form.funding_agency_id"
            placeholder="Select Source of Funding">
            <el-option
              v-for="item of funding_agencies"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="Department">
          <el-select
            autosize
            style="width: 100%"
            v-model="form.department_id"
            :value="form.department_id"
            placeholder="Select Department">
            <el-option
              v-for="item of departments"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="Type">
          <el-select
            style="width: 100%"
            v-model="form.structure_id"
            :value="form.structure_id"
            placeholder="Select Structure Type">
            <el-option
              v-for="item of structures"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div>Activities</div>
      <form v-for="(report, index) of order.reports">
        <div style="padding: 10px">
          <p style="float:left"><b>VISIT - {{ index+1 }}</b></p>
          <el-button v-if="order.reports && order.reports.length > 1" style="float:right" type="danger" icon="el-icon-delete" size="mini" @click="deleteReport(index, report)">Delete</el-button>
          <el-button type="primary" style="float:right; margin-right:10px;" icon="el-icon-upload2" size="mini" @click="updateReport(index, report)">Save</el-button>
        </div>
        <el-input
          style="padding: 10px"
          type="textarea"
          :rows="2"
          placeholder="Please input"
          :value="report.description"
          v-model="report.description">
        </el-input>
        <el-row>
          <el-col :span="12">
            <div class="item-view">
              <img class="image-view" :src="report.image1" @click="imagePre = report.image1">
              <input style="padding: 10px" type="file" @change="val => onImgChange1(index, val)">
            </div>
          </el-col>
          <el-col :span="12">
            <div class="item-view">
              <img class="image-view" :src="report.image2" @click="imagePre = report.image2">
              <input style="padding: 10px" type="file" @change="val => onImgChange2(index, val)">
            </div>
          </el-col>
          <el-col :span="12">
            <div class="item-view">
              <img class="image-view" :src="report.image3" @click="imagePre = report.image3">
              <input style="padding: 10px" type="file" @change="val => onImgChange3(index, val)">
            </div>
          </el-col>
          <el-col :span="12">
            <div class="item-view">
              <img class="image-view" :src="report.image4" @click="imagePre = report.image4">
              <input style="padding: 10px" type="file" @change="val => onImgChange4(index, val)">
            </div>
          </el-col>
        </el-row>
        <div>
        </div>
      </form>
    </el-dialog>
    <el-dialog
      @close="imagePre = undefined"
      :visible="showPreview">
      <a :href="imagePre" target="_blank" title="Click to download image">
        <img :src="imagePre">
      </a>
    </el-dialog>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import api from '@/api'

  export default {
    name: 'OrderEdit',

    data () {
      return {
        loading: false,
        order: {},
        departments: [],
        funding_agencies: [],
        structures: [],
        form: {
          project_name: undefined,
          name: undefined,
          department_id: undefined,
          funding_agency_id: undefined,
          structure_id: undefined
        },
        imagePre: undefined,

        construction: undefined
      }
    },

    props: {
      value: {
        type: Boolean,
        required: true
      },
      orderId: {
        type: Number,
        required: true
      }
    },

    methods: {

      onImgChange1 (index, event) {
        this.order.reports[index].newImage1 = event.target.files[0]
      },

      onImgChange2 (index, event) {
        this.order.reports[index].newImage2 = event.target.files[0]
      },
      onImgChange3 (index, event) {
        this.order.reports[index].newImage3 = event.target.files[0]
      },
      onImgChange4 (index, event) {
        this.order.reports[index].newImage4 = event.target.files[0]
      },
      deleteReport (index, report) {
        this.$confirm(`This will permanently delete the activity. Continue?`, 'Warning', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }).then(() => {
          api.deleteReport(report.id)
            .then(_ => {
              this.$notify({
                type: 'success',
                message: 'Delete completed'
              })
              this.refreshData()
            })
            .catch(_ => {
              this.$notify({
                type: 'error',
                message: 'Delete Failed'
              })
            })
        })
      },

      updateReport (index, report) {
        let form = new FormData()
        if (report.newImage1) {
          form.set('image1', report.newImage1)
        }
        if (report.newImage2) {
          form.set('image2', report.newImage2)
        }
        if (report.newImage3) {
          form.set('image3', report.newImage3)
        }
        if (report.newImage4) {
          form.set('image4', report.newImage4)
        }
        form.set('description', report.description)
        api.updateReport(report.id, form)
          .then(res => {
            this.$notify({
              type: 'success',
              message: 'Updated success'
            })
            this.refreshData()
          })
          .catch(error => {
            this.$notify({
              type: 'error',
              message: 'Updated failed'
            })
            console.log(error)
          })
      },

      updateOrder () {
        api.updateOrder(this.order.id, this.form)
          .then(res => {
            this.$notify({
              type: 'success',
              message: 'Updated success'
            })
            this.refreshData()
          })
          .catch(error => {
            this.$notify({
              type: 'error',
              message: 'Updated failed'
            })
            console.log(error)
          })
      },

      refreshData () {
        this.loading = true
        api.orderDetails(this.orderId).then(res => {
          if (res.data && res.data !== null) {
            this.order = res.data
            this.$emit('updatedOrder', this.order)
          } else {
            this.showForm = false
          }
          this.loading = false
        }).catch(_ => {
          this.loading = false
        })
      }
    },

    computed: {
      ...mapGetters(['token']),

      showForm: {
        get () {
          return this.value
        },
        set (val) {
          this.$emit('input', val)
        }
      },
      showPreview () {
        return this.imagePre !== undefined
      }
    },
    mounted () {
      this.loading = true
      api.orderDetails(this.orderId).then(res => {
        this.order = res.data
        this.form.department_id = this.order.construction.department_id
        this.form.structure_id = this.order.construction.structure_id
        this.form.funding_agency_id = this.order.construction.funding_agency_id
        this.form.name = this.order.name
        this.form.project_name = this.order.construction.name
        this.form.department = this.order.department
        this.form.funding_agency = this.order.funding_agency
        this.loading = false
      }).catch(_ => {
        this.loading = false
      })
    },
    created () {
      api.getDepartments()
        .then(res => {
          this.departments = res.data
        })
      api.getStructures()
        .then(res => {
          this.structures = res.data
        })
      api.getFundingAgencies()
        .then(res => {
          this.funding_agencies = res.data
        })
    }
  }

</script>

<style scoped>

  .image-view {
    height: 200px;
    width: auto;
    margin: auto;
    padding: 10px;
  }

  .item-view {
    display: flex;
    flex-flow: column
  }
</style>



