<template>
<a :href="link" :target="newTab ? '_blank' : '_self'" class="services-item">
  <i class="material-icons">{{ materialIcon }}</i>
  <div>{{ text }}</div>
</a>
</template>

<script>
export default {
  name: 'navigation-item',

  props: {
    link: { type: String },
    materialIcon: { type: String },
    text: { type: String },
    newTab: { type: Boolean, default: false }
  }
}
</script>

<style lang="css" scoped>
.services-item {
  width: 100px;
  height: 100px;
  text-decoration: none;
  color: black;
  transition: 0.3s;
}

.services-item:hover, .services-item:focus {
  transform: scale(1.1);
  color: #20A0FF;
}

.services-item > i {
  display: block;
  text-align: center;
  font-size: 48px;
}

.services-item > div {
  text-align: center;
  font-size: 16px;
}
</style>



