<template>
<el-popover
  placement="bottom"
  width="320"
  trigger="click">
  <div class="services-popover">
    <div class="services-row">
      <navigation-item link="/#/home" material-icon="home" text="Home"></navigation-item>
    </div>
  </div>

  <template slot="reference">
    <slot name="reference"></slot>
  </template>
</el-popover>
</template>

<script>
import NavigationItem from './NavigationItem'
import { mapGetters } from 'vuex'

export default {
  name: 'navigation-pop-over',

  components: {
    NavigationItem
  },

  computed: {
    ...mapGetters(['me'])
  }
}
</script>

<style lang="css" scoped>
.services-popover  {
  padding: 15px;
}

.services-row {
  display: flex;
}
</style>



