<template>
  <div>
    <el-badge style="margin-right: 10px;" :value="countNew">
      <el-button
        type="text"
        @click="getNotifications"
        v-popover:notificationPopover
        style="padding: 0px; color: white;"
      ><i class="material-icons">notifications</i></el-button>
    </el-badge>
    <el-popover
      ref="notificationPopover"
      placement="bottom-end"
      width="450"
      trigger="click">
      <div style="display: inline-flex; width: 100%; align-items: center; border-bottom: 1px gray;">
        <h4 style="flex: 1">Notifications</h4>
        <el-button
          type="text"
          size="mini"
          circle
          title="Create new"
          v-if="me.isAdmin"
          @click="showAddNotificationForm" plain>
          <i class="material-icons">add</i></el-button>
      </div>
      <v-list v-if="notifications && notifications.length > 0 && me.isAdmin != true" two-line>
        <template v-for="(item, index) in notifications">
          <v-divider
            :key="index"
          ></v-divider>
          <v-list-tile
            :key="item.title"
            avatar
            @click="getDetail(item)"
          >
            <v-list-tile-avatar>
              <img src="../assets/images/attendees.png">
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>{{ item.data.message }}</v-list-tile-title>
              <v-list-tile-sub-title class="text--primary">{{ item.data.title }}</v-list-tile-sub-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-list-tile-action-text>{{ calculateTime(item.created_at) }}</v-list-tile-action-text>
              <el-tag
                size="mini"
                type="text"
                v-if="!item.read_at">New</el-tag>
            </v-list-tile-action>
          </v-list-tile>
        </template>
      </v-list>

      <v-list v-else-if="notifications && notifications.length > 0 && me.isAdmin" two-line style="max-height: 35rem; overflow-y: auto;">
        <template v-for="(item, index) in notifications">
          <v-divider
          ></v-divider>
          <v-list-tile
            :key="index"
            avatar
          >
            <v-list-tile-avatar @click="getAdminDetail(item)">
              <img src="../assets/images/attendees.png">
            </v-list-tile-avatar>
            <v-list-tile-content @click="getAdminDetail(item)">
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
              <v-list-tile-sub-title class="text--primary">{{ convertTime(item.start_date) }}&nbsp;&nbsp;&nbsp;<span v-if="item.readed==false" style="background-color:#00BFA5;color:white;padding-left:8px;padding-right:8px;border-radius:4px;">new</span></v-list-tile-sub-title>
            </v-list-tile-content>
            <v-list-tile-action @click="getAdminDetail(item)">
              <v-list-tile-action-text>{{ calculateTime(item.created_at) }}</v-list-tile-action-text>
            </v-list-tile-action>
            <v-list-tile-action>
              <el-button @click="deleteNotification(item.id)" type="danger" icon="el-icon-delete" circle size="mini"></el-button>
            </v-list-tile-action>
          </v-list-tile>
        </template>
      </v-list>
      <div v-else style="padding: 10px">Empty</div>
    </el-popover>

    <el-dialog title="Create Notification" :visible.sync="dialogVisible">
      <el-form ref="createdNotificationForm" label-position="right" label-width="120px">
        <el-form-item label="Title">
          <el-input size="large" v-model="createdNotificationForm.title" placeholder="Enter title meeting"></el-input>
        </el-form-item>
        <el-form-item label="Attendees">
          <el-select size="large" @change="checkValue" v-model="createdNotificationForm.departments" multiple collapse-tags placeholder="Select departments" style="width:100%">
            <el-option
              label="Select all"
              value="Select all"
            >
            </el-option>
            <el-option
              label="Deselect all"
              value="Deselect all"
            >
            </el-option>
            <el-option
              v-for="item of departments"
              :label="item.name"
              :value="item.id"
              :key="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="width: 300px" label="Select Date">
          <el-date-picker
            v-model="createdNotificationForm.date"
            style="width: 300px"
            type="datetime"
            placeholder="Select date and time">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="Remarks">
          <el-input type="textarea" :rows="4"  v-model="createdNotificationForm.content" placeholder="Enter remark"></el-input>
        </el-form-item>
        <input @change="onFileChanged" type="file" multiple>
        <el-form-item >
          <el-button type="primary" style="float: right" @click="onSubmit">SUBMIT</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog v-if="notificationSelected" :title="notificationSelected.title" :visible.sync="dialogVisibleDetail">
      <el-form>
        <el-form-item :label="'Meeting Time: ' + convertTime(notificationSelected.start_date)">
        </el-form-item>
        <el-form-item label="Attendees">
          <el-tag
            style="margin: 4px"
            type="info"
            v-for="item of notificationSelected.attendees"
            :key="item.id">{{ item.department.name }}</el-tag>
        </el-form-item>
        <el-form-item label="Attachments">
        <br>
          <el-card>
            <div>
              <a style="padding: 10px" v-for="(file, index ) of notificationSelected.files" :href="'/'+file" target="_blank"><i class="el-icon-download"></i>{{cutString(file,index)[0]}}<br></a>
            </div>
          </el-card>
        </el-form-item>
        <el-form-item label="Remarks">
          <el-input type="textarea" :rows="3"  :value="notificationSelected.description"></el-input>
        </el-form-item>
        <el-tag style="float: initial" type="info">{{convertTime(notificationSelected.created_at)}}</el-tag>
      </el-form>
    </el-dialog>

    <el-dialog v-if="updatedNotificationForm" :visible.sync="dialogVisibleAdminDetail">
      <el-form ref="updatedNotificationForm" label-position="right" label-width="120px">
        <el-form-item label="Title">
          <el-input size="large" v-model="updatedNotificationForm.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="Attendees">
          <el-select size="large" @change="checkValue" v-model="updatedNotificationForm.departments" multiple collapse-tags style="width:100%">
            <el-option
              label="Select all"
              value="Select all"
            >
            </el-option>
            <el-option
              label="Deselect all"
              value="Deselect all"
            >
            </el-option>
            <el-option
              v-for="item of departments"
              :label="item.name"
              :value="item.id"
              :key="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="width: 300px" label="Select Date">
          <el-date-picker
            v-model="updatedNotificationForm.date"
            style="width: 300px"
            type="datetime"
            placeholder="Select date and time">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="Remarks">
          <el-input type="textarea" :rows="4"  v-model="updatedNotificationForm.description"></el-input>
        </el-form-item>
        <el-form-item label="Attachments">
        <br>
          <el-card>
            <div>
              <a style="padding: 10px" v-for="(file, index ) of updatedNotificationForm.files" :href="'/'+file" target="_blank"><i class="el-icon-download"></i>{{cutString(file,index)[0]}}<br></a>
            </div>
          </el-card>
        </el-form-item>
        <input ref="updatedFile" @change="onAdminFileChanged" type="file" multiple>
        <el-form-item >
          <el-button type="primary" style="float: right" @click="onUpdate(updatedNotificationForm.id)">UPDATE</el-button>
        </el-form-item>
        <el-tag style="float: initial" type="info">Created at: {{convertTime(updatedNotificationForm.created_at)}}</el-tag>
        <el-tag style="float: initial" type="info">Updated at: {{convertTime(updatedNotificationForm.updated_at)}}</el-tag>
      </el-form>
    </el-dialog>

  </div>
</template>

<script>
import api from '@/api'
import moment from 'moment'
import { mapGetters } from 'vuex'
import { cloneDeep } from 'lodash'

export default {
  name: 'Notification',
  created () {
    this.getNotifications()
    this.getDepartments()
  },
  data () {
    return {
      countNew: undefined,
      createdNotificationForm: {
        title: '',
        date: new Date(),
        departments: [],
        content: '',
        images: []
      },
      updatedNotificationForm: {
        title: '',
        date: new Date(),
        departments: [],
        content: '',
        images: [],
        update_images: []
      },
      notificationSelected: undefined,
      adminnotificationSelected: undefined,
      position: 'top',
      dialogProfileVisible: false,
      dialogVisible: false,
      dialogVisibleDetail: false,
      dialogVisibleAdminDetail: false,
      departments: [],
      loading: false,
      notifications: [],
      files: []
    }
  },

  computed: {
    ...mapGetters([
      'me'
    ])
  },

  methods: {
    checkValue () {
      if (this.createdNotificationForm.departments.includes('Select all')) {
        this.createdNotificationForm.departments = []
        for (let i = 0; i < this.departments.length; i++) {
          const element = this.departments[i]
          this.createdNotificationForm.departments.push(element.id)
        }
      }
      if (this.createdNotificationForm.departments.includes('Deselect all')) {
        this.createdNotificationForm.departments = []
      }

      if (this.updatedNotificationForm.departments.includes('Select all')) {
        this.updatedNotificationForm.departments = []
        for (let i = 0; i < this.departments.length; i++) {
          const element = this.departments[i]
          this.updatedNotificationForm.departments.push(element.id)
        }
      }
      if (this.updatedNotificationForm.departments.includes('Deselect all')) {
        this.updatedNotificationForm.departments = []
      }
    },
    getNotifications () {
      api.getNotifications()
        .then(res => {
          this.countNew = res.data.unread_count > 0 ? res.data.unread_count : undefined
          this.notifications = res.data.notifications
        })
    },
    convertTime (utcStr) {
      return moment.parseZone(utcStr, 'YYYY-MM-DD HH:mm:ss', 'UTC').toDate().toLocaleString()
    },
    calculateTime (utcStr) {
      let duration = moment.duration(moment().diff(moment(moment.utc(utcStr, 'YYYY-MM-DD HH:mm:ss'))))
      if (duration.asDays() > 1) {
        let day = parseInt(duration.asDays(), 10)
        return `${day}d`
      } else if (duration.asHours() > 1) {
        let hour = parseInt(duration.asHours(), 10)
        return `${hour}h `
      } else if (duration.asMinutes() > 1) {
        let min = parseInt(duration.asMinutes(), 10)
        return `${min}m`
      } else {
        return 'now'
      }
    },
    showAddNotificationForm () {
      this.dialogVisible = true
      if (this.$refs['createdNotificationForm']) {
        this.$refs['createdNotificationForm'].resetFields()
      }
    },
    resetDetailsForm () {
      if (this.$refs['detailNotificationForm']) {
        this.$refs['detailNotificationForm'].resetFields()
      }
    },
    resetAdminDetailsForm () {
      if (this.$refs['detailAdminNotificationForm']) {
        this.$refs['detailAdminNotificationForm'].resetFields()
      }
    },
    getDepartments () {
      api.getDepartments()
        .then(res => {
          this.departments = res.data
        })
        .catch(err => console.log(err))
    },
    onSubmit () {
      let form = new FormData()
      form.set('title', this.createdNotificationForm.title)
      form.set('content', this.createdNotificationForm.content)
      form.set('date', this.createdNotificationForm.date.toUTCString())
      form.set('departments', JSON.stringify(this.createdNotificationForm.departments))

      for (let i = 0; i < this.createdNotificationForm.images.length; i++) {
        form.append('images[]', this.createdNotificationForm.images[i])
      }

      api.addMeeting(form)
        .then(res => {
          this.$notify({
            type: 'success',
            message: 'Created success'
          })
          this.dialogVisible = false
          this.getNotifications()
        })
        .catch(error => {
          this.$notify({
            type: 'error',
            message: 'Create failed'
          })
          console.log(error)
          this.getNotifications()
        })
    },
    onUpdate (id) {
      let form = new FormData()
      form.set('title', this.updatedNotificationForm.title)
      form.set('content', this.updatedNotificationForm.description)
      form.set('date', this.updatedNotificationForm.date.toUTCString())
      form.set('departments', JSON.stringify(this.updatedNotificationForm.departments))
      if (this.updatedNotificationForm.update_images === undefined) {
      } else {
        for (let i = 0; i < this.updatedNotificationForm.update_images.length; i++) {
          form.append('images[]', this.updatedNotificationForm.update_images[i])
        }
      }
      this.dialogVisibleAdminDetail = false
      api.updateMeeting(id, form)
        .then(res => {
          this.$notify({
            type: 'success',
            message: 'Updated success'
          })
          this.dialogVisible = false
        })
        .catch(error => {
          this.$notify({
            type: 'error',
            message: 'Update failed'
          })
          console.log(error)
        })
    },
    getDetail (item) {
      if (!item.read_at) {
        item.read_at = true
        if (this.countNew > 1) {
          this.countNew--
        } else {
          this.countNew = undefined
        }
      }
      this.resetDetailsForm()
      console.log(item)
      api.showNotification(item.id)
        .then(res => {
          this.notificationSelected = res.data
          this.dialogVisibleDetail = true
        })
      this.getNotifications()
    },
    getAdminDetail (item) {
      if (this.$refs['updatedFile']) {
        this.$refs['updatedFile'].value = null
      }
      api.adminShowNotification(item.id)
        .then(res => {
          this.updatedNotificationForm = res.data
          this.updatedNotificationForm.departments = []
          for (let item of res.data.attendees) {
            this.updatedNotificationForm.departments.push(item.department.id)
          }
          this.updatedNotificationForm.date = moment.parseZone(res.data.start_date, 'YYYY-MM-DD HH:mm:ss', 'UTC').toDate()
          this.updatedNotificationForm = cloneDeep(this.updatedNotificationForm)
          this.dialogVisibleAdminDetail = true
          this.getNotifications()
        })
    },
    maskRead (id) {
      api.readNotification(id)
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    onFileChanged (ev) {
      this.createdNotificationForm.images = ev.target.files
    },
    onAdminFileChanged (ev) {
      this.updatedNotificationForm.update_images = ev.target.files
    },
    cutString: function (files, index) {
      return (files.split('/')).reverse()
    },
    deleteNotification (id) {
      this.$confirm('Are you sure delete metting?')
        .then(_ => {
          api.deleteNotification(id)
        })
    }
  },
  components: {
  },
  watch: {
  }
}
</script>

<style lang="css" scoped>
</style>



