<template>
  <div class="dashboard-sidebar">
    <div style="padding: 6px">
      <div class="dashboard-title">Filters</div>
      <filter-form
        ref="dashboardFilterForm"
        @filterEdit="filterEdit"/>
      <div class="dashboard-title">Search</div>
      <search-form @searchChanged="val => $emit('searchChanged', val)"/>
      <div class="dashboard-title">Project Names</div>
    </div>
    <ul
      class="infinite-list order-list"
      :infinite-scroll-disabled="loading && !allowMore"
      v-infinite-scroll="loadMore">
      <li v-for="(construction, index) of constructions" class="infinite-list-item" :key="index">
        <ConstructionItem
          :construction="construction"
          @onDelete="val=> $emit('onDeleteItem', {index: index, order: val, construction: construction})"
          @viewMap="_ => $emit('viewMap', construction)"
        />
      </li>
      <p style="color: white; padding-left: 20px" v-if="loading">Loading...</p>
      <li v-if="noData()" class="no-data">No data</li>
    </ul>
  </div>
</template>

<script>
  import SearchForm from './SearchForm'
  import FilterForm from './FitlerForm/FilterForm'
  import ConstructionItem from './ConstructionItem'

  export default {
    name: 'Sidebar',
    props: {
      temp: undefined,
      allowMore: false,
      loading: false,
      constructions: {
        type: Array,
        required: true
      }
    },
    methods: {
      noData () {
        if (this.loading) {
          return false
        }
        if (this.constructions && this.constructions.length === 0) {
          return true
        }
        return false
      },
      loadMore () {
        this.$emit('loadMore')
      },
      filterEdit (form) {
        this.$emit('filterEdit', form)
      },
      updateFunding (id) {
        this.$refs.dashboardFilterForm.updateFunding(id)
      }
    },
    components: {
      ConstructionItem,
      FilterForm,
      SearchForm
    }
  }
</script>

<style scoped>
  /* Sidebar */
  .dashboard-sidebar {
    width: 300px;
    height: 100%;
    display: flex;
    background-color: #324157;
    flex-flow: column;
  }

  .dashboard-title {
    color: #cccccc;
    margin-top: 10px;
    flex: none;
    font-size: 12px;
  }

  .no-data {
    color: #cccccc;
    padding: 6px;
  }

  .order-list {
    padding-left: 0;
    list-style: none;
    overflow-y: auto;
  }
</style>



