import BaseApi from './BaseApi'
import store from '@/store' // eslint-disable-line

import toFormData from './utils/toFormData' // eslint-disable-line

import { END_POINT } from '@/config'

class Api extends BaseApi {
  /** Overide base methods **/

  /**
   * Normal http request
   *
   * @param {string} method http method
   * @param {string} url http url
   * @param {Object} data params for get or body data for post, put, patch
   * @param {Object} config axios config object. See https://github.com/mzabriskie/axios
   */
  submit (method, url, data, config = {}) {
    url = END_POINT + url
    const submit = super.submit(method, url, data, config)
    return submit
  }

  /**
   * Submit http request with Authorization header
   *
   * @param {string} method http method
   * @param {string} url http url
   * @param {Object} data params for get or body data for post, put, patch
   * @param {Object} config axios config object. See https://github.com/mzabriskie/axios
   */
  authSubmit (method, url, data, config = {}) {
    url = END_POINT + url
    const submit = super.authSubmit(method, url, data, config)
    return submit
  }

  /** Api methods **/
  syncResources () {
    return this.authSubmit('get', `/sync/resources`)
  }
  getUsers (form) {
    return this.authSubmit('get', `/users`, form)
  }
  addUser (userData) {
    return this.authSubmit('post', `/users`, userData)
  }
  updateUser (user) {
    return this.authSubmit('put', `/users/${user.id}`, user)
  }
  setPassword (user) {
    return this.authSubmit('put', `/users/${user.id}/newpassword`, user)
  }
  deleteUser (id) {
    return this.authSubmit('delete', `/users/${id}`)
  }
  approveUser (code) {
    return this.authSubmit('get', `/approve/${code}`)
  }

  getDepartments () {
    return this.authSubmit('get', `/departments`)
  }

  getStructures () {
    return this.authSubmit('get', `/structures`)
  }

  getDistricts () {
    return this.authSubmit('get', `/districts`)
  }

  getCircles (distName) {
    return this.authSubmit('get', `/circles/${distName}`)
  }
  getBlocks (distName) {
    return this.authSubmit('get', `/blocks/${distName}`)
  }

  getProjects (form) {
    return this.authSubmit('get', `/projects`, form)
  }

  checkExistMIS (form) {
    return this.authSubmit('get', `/checkMISExists`, form)
  }

  exportPdf (itemId) {
    return this.authSubmit('get', `/summary/report/${itemId}`)
  }

  getExecutingDepartments () {
    return this.authSubmit('get', `/projects/values/executingdepartments`)
  }

  showProjects (projectId) {
    return this.authSubmit('get', `/projects/${projectId}`)
  }
  addProjects (form) {
    return this.authSubmit('post', `/projects`, form)
  }
  updateProjects (form) {
    return this.authSubmit('put', `/projects/${form.id}`, form)
  }
  deleteProjects (projectId) {
    return this.authSubmit('delete', `/projects/${projectId}`)
  }
  deleteReport (id) {
    return this.authSubmit('delete', `/reports/${id}`)
  }
  updateReport (id, form) {
    return this.authSubmit('post', `/reports/${id}`, form, {headers: { 'Content-Type': 'multipart/form-data' }})
  }
  orderDetails (id) {
    return this.authSubmit('get', `/orders/${id}`)
  }
  deleteOrder (id) {
    return this.authSubmit('delete', `/orders/${id}`)
  }
  deleteConstruction (id) {
    return this.authSubmit('delete', `/constructions/${id}`)
  }
  updateOrder (id, form) {
    return this.authSubmit('put', `/orders/${id}`, form)
  }
  getNotifications (filter) {
    return this.authSubmit('get', `/notifications`, filter)
  }
  showNotification (id) {
    return this.authSubmit('get', `/notifications/${id}`)
  }
  adminShowNotification (id) {
    return this.authSubmit('get', `/notifications/admin/${id}`)
  }
  readNotification (id) {
    return this.authSubmit('get', `/notifications/${id}/read`)
  }
  addMeeting (form) {
    return this.authSubmit('post', `/meetings`, form, {headers: { 'Content-Type': 'multipart/form-data' }})
  }
  updateMeeting (id, form) {
    return this.authSubmit('post', `/update_meeting/${id}`, form)
  }
  getWorkOrders (form) {
    return this.authSubmit('get', `/work_orders`, form)
  }
  getConstructions (form) {
    return this.authSubmit('get', `/webconstructions`, form)
  }
  showConstructions (id) {
    return this.authSubmit('get', `/constructions/${id}`)
  }
  getFundingAgencies () {
    return this.authSubmit('get', `/funding_agencies`)
  }
  addFundingAgency (form) {
    return this.authSubmit('post', `/funding_agencies`, form)
  }
  getFundingSources () {
    return this.authSubmit('get', `/funding_sources`)
  }
  updateFundingAgency (form) {
    return this.authSubmit('put', `/funding_agencies/${form.id}`, form)
  }
  deleteFunding (id) {
    return this.authSubmit('delete', `/funding_agencies/${id}`)
  }
  deleteNotification (id) {
    return this.authSubmit('delete', `/notifications/${id}/delete`)
  }
  getLegends () {
    return this.authSubmit('get', `/legends`)
  }
  filterData (data) {
    return this.authSubmit('get', `/dashboard`, data)
  }
  getGeometryDistrict () {
    return this.authSubmit('get', `/dashboard/districts`)
  }
  getGeometryCircle () {
    return this.authSubmit('get', `/dashboard/circles`)
  }
}

export default Api



