<template>
  <div class="user-registration">
    <div style="width: 100%; display: inline-flex;margin-bottom: 20px;">

<!--      <el-form label-width="50px" style="flex: 1">-->
<!--        <el-form-item label="Search">-->
      <div>
        <el-select
          style="width: 600px"
          v-model="filtersForm.departments"
          multiple
          filterable
          :filter-method="filterDep"
          @focus="resetDep"
          @change="getTableData"
          collapse-tags
          size="mini"
          placeholder="Select departments">
          <el-option
            v-for="item in departments"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
        <br>
        <el-input size="mini" style="width: 600px; margin-top: 10px" v-model="filtersForm.search" placeholder="Search user (name, mobile, email)" @keyup.enter.native.prevent.stop="getTableData"></el-input>
        <el-button size="mini" style="height: 28px;margin-left: 10px;" :autofocus="true" :loading="loading" type="primary" icon="el-icon-search" @click="getTableData" plain>Search</el-button>
        <el-button size="mini" style="height: 28px;" type="primary" icon="el-icon-refresh" @click="resetSearch" plain>Reset</el-button>
      </div>

<!--        </el-form-item>-->
<!--      </el-form>-->
      <div style="position: fixed; right: 2%;">
        <el-button type="primary" @click="approveSelected" plain>Approve</el-button>
        <el-button type="primary" @click="deleteUsersSelected" plain>Delete</el-button>
      </div>
    </div>

    <el-pagination
      size="mini"
      @size-change="sizeChanged"
      @current-change="pageChanged"
      :page-sizes="[10, 15, 20, 50]"
      :page-size="filtersForm.page_size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalUser">
    </el-pagination>
    <el-table size="mini" :loading="loading" :data="tableData" @selection-change="onChangeSelected" style="overflow-y: auto">
      <el-table-column
          type="selection"
          width="55">
      </el-table-column>
      <el-table-column
          prop="created_at"
          label="Created at"
          min-width="90">
      </el-table-column>
      <el-table-column
          prop="updated_at"
          label="Updated at"
          min-width="90">
      </el-table-column>
      <el-table-column
          prop="name"
          label="Name"
          min-width="140">
      </el-table-column>
      <el-table-column
          prop="mobile"
          label="Mobile"
          min-width="140">
      </el-table-column>
      <el-table-column
        label="Department"
        min-width="200">
        <template slot-scope="props">
          {{ props.row.department ? props.row.department.name : '' }}
        </template>
      </el-table-column>
      <el-table-column
          prop="email"
          label="Email"
          min-width="180">
      </el-table-column>
      <el-table-column
          label="Operators"
          min-width="160">
        <template slot-scope="props">
          <el-button-group>
            <el-button size="mini" type="primary" @click="approveUser(props.row)">Approve</el-button>
            <el-button size="mini" type="danger" @click="confirmDeleteUser(props.row)">Delete</el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import api from '@/api'
  import {convertUTCToLocalDate} from '@/utils/Date'
  import { filterArr } from '@/utils/Array'

  export default {
    name: 'request-users',
    data () {
      return {
        loading: false,
        dialogVisible: false,
        tableData: [],
        departments: [],
        _departments: [],
        activeIndex: '1',
        selectedUsers: [],
        filtersForm: {
          search: undefined,
          page_size: 10,
          page: 1,
          confirmed: false
        },
        totalUser: null
      }
    },
    computed: {
      ...mapGetters([
        'deleteUserRequest',
        'approveUserRequest',
        'userFilters'
      ])
    },
    methods: {
      resetDep () {
        this.departments = this._departments
      },
      filterDep (value) {
        this.departments = filterArr(value, this._departments, 'name')
      },
      onChangeSelected (val) {
        this.selectedUsers = val
      },
      sizeChanged (val) {
        this.filtersForm.page_size = val
        this.filtersForm.page = 1
        this.getTableData()
      },
      pageChanged (val) {
        this.filtersForm.page = val
        this.getTableData()
      },
      resetSearch () {
        this.filtersForm = {
          search: undefined,
          page_size: 10,
          page: 1,
          confirmed: false
        }
        this.getTableData()
      },
      getTableData () {
        api.getUsers(this.filtersForm)
          .then(res => {
            this.tableData = res.data.data.map(val => {
              val.created_at = convertUTCToLocalDate(val.created_at)
              val.updated_at = convertUTCToLocalDate(val.updated_at)
              return val
            })
            this.totalUser = res.data.total
          })
          .catch(error => {
            console.log(error)
          })
      },
      confirmDeleteUser (item) {
        this.$confirm('Are you sure to remove this user?')
          .then(_ => {
            this.deleteUser(item.id)
          })
          .catch(_ => {
          })
      },
      deleteUsersSelected () {
        this.$confirm('Are you sure to remove selected users?')
          .then(_ => {
            for (let item of this.selectedUsers) {
              this.deleteUser(item.id)
            }
          })
          .catch(_ => {
          })
      },
      approveSelected () {
        for (let item of this.selectedUsers) {
          this.approveUser(item)
        }
      },
      deleteUser (userId) {
        api.deleteUser(userId).then(res => {
          this.$notify({
            type: 'success',
            message: 'Deleted success'
          })
          this.getTableData()
        }).catch(e => {
          this.$notify({
            type: 'error',
            message: 'Deleted failed'
          })
        })
      },
      approveUser (user) {
        api.approveUser(user.confirmation_code)
          .then(_ => {
            this.$notify({
              type: 'success',
              message: 'Approved success'
            })
            this.getTableData()
          }).catch(e => {
            this.$notify({
              type: 'error',
              message: 'Approved failed'
            })
          })
      }
    },
    mounted () {
      this.getTableData()
      api.getDepartments()
        .then(res => {
          this.departments = res.data
          this._departments = res.data
        })
        .catch(err => console.log(err))
    }
  }
</script>

<style scoped>
  .user-registration {
    display: flex;
    flex-flow: column;
    height: 100%;
    width: 100%;
  }
</style>



