<template>
  <div class="base-layer-container">
    <header v-if="showHeader" >
      <main-navigation-bar></main-navigation-bar>
    </header>
    <div class="base-layer-content">
      <nav></nav>
      <main style="overflow-y: hidden">
        <slot></slot>
      </main>
      <aside></aside>
    </div>
    <footer v-if="showFooter">
      <h4>© Made by Co. SmartMap 2017</h4>
    </footer>
  </div>
</template>

<script>
import MainNavigationBar from './MainNavigationBar'

export default {
  name: 'base-layer',

  props: {
    showHeader: {
      type: Boolean,
      default: true
    },

    showFooter: {
      type: Boolean,
      default: false
    }
  },

  components: {
    MainNavigationBar
  }
}
</script>

<style lang="css" scoped>
/* Holy Grail */
.base-layer-container {
  display: flex;
  overflow-y: hidden;
  height: 100vh; /* 1, 3 */
  flex-direction: column;
}

.base-layer-container > header,
.base-layer-container > footer {
  flex: none; /* 2 */
  min-height: 0;
  min-width: 0;
}

.base-layer-container > .base-layer-content {
  display: flex;
  /*flex: 1 0 auto; - This will not work*/
  flex: 1;
  min-height: 0;
  overflow-y: hidden;
  flex-direction: row;
  padding: 0;
}

.base-layer-content > main {
  flex: 1;
  min-width: 0;
  overflow-y: auto;
}

.base-layer-content > nav,
.base-layer-content > aside {
  flex: none;
  min-width: 0;
}

.base-layer-container > footer {
  background: white;
  display: flex;
  justify-content: center;
}

.base-layer-content {
  height: 100%;
}
</style>



