export const remove = (arr, predicate) => {
  for (let i = arr.length - 1; i >= 0; i--) {
    const item = arr[i]

    if (predicate(item)) arr.splice(i, 1)
  }
}

export const filterArr = (value, array, prop) => {
  if (!value || value === '') {
    return array
  }
  return array.filter(function (el) {
    return el[prop].toLowerCase().indexOf(value.toLowerCase()) === 0
  })
}



