<template>
  <div class="user-registration-container">
    <div class="user-registration-sidebar">
      <el-menu  style="width: 100%" background-color="#324157" text-color="#f4f4f5" @select="handleSelect" :default-active="defaultActive">
        <el-menu-item index="1"><i class="el-icon-menu"></i>Registration Request</el-menu-item>
        <el-menu-item index="2"><i class="el-icon-setting"></i>Application Users</el-menu-item>
      </el-menu>
    </div>
    <div class="user-registration-content-view">
      <request-users v-if="activeIndex == 1"></request-users>
      <app-users v-if="activeIndex == 2"></app-users>
    </div>
  </div>
</template>


<script>
import RequestUsers from './RequestUsers'
import AppUsers from './AppUsers'
export default {
  name: 'userRegistration',
  data () {
    return {
      defaultActive: '1',
      activeIndex: '1'
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      this.activeIndex = key
    }
  },

  components: {
    RequestUsers,
    AppUsers
  }
}
</script>

<style scoped>
  .user-registration-container {
    height: 100%;
    display: flex;
    width: 100%;
  }
  .user-registration-sidebar {
    border-radius: 0;
    width: 300px;
    color: white;
    flex: none;
    height: 100%;
    display: flex;
    background-color: #324157;
  }
  .user-registration-content-view {
    padding: 16px;
    height: 100%;
    width: 100%;
    flex: 1;
  }

</style>




