<template>
  <div>
<!--    <el-button style="float: right; margin-bottom: 10px" size="mini" type="primary" icon="el-icon-plus" @click="showAddForm" plain>Add</el-button>-->
    <el-dialog  :title="edit? ' Update Physical Progress Summary' : ' Update Physical Progress Summary'" :visible.sync="dialogFormVisible">
      <el-form :model="form" label-position="top" label-width="200px">
        <el-form-item>
          <v-layout>
            <v-layout column>
              <div>Year</div>
              <el-date-picker
                disabled
                style="width: 100%;"
                v-model="form.year"
                type="year"
                placeholder="Pick a year">
              </el-date-picker>
            </v-layout>
            <v-layout column>
              <div>Quarter</div>
              <el-select disabled v-model="form.quarter" placeholder="Select Quarter" :value="quarters[0]">
                <el-option
                  v-for="item of quarters"
                  :key="item"
                  :label="item"
                  :value="item">
                </el-option>
              </el-select>
            </v-layout>
            <v-layout column>
              <div>Status</div>
              <el-select v-model="form.status" @change="onStatusChange" placeholder="Select status">
                <el-option
                  v-for="item of statuses"
                  :key="item"
                  :label="item"
                  :value="item">
                </el-option>
              </el-select>
            </v-layout>
          </v-layout>
        </el-form-item>
        <el-form-item label="Physical Progress (%)">
          <el-slider
              :min="0" :max="100" :step="5"
              v-model="form.physical_percent"
              :disabled="form.status === 'Completed'"
              show-stops
              show-tooltip
              @change="onPPChange"
              show-input>
          </el-slider>
        </el-form-item>
        <el-form-item label="Financial Progress (%)">
          <el-slider
              :min="0" :max="100" :step="5"
              v-model="form.financial_percent"
              :disabled="form.status === 'Completed'"
              show-stops
              @change="onFPChange"
              show-input>
          </el-slider>
        </el-form-item>
        <el-row type="flex" class="row-bg form-row" justify="space-between">
          <el-col :span="7">
            <label>Completion Certificate Status</label>
            <el-select disabled style="width: 100%"  v-model="form.cc_status" placeholder="Completion Certificate Status" :value="cc_statuses[0]">
              <el-option
                v-for="item of cc_statuses"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="7">
            <label>Documents</label>
            <el-upload
              :disabled="form.cc_status !== 'Submitted'"
              class="upload-demo"
              action="/api/projects/upload/document"
              :file-list="form.cc_documents"
              :limit="1"
              :on-error="handleError"
              :on-remove="handleCCRemove"
              :on-success="handleCCSuccess">
              <el-button :disabled="form.cc_status !== 'Submitted'" size="small" type="primary" plain>Click to upload document</el-button>
              <div slot="tip" style="line-height: 1" class="el-upload__tip">Document (pdf, doc, docx, xls, xlsx, zip, rar) files with a size less than 4mb</div>
            </el-upload>
          </el-col>
          <el-col :span="8">
              <label>Date of submission</label>
              <el-date-picker
                :disabled="form.cc_status !== 'Pending' && form.cc_status !== 'Submitted'"
                placeholder="Date of submission"
                :picker-options="dateOptions"
                v-model="form.cc_date"
                type="date"
                style="width: 100%"
                format="yyyy/MM/dd"
                value-format="yyyy-MM-dd">
              </el-date-picker>
          </el-col>
        </el-row>
        <el-form-item label="Photos" >
          <el-upload
              class="upload-demo"
              action="/api/projects/upload/image"
              :file-list="form.photos"
              :limit="4"
              :disabled="true"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              list-type="picture">
<!--            <el-button size="small" type="primary" v-if="!edit" >Click to upload</el-button>-->
<!--            <div slot="tip" class="el-upload__tip">jpg/png files with a size less than 2mb</div>-->
          </el-upload>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">Cancel</el-button>
        <el-button type="primary" @click="submitProgress">Submit</el-button>
      </span>
    </el-dialog>
    <el-table
        size="mini"
        :data="physicalProgress"
        style="width: 100%">
      <el-table-column
          type="index"
          label="Sl.No."
          width="60">
      </el-table-column>
      <el-table-column
        label="Year"
        width="100">
        <template slot-scope="scope">
          <div>{{ new Date(scope.row.year).getFullYear()}}</div>
        </template>
      </el-table-column>

      <el-table-column
          prop="quarter"
          label="Quarter"
          min-width="100">
      </el-table-column>
      <el-table-column
          prop="physical_percent"
          label="Physical Progress (%)"
          min-width="120">
      </el-table-column>
      <el-table-column
          prop="financial_percent"
          label="Financial Progress (%)"
          min-width="120">
      </el-table-column>
      <el-table-column
          prop="status"
          label="Status"
          min-width="120">
      </el-table-column>
      <el-table-column
          label="Photographs"
          min-width="120">
        <template slot-scope="scope">
          <el-upload
              action="/api/projects/upload/image"
              :file-list="scope.row.photos"
              :disabled="true"
              list-type="picture">
          </el-upload>
        </template>
      </el-table-column>
      <el-table-column
          label="Operators"
          width="120">
        <template slot-scope="scope">
          <el-button-group v-if="!edit">
            <el-button size="mini" type="info" icon="el-icon-edit" @click="showEditForm(scope.row, scope.$index)" plain>
            </el-button>
<!--            <el-button size="mini" type="info" icon="el-icon-delete" @click="confirmDelete(scope.$index)" plain>-->
<!--            </el-button>-->
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import { cloneDeep } from 'lodash'
  import { getPhysicalProgressError } from '@/utils/projectUtils'
  export default {
    name: 'physical-progress',
    components: {
    },
    data () {
      return {
        dateOptions: {
          disabledDate: this.disabledDate
        },
        dialogFormVisible: false,
        cc_statuses: ['Submitted', 'Not submitted'],
        quarters: ['1st quarter (April - June)', '2nd quarter (July - Sept)', '3rd quarter (Oct - Dec)', '4th quarter (Jan - March)'],
        statuses: ['Ongoing', 'Completed', 'Not Started'],
        form: {
          year: new Date(),
          status: '',
          cc_status: '',
          cc_documents: [],
          cc_date: undefined,
          quarter: '',
          physical_percent: 0,
          financial_percent: 0,
          photos: []
        }
      }
    },
    props: {
      physicalProgress: Array,
      edit: Boolean
    },
    methods: {
      disabledDate (time) {
        return time.getTime() > new Date()
      },
      handleRemove (file) {
        let index = this.form.photos.indexOf(file)
        this.form.photos.splice(index, 1)
      },
      handleSuccess (file) {
        this.form.photos.push(file)
      },
      handleError () {
        this.$notify.error('An error when upload file.')
      },
      handleCCRemove (file) {
        let index = this.form.cc_documents.indexOf(file)
        this.form.cc_documents.splice(index, 1)
      },
      handleCCSuccess (file) {
        if (!this.form.cc_documents) {
          this.form.cc_documents = []
        }
        this.form.cc_documents.push(file)
      },
      onStatusChange (status) {
        if (status === 'Completed') {
          this.form.cc_status = 'Submitted'
          this.form.physical_percent = 100
          this.form.financial_percent = 100
        } else if (status === 'Not Started') {
          this.form.physical_percent = 0
          this.form.financial_percent = 0
          this.form.cc_status = ''
        } else {
          this.form.physical_percent = 0
          this.form.financial_percent = 0
          this.form.cc_status = ''
        }
      },
      onPPChange (value) {
        this.form.physical_percent = parseInt(value)
        if (this.form.physical_percent === 100 && this.form.financial_percent === 100) {
          this.form.status = 'Completed'
        } else if (this.form.physical_percent === 0 && this.form.financial_percent === 0) {
          this.form.status = 'Not Started'
        } else {
          this.form.status = 'Ongoing'
        }
      },
      onFPChange (value) {
        this.form.financial_percent = parseInt(value)
        if (this.form.physical_percent === 100 && this.form.financial_percent === 100) {
          this.form.status = 'Completed'
        } else if (this.form.physical_percent === 0 && this.form.financial_percent === 0) {
          this.form.status = 'Not Started'
        } else {
          this.form.status = 'Ongoing'
        }
      },
      showAddForm () {
        this.form = {
          year: new Date(),
          status: '',
          quarter: '',
          physical_percent: 0,
          financial_percent: 0,
          photos: [],
          cc_status: '',
          cc_documents: [],
          cc_date: undefined,
          index: -1
        }
        this.dialogFormVisible = true
      },
      showEditForm (form, index) {
        form.index = index
        let tmp = cloneDeep(form)
        if (!tmp.cc_documents) {
          tmp.cc_documents = []
        }
        this.form = tmp
        this.dialogFormVisible = true
      },
      confirmDelete (index) {
        this.$confirm('Are you sure to delete the row?', '', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel'
        }).then(() => {
          this.$emit('onDeleteProgress', index)
        }).catch(() => {})
      },
      submitProgress () {
        this.errors = getPhysicalProgressError(this.form, this.physicalProgress)
        if (!this.errors.valid) {
          delete this.errors.valid
          delete this.errors.alert
          this.$notify({
            title: 'Invalid',
            message: Object.values(this.errors).join(', '),
            type: 'error'
          })
          return
        }
        this.dialogFormVisible = false
        if (this.form.index === -1) {
          this.$emit('onAddProgress', cloneDeep(this.form))
        } else {
          this.$emit('onEditProgress', cloneDeep(this.form))
        }
      }
    },
    mounted () {
    }
  }
</script>

<style scoped>
</style>



