<template>
  <div id="AOIMap" style="width: 100%; height: 100%"/>
</template>
<script>
import mapboxgl from 'mapbox-gl/dist/mapbox-gl'
// import RulerControl from 'mapbox-gl-controls/lib/ruler'
// import ZoomControl from 'mapbox-gl-controls'
let map
export default {
  components: {
  },
  created () {
  },
  mounted () {
    let bounds = [
      [90.5754, 25.5311],
      [98.20909, 30.2833]
    ]
    mapboxgl.accessToken = 'pk.eyJ1IjoiaG9hdGllbnR1IiwiYSI6ImNrYXMwNmt4ZzA4YTIyeXAzcjZicmhsNXMifQ.9hvfCuoiO1-1cFmikE14LA'
    map = new mapboxgl.Map({
      container: 'AOIMap', // container id
      style: 'https://tiles.eofactory.ai/styles/basic/style.json',
      attributionControl: false,
      zoom: 4, // starting zoom
      maxBounds: bounds
    })
    let popup = new mapboxgl.Popup({
      closeButton: false
    })
    map.addControl(new mapboxgl.NavigationControl())
    map.on('click', 'circle', (e) => {
      map.getCanvas().style.cursor = 'pointer'
      const description = e.features[0].properties
      this.$emit('setPopup', {
        status: true,
        description: description
      })
    })
    map.on('style.load', () => {
      map.resize()
      map.dragRotate.disable()
      map.fitBounds(bounds, {
        duration: 0,
        padding: 10
      })
      this.$emit('mapLoaded')
      map.on('mousemove', 'circle', function (e) {
        map.getCanvas().style.cursor = 'pointer'
        let feature = e.features[0]
        popup.setLngLat(e.lngLat)
          .setHTML('<span><b>District: </b>' + feature.properties.dist_name + '</span><br><span><b>Circle: </b>' + feature.properties.name + '</span><br>' + '<span><b>Total project: </b>' + feature.properties.value + '</span>')
          .addTo(map)
      })
      map.on('mouseleave', 'circle', function () {
        map.getCanvas().style.cursor = ''
        popup.remove()
      })
    })
  },
  data () {
    return {
      geoCircles: [],
      geoDistricts: []
    }
  },
  methods: {
    addDistricts (features) {
      const featureCollection = {
        'type': 'FeatureCollection',
        'features': features
      }
      const source = {
        'type': 'geojson',
        'data': featureCollection
      }
      map.addSource('districts', source)
      map.addLayer({
        'id': 'districts',
        'type': 'line',
        'source': 'districts',
        'paint': {
          'line-color': '#D20C0C',
          'line-opacity': 1,
          'line-width': 2
        }
      })
      map.addLayer({
        'id': 'district_name',
        'type': 'symbol',
        'source': 'districts',
        'maxzoom': 6,
        'layout': {
          'text-field': ['get', 'name'],
          'text-font': [
            'Noto Sans Regular'
          ],
          'text-size': 12
        },
        'paint': {
          'text-color': 'rgba(0, 0, 0, 1)'
        }
      })
    },
    addCircles (features) {
      const featureCollection = {
        'type': 'FeatureCollection',
        'features': features
      }
      const source = {
        'type': 'geojson',
        'data': featureCollection
      }
      this.geoCircles = featureCollection
      map.addSource('circle', source)
      map.addLayer({
        'id': 'circle',
        'type': 'fill',
        'minzoom': 6,
        'source': 'circle',
        'paint': {
          'fill-outline-color': '#D20C0C',
          'fill-color': ['get', 'color'],
          'fill-opacity': ['get', 'opacity']
        }
      })
      map.addLayer({
        'id': 'labels-map',
        'type': 'symbol',
        'source': 'circle',
        'minzoom': 6,
        'layout': {
          'text-field': ['get', 'value'],
          'text-font': [
            'Noto Sans Regular'
          ],
          'text-size': 10
        },
        'paint': {
          'text-color': 'rgba(0, 0, 0, 1)'
        }
      })
    },
    resetData () {
      this.geoCircles.features = this.geoCircles.features.map(val => {
        val.properties.value = ''
        val.properties.color = '#ffebee'
        val.properties.opacity = 0.4
        return val
      })
    },
    updateData (data) {
      this.resetData()
      this.geoCircles.features = this.geoCircles.features.map(val => {
        let value = data.data.dataCircle[val.properties.name]
        val.properties.value = value
        val.properties.color = this.mapColor(value)
        val.properties.opacity = !value ? 0.4 : 0.8
        return val
      })
      this.updateMap()
    },
    updateMap () {
      map.getSource('circle').setData(this.geoCircles)
    },
    mapColor (totalProject) {
      if (!totalProject) {
        return '#ffebee'
      } else if (totalProject < 100) {
        return '#ffcdd2'
      } else if (totalProject < 200) {
        return '#e57373'
      } else if (totalProject < 300) {
        return '#f44336'
      } else if (totalProject < 400) {
        return '#d32f2f'
      } else {
        return '#7f0000'
      }
    }
  }
}
</script>
<style scoped>
/deep/
.mapboxgl-popup-content {
  max-width: 400px !important;
  font: 12px/20px 'Helvetica Neue', Arial, Helvetica, sans-serif;
}
</style>



