<template>
  <div>
    <el-button style="float: right; margin-bottom: 10px" size="mini" type="primary" icon="el-icon-plus" @click="showAddForm" plain>Add</el-button>
    <el-dialog title="Add new progress" :visible.sync="dialogFormVisible">
      <el-form :model="form" label-position="top" label-width="200px">
        <el-row type="flex" class="row-bg form-row" justify="space-between">
          <el-row :span="14" type="flex" class="row-bg form-row" justify="space-between">
            <el-col :span="10">
              <label>Instalment</label>
              <el-select style="width: 100%" disabled v-model="form.instalment" placeholder="Instalment">
                <el-option
                  v-for="item of instalments"
                  :key="item"
                  :label="item"
                  :value="item">
                </el-option>
              </el-select>
            </el-col>
            <el-col :span="12">
              <label>Amount (In Lakhs)</label>
              <el-input type="number" style="width: 100%;" :min="0" :controls="false" placeholder="Amount" v-model="form.amount"></el-input>
            </el-col>
          </el-row>
          <el-col :span="8">
            <label>Date of Sanction</label>
            <el-date-picker placeholder="Date of Sanction" :picker-options="dateOptions" v-model="form.sanction_date" type="date" style="width: 100%" format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd" >
            </el-date-picker>
            <label>Date of Release</label>
            <el-date-picker placeholder="Date of Release" :picker-options="dateOptions" v-model="form.release_date" type="date" style="width: 100%" format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-col>
        </el-row>

        <el-row type="flex" class="row-bg form-row" justify="space-between">
          <el-col :span="7">
            <label>Utilization Certificate Status</label>
            <el-select style="width: 100%" v-model="form.uc_status" placeholder="Utilization Certificate Status" :value="uc_statuses[0]">
              <el-option
                v-for="item of uc_statuses"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="7">
            <label>Documents</label>
            <el-upload
              class="upload-demo"
              action="/api/projects/upload/document"
              :file-list="form.uc_documents"
              :limit="4"
              :on-error="handleError"
              :on-remove="handleUCRemove"
              :on-success="handleUCSuccess">
              <el-button size="small" type="primary" plain>Click to upload document</el-button>
              <div slot="tip" style="line-height: 1" class="el-upload__tip">Document (pdf, doc, docx, xls, xlsx, zip, rar) files with a size less than 4mb</div>
            </el-upload>
          </el-col>
          <el-col :span="8">
            <label>Date of submission</label>
            <el-date-picker 
              :disabled="form.uc_status !== 'Pending' && form.uc_status !== 'Submitted'"
              placeholder="Date of submission"
              :picker-options="dateOptions"
              v-model="form.uc_date"
              type="date"
              style="width: 100%"
              format="yyyy/MM/dd"
              value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-col>
        </el-row>

        <!-- <el-row type="flex" class="row-bg form-row" justify="space-between">
          <el-col :span="7">
            <label>Completion Certificate Status</label>
            <el-select style="width: 100%"  v-model="form.cc_status" placeholder="Completion Certificate Status" :value="cc_statuses[0]">
              <el-option
                v-for="item of cc_statuses"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="7">
            <label>Documents</label>
            <el-upload
              class="upload-demo"
              action="/api/projects/upload/document"
              :file-list="form.cc_documents"
              :limit="4"
              :on-error="handleError"
              :on-remove="handleCCRemove"
              :on-success="handleCCSuccess">
              <el-button size="small" type="primary" plain>Click to upload document</el-button>
              <div slot="tip" style="line-height: 1" class="el-upload__tip">Document (pdf, doc, docx, xls, xlsx, zip, rar) files with a size less than 4mb</div>
            </el-upload>
          </el-col>
          <el-col :span="8">
              <label>Date of submission</label>
              <el-date-picker :disabled="form.cc_status !== 'Pending' && form.cc_status !== 'Submitted'" placeholder="Date of submission" v-model="form.cc_date" type="date" style="width: 100%" format="yyyy/MM/dd"
                              value-format="yyyy-MM-dd">
              </el-date-picker>
          </el-col>
        </el-row> -->

        <el-row type="flex" class="row-bg form-row" justify="space-between">
          <el-col :span="14">
            <el-form-item label="Handed over of Completed Project by the Executing Department">
              <el-input placeholder="Handed over of Completed Project by the Executing Department" v-model="form.handed_over">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="Date of handed over">
              <el-date-picker :disabled="form.handed_over === ''" placeholder="Date of handed over" v-model="form.handed_over_date" type="date" style="width: 100%" format="yyyy/MM/dd"
                              value-format="yyyy-MM-dd">
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row type="flex" class="row-bg form-row" justify="space-between">
          <el-col :span="14">
            <el-form-item label="Taken over of completed project by the Beneficiary Department">
              <el-input placeholder="Taken over of completed project by the Beneficiary Department" v-model="form.taken_over">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="Date of taken over">
              <el-date-picker :disabled="form.taken_over === ''" placeholder="Date of taken over" v-model="form.taken_over_date" type="date" style="width: 100%" format="yyyy/MM/dd"
                              value-format="yyyy-MM-dd">
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">Cancel</el-button>
        <el-button type="primary" @click="submitProgress">Submit</el-button>
      </span>
    </el-dialog>
    <el-table
        size="mini"
        :data="financialProgress"
        style="width: 100%">
      <el-table-column
          type="expand">
        <template slot-scope="props">
          <label>Handed over of Completed Project by the Executing Department: <b>{{ props.row.handed_over }}</b></label>
          <br><label>Date of Handed over:  <b>{{ props.row.handed_over_date ? new Date(props.row.handed_over_date).toLocaleDateString() : ''}}</b></label>
          <br><label>Taken over of completed project by the Beneficiary Department: <b>{{ props.row.taken_over }}</b></label>
          <br><label>Date of Taken over: <b>{{props.row.taken_over_date ? new Date(props.row.taken_over_date).toLocaleDateString() : '' }}</b></label>
        </template>
      </el-table-column>
      <el-table-column
          type="index"
          label="Sl.No."
          width="60">
      </el-table-column>
      <el-table-column
          prop="instalment"
          label="Instalment"
          min-width="100">
      </el-table-column>
      <el-table-column
          prop="amount"
          label="Amount (in Lakhs)"
          min-width="100">
      </el-table-column>
      <el-table-column
          label="Date of Release"
          min-width="140">
         <template slot-scope="scope">{{ scope.row.release_date ? new Date(scope.row.release_date).toLocaleDateString() : '' }}</template>
      </el-table-column>
      <el-table-column
          prop="uc_status"
          label="Utilization Certificate Status"
          min-width="180">
      </el-table-column>
      <el-table-column
          label="Date of submission"
          min-width="140">
        <template slot-scope="scope">{{ scope.row.uc_date ? new Date(scope.row.uc_date).toLocaleDateString() : '' }}</template>
      </el-table-column>
      <el-table-column
          fixed="right"
          label="Operations"
          min-width="110">
        <template slot-scope="scope">
          <el-button-group>
            <el-button size="mini" type="info" icon="el-icon-edit" @click="showEditForm(scope.row, scope.$index)" plain>
            </el-button>
            <el-button size="mini" type="info" icon="el-icon-delete" @click="confirmDelete(scope.$index)" plain>
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import { cloneDeep } from 'lodash'
  import { getFinancialProgressError } from '@/utils/projectUtils'
  // import moment from 'moment'

  export default {
    name: 'financial-progress',
    components: {
    },
    data () {
      return {
        dateOptions: {
          disabledDate: this.disabledDate
        },
        instalments: ['1st Instalment', '2nd Instalment', '3rd Instalment', '4th Instalment', '5th Instalment', '6th Instalment', '7th Instalment', '8th Instalment'],
        uc_statuses: ['Pending', 'Submitted', 'Not submitted'],
        cc_statuses: ['Submitted', 'Not submitted'],
        dialogFormVisible: false,
        form: {
          shared_by: '',
          instalment: '',
          amount: 0,
          sanction_date: new Date(),
          release_date: new Date(),
          uc_status: '',
          uc_documents: [],
          uc_date: undefined,
          cc_status: '',
          cc_documents: [],
          cc_date: undefined,
          handed_over: '',
          handed_over_date: undefined,
          taken_over: '',
          taken_over_date: undefined,
          index: -1
        }
      }
    },
    props: {
      percent: Number,
      estimated_cost: Number,
      financialProgress: Array,
      shared: String
    },
    methods: {
      disabledDate (time) {
        return time.getTime() > new Date()
      },
      handleError () {
        this.$notify.error('An error when upload file.')
      },
      handleCCRemove (file) {
        let index = this.form.cc_documents.indexOf(file)
        this.form.cc_documents.splice(index, 1)
      },
      handleCCSuccess (file) {
        this.form.cc_documents.push(file)
      },
      handleUCRemove (file) {
        let index = this.form.uc_documents.indexOf(file)
        this.form.uc_documents.splice(index, 1)
      },
      handleUCSuccess (file) {
        this.form.uc_documents.push(file)
      },
      showAddForm () {
        const count = this.financialProgress.length
        const instalment = this.instalments[count]
        this.form = {
          shared_by: this.shared,
          instalment: instalment,
          amount: '0',
          sanction_date: undefined,
          release_date: undefined,
          uc_status: '',
          uc_documents: [],
          uc_date: undefined,
          cc_status: '',
          cc_documents: [],
          cc_date: undefined,
          handed_over: '',
          handed_over_date: undefined,
          taken_over: '',
          taken_over_date: undefined,
          index: -1
        }
        this.dialogFormVisible = true
      },
      showEditForm (form, index) {
        form.index = index
        this.form = cloneDeep(form)
        this.dialogFormVisible = true
      },
      confirmDelete (index) {
        this.$confirm('Are you sure to delete the row?', '', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel'
        }).then(() => {
          this.$emit('onDeleteProgress', index)
        }).catch(() => {})
      },
      transformForm (form) {
        if (form.uc_status !== 'Pending' && form.uc_status !== 'Submitted') {
          form.date_sending_uc = undefined
        }
        if (form.cc_status !== 'Pending' && form.cc_status !== 'Submitted') {
          form.date_sending = undefined
        }

        if (form.handed_over === '') {
          form.handed_over_date = undefined
        }
        if (form.taken_over === '') {
          form.taken_over_date = undefined
        }
      },
      submitProgress () {
        this.errors = getFinancialProgressError(this.form, this.estimated_cost, this.percent, this.financialProgress)
        if (!this.errors.valid) {
          delete this.errors.valid
          delete this.errors.alert
          this.$notify({
            title: 'Invalid',
            message: Object.values(this.errors).join(', '),
            type: 'error'
          })
          return
        }
        this.transformForm(this.form)
        this.dialogFormVisible = false
        if (this.form.index === -1) {
          this.$emit('onAddProgress', cloneDeep(this.form))
        } else {
          this.$emit('onEditProgress', cloneDeep(this.form))
        }
      }
    },
    mounted () {
      this.form.shared_by = this.shared
    }
  }
</script>

<style scoped>
</style>



