import Cesium from 'cesium/Cesium'
import CartesianConverter from './utils/CartesianConverter'
import RectangleConverter from './utils/RectangleConverter'
import GeoJsonHelper from './utils/GeoJsonHelper'

export default class SatelliteImagesController {
  // Construct Methods
  static newInstance (map) {
    const instance = new SatelliteImagesController(map)
    return instance
  }

  // Constructor
  constructor (map) {
    this._map = map
    this._imageryLayers = map.scene.imageryLayers

    this._tileLayers = []
    this._entities = []

    this._map.entities.collectionChanged.addEventListener(this._handleCollectionChanged, this)
  }

  // Properties
  get geometry () { return this._geometry }
  set geometry (val) { this._geometry = val }

  // Public methods
  showSatelliteImages (images) {
    this._clear()

    images.forEach(image => {
      this._addImage(image)
    })
  }

  addImages (images) {
    // console.log('add images', images ? images.length : undefined)
    images.forEach(image => {
      this._addImage(image)
    })
  }

  update (updateItems) {
    if (!Array.isArray(updateItems)) throw new Error('update items is not an array' + updateItems)

    const layers = this._tileLayers

    // Loop through layers
    for (let i = 0, len = layers.length; i < len; i++) {
      const layer = layers[i]

      // Determine whether layer need to update or not by finding it in updateItems array
      const updateItem = updateItems.find(item => item.id === layer._imageId)
      if (!updateItem) continue

      const props = updateItem.props
      if (!Array.isArray(props)) throw new Error('prop is not an array: ' + props)

      props.forEach(prop => {
        this.updateItem(layer, prop.name, prop.value)
      })
    }
  }

  updateItem (layer, name, value) {
    switch (name) {
      case 'alpha': layer.alpha = value; break
      case 'brightness': layer.brightness = value / 100; break
      case 'contrast': layer.contrast = value / 100; break
      case 'hue': layer.hue = value * Math.PI / 180; break // Convert degree to radians
      case 'saturation': layer.saturation = value; break
      case 'gamma': layer.gamma = value; break
      case 'raise_to_top': this._imageryLayers.raiseToTop(layer); break
      case 'lower_to_bottom': this._imageryLayers.lowerToBottom(layer); break
    }
  }

  removeImages (images) {
    // console.log('remove images', images ? images.length : undefined)
    if (Array.isArray(images) && images.length > 0) {
      for (let i = this._tileLayers.length - 1; i >= 0; i--) {
        const layer = this._tileLayers[i]
        if (images.findIndex(i => i.id === layer._imageId) >= 0) {
          this._map.scene.imageryLayers.remove(layer)
          this._tileLayers.splice(i, 1)
        }
      }

      for (let i = this._entities.length - 1; i >= 0; i--) {
        const layer = this._entities[i]
        if (images.findIndex(i => i.id === layer._imageId) >= 0) {
          this._map.entities.remove(layer)
          this._entities.splice(i, 1)
        }
      }
    }
  }

  clear () {
    this._clear()
  }

  // Private methods
  _clear () {
    for (let i = this._tileLayers.length; i >= 0; i--) {
      this._map.scene.imageryLayers.remove(this._tileLayers[i])
    }

    for (let i = this._entities.length; i >= 0; i--) {
      const entity = this._entities[i]
      this._map.entities.remove(entity)
      this._entities.splice(i, 1)
    }
  }

  _addImage (image) {
    let provider
    let entity

    switch (image.layer_type.name) {
      case 'image':
        entity = this._createImageryEntity(image)
        break
      case 'kmz':
        entity = this._createKmzEntity(image)
        break
      case 'web_map_service':
        provider = this._createWMSProvider(image)
        break
      case 'tiles':
        provider = this._createImageryProvider(image)
        break
      case 'gee_imagery':
        provider = this._createGEEImageryProvider(image)
        break
    }

    if (provider) {
      const layer = this._imageryLayers.addImageryProvider(provider)
      layer._imageId = image.id
      this._tileLayers.push(layer)
      return layer
    }

    if (entity) {
      const imageEntity = this._map.entities.add(entity)
      imageEntity._imageId = image.id
      this._entities.push(imageEntity)
      return imageEntity
    }
  }

  _createWMSProvider (image) {
    const url = image.tile_url.replace('{-y}', '{reverseY}')
    const subdomains = image.subdomains || 'abc'
    const meta = image.meta || {}
    let bounds = meta.clip ? meta.view_bounds : image.bounds
    bounds = GeoJsonHelper.toGeoJson(bounds)
    bounds = bounds ? RectangleConverter.fromDegreesPoints(bounds.geometry.coordinates[0]) : Cesium.Rectangle.MAX_VALUE

    return new Cesium.WebMapServiceImageryProvider({
      url: url,
      layers: meta.layers || '',
      subdomains: subdomains,
      rectangle: bounds,
      parameters: {
        service: 'WMS',
        version: '1.3.0',
        request: 'GetMap',
        styles: '',
        format: 'image/png',
        transparent: true
      },
      getFeatureInfoParameters: {
        service: 'WMS',
        version: '1.3.0',
        request: 'GetFeatureInfo'
      }
    })
  }

  _createImageryProvider (image) {
    const url = image.tile_url.replace('{-y}', '{reverseY}')
    const subdomains = image.subdomains | ''
    let bounds = image.bounds
    bounds = GeoJsonHelper.toGeoJson(image.bounds)
    bounds = bounds ? RectangleConverter.fromDegreesPoints(bounds.geometry.coordinates[0]) : Cesium.Rectangle.MAX_VALUE

    return new Cesium.UrlTemplateImageryProvider({
      url: url,
      subdomains: subdomains,
      rectangle: bounds
    })
  }

  _createGEEImageryProvider (image) {
    return new Cesium.GoogleEarthEnterpriseImageryProvider({
      url: image.tile_url
    })
  }

  _createImageryEntity (image) {
    const cartesian3Vertices = CartesianConverter.fromDegreesPoints(image.bounds.coordinates[0])

    return {
      id: image.id,
      polygon: {
        hierarchy: {
          positions: cartesian3Vertices
        },
        material: new Cesium.ImageMaterialProperty({
          image: image.tile_url,
          transparent: true
        })
      }
    }
  }

  _createKmzEntity (image) {
    const cartesian3Vertices = CartesianConverter.fromDegreesPoints(image.bounds.geometry.coordinates[0])

    return {
      id: image.id,
      polygon: {
        hierarchy: {
          positions: cartesian3Vertices
        },
        material: new Cesium.ImageMaterialProperty({
          image: window.URL.createObjectURL(image.tile_url),
          transparent: true
        })
      }
    }
  }

  _handleCollectionChanged (collection, added, removed, changed) {
    if (removed.length > 0) {
      let entity = removed.pop()
      while (entity) {
        try {
          const url = entity._polygon._material._image._value
          window.URL.revokeObjectURL(url)
        } catch (error) {
          // ignore
          console.error(error)
        }

        entity = removed.pop()
      }
    }
  }
}



