<template>
<el-button size="mini" :loading="logoutRequest.isRequesting()" v-if="type === 'button'" type="danger" @click="logout" >Logout</el-button>
<nav-bar-item v-else @click="logout">Logout</nav-bar-item>
</template>

<script>
import NavBarItem from '@/components/NavBarItem'
import { mapGetters } from 'vuex'

export default {
  name: 'logout-link',

  props: {
    type: {
      type: String,
      default: 'link'
    }
  },

  computed: {
    ...mapGetters(['logoutRequest']),

    status () { return this.logoutRequest.status }
  },

  watch: {
    status (val, old) {
      if (this.logoutRequest.isSuccess()) {
        // this.$router.push({ path: '' })
        this.$router.push({ path: '/login' })
      } else if (this.logoutRequest.isFailure() && !this.logoutRequest.error.isUnprocessableEntity()) {
        this.$notify.error({
          title: 'Logout failed',
          message: this.logoutRequest.error.message
        })
      }
    }
  },

  methods: {
    logout () {
      this.$store.dispatch('logout')
    }
  },

  components: {
    NavBarItem
  }
}
</script>



