<template>
  <div v-if="compareMode" class="compare-layers-slider">
    <compare-slider
      ref="compare-slider"
      @splitPositionChanged="val => $emit('splitPositionChanged', val)"
      v-model="splitValue">
    </compare-slider>

    <div class="compare-content-container">
      <div class="compare-content">
        <div v-for="(content, index) of leftCompareContent" :key="index" :title="content" class="one-line-text compare-content-item">{{ content }}</div>
      </div>
      <div class="compare-content">
        <div v-for="(content, index) of rightCompareContent" :key="index" :title="content" class="one-line-text compare-content-item">{{ content }}</div>
      </div>
    </div>
  </div>
</template>

<script>
  import CompareSlider from '../CompareSlider'

  export default {
    name: 'compare-layers-slider',

    props: {
      compareMode: {
        type: Boolean,
        default: false
      },
      leftCompareContent: {
        type: Array,
        default: []
      },
      rightCompareContent: {
        type: Array,
        default: []
      }
    },
    data () {
      return {
        splitValue: 0.5
      }
    },
    components: {
      CompareSlider
    },

    mounted () {
      this.splitValue = 0.5
    }
  }
</script>

<style lang="css" scoped>
  .compare-layers-slider {
    width: 100%;
    height: 100%;
    user-select: none;
    pointer-events: none;
    position: relative;
  }

  .compare-content-container {
    pointer-events: none;
    top: 0;
    left: 0;
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    margin-top: 20px;
  }

  .compare-content {
    padding: 10px 10px;
    background: white;
    border-radius: 5px;
    width: 180px;
    height: 48px;
    box-shadow: var(--default-box-shadow);
    z-index: 1000;
    overflow-x: hidden;
    overflow-y: hidden;
    margin: 0 40px;
    pointer-events: auto;
    display: flex;
    flex-flow: column;
    justify-content: center;
  }

  .compare-content:hover, .compare-content:focus {
    overflow-y: auto;
  }

  .compare-content-item {
    flex: none;
    text-align: center;
  }
</style>



