<template>
  <div class="compare-slider-container">
    <div ref="compare-divider" class="compare-divider" :style="dividerStyle">
      <div @mousedown.prevent.stop="handleMouseDown" class="compare-hand">
        <div class="compare-hand-arrow" style="margin-right: 4px">&#9664;</div>
        <div class="compare-hand-arrow" style="margin-left: 4px">&#9654;</div>
      </div>
    </div>
  </div>
</template>

<script>
  let tmpHandleMouseUp
  let tmpHandleMouseMove

  /**
   * @Events:
   *  split-position-changed: value => {}
   */
  export default {
    name: 'compare-slider',

    data () {
      return {
        divider: {
          element: undefined
        },
        containerRect: {
          left: 0,
          width: 0
        }
      }
    },

    props: {
      dividerWidth: {
        type: Number,
        default: 2
      },

      value: {
        type: Number,
        default: 0
      }
    },

    computed: {
      // left position in percentage of divider relative to container
      splitValue: {
        set (val) {
          val = Math.max(val, 0)
          val = Math.min(val, 1)
          this.$emit('input', val)
        },
        get () { return this.value }
      },

      // left position in pixel of divider relative to document
      splitPosition () {
        const pos = this.splitValue * this.containerRect.width + this.containerRect.left
        this.$emit('splitPositionChanged', pos)
        return pos
      },

      dividerStyle () {
        if (!this.divider) {
          return {
            left: 0,
            width: 0
          }
        }

        const left = this.splitPosition - this.dividerWidth / 2 - this.containerRect.left

        return {
          left: left + 'px',
          width: this.dividerWidth + 'px'
        }
      }
    },

    methods: {
      handleMouseDown (e) {
        window.addEventListener('mousemove', tmpHandleMouseMove, true)
      },

      handleMouseUp (e) {
        e.preventDefault()
        e.stopPropagation()
        window.removeEventListener('mousemove', tmpHandleMouseMove, true)
      },

      handleMouseMove (e) {
        e.preventDefault()
        e.stopPropagation()
        this.splitValue = (e.clientX - this.containerRect.left) / this.containerRect.width
      },

      // Sync props from DOM element to data
      syncData () {
        const rect = this.divider.element.parentElement.getBoundingClientRect()
        this.containerRect.left = rect.left
        this.containerRect.width = rect.width
        // console.log('sync data')
        window.requestAnimationFrame(this.syncData.bind(this))
      }
    },

    mounted () {
      this.divider = this.$refs['compare-divider']
      this.$set(this.divider, 'element', this.$refs['compare-divider'])

      tmpHandleMouseUp = this.handleMouseUp.bind(this)
      tmpHandleMouseMove = this.handleMouseMove.bind(this)
      window.addEventListener('mouseup', tmpHandleMouseUp, false)

      // start to sync data
      window.requestAnimationFrame(this.syncData.bind(this))
    },

    beforeDestroy () {
      window.removeEventListener('mouseup', tmpHandleMouseUp, false)
      window.removeEventListener('mousemove', tmpHandleMouseMove, true)
    }
  }
</script>

<style lang="css" scoped>
  .compare-slider-container {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1001;
    pointer-events: none;
  }

  .compare-divider {
    position: absolute;
    left: 50%;
    top: 0px;
    background-color: white;
    width: 2px;
    height: 100%;
    display: flex;
    pointer-events: auto;
  }

  .compare-hand {
    width: 42px;
    height: 42px;
    background: transparent;
    border: 2px solid white;
    border-radius: 50%;
    margin: auto;
    overflow: visible;
    flex: none;
    min-width: 0;
    transform: translateX(calc(-50% + 1px));
    pointer-events: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .compare-hand:hover, .compare-hand:focus {
    cursor: ew-resize;
  }

  .compare-hand-arrow {
    font-size: 12px;
    background: transparent;
    user-select: none;
    color: white;
  }
</style>



