<template>
  <div class="dashboard-page">
    <Sidebar
      ref="dashboardSidebar"
      :constructions="constructions"
      :temp="temp"
      @loadMore="loadMore"
      @viewMap="viewMap"
      :loading="loading"
      :allowMore="allData"
      @filterEdit="filterChanged"
      @onDeleteItem="deleteItem"
      @searchChanged="searchChanged"
    />
    <MapView
      @constructionSelected="constructionSelected"
      @changeFundingFilter="changeFundingFilter"
      @constructionUnSelected="constructionUnSelected"
      style="flex: 1"
      ref="dashboardMapView"
    />
  </div>
</template>

<script>
  import MapView from './MapView'
  import Sidebar from './Sidebar'
  import api from '@/api'
  import {cloneDeep} from 'lodash'

  export default {
    name: 'Dashboard',

    data () {
      return {
        search: {},
        filter: {},
        constructions: [],
        temp: null,
        form: {},
        allData: false
      }
    },

    components: {
      MapView,
      Sidebar
    },
    methods: {
      viewMap (construction) {
        this.$refs.dashboardMapView.zoomToConstruction(construction)
      },
      addConstructions (constructions) {
        if (this.$refs.dashboardMapView) {
          this.$refs.dashboardMapView.addConstructions(constructions)
        }
      },
      filterChanged (form) {
        this.filter = form
        this.getData()
      },
      changeFundingFilter (id) {
        this.$refs.dashboardSidebar.updateFunding(id)
      },
      searchChanged (form) {
        this.search = form
        this.getData()
      },
      getData () {
        this.getMapdata()
        this.getSidebarData()
      },
      async getMapdata () {
        let form
        form = cloneDeep(this.filter)
        form.order_name = this.search.workOrder
        form.name = this.search.construction
        let mapConstructions = []
        form.page = 1
        form.page_size = 50000
        form.load_type = 'lazy'
        let res = await api.getConstructions(form)
        let lastPage = res.data.last_page
        mapConstructions = res.data.data
        for (let i = 2; i <= lastPage; i++) {
          form.page = i
          let tmp = await api.getConstructions(form)
          mapConstructions = [...mapConstructions, ...tmp.data.data]
        }
        this.addConstructions(mapConstructions)
      },
      getSidebarData () {
        let form
        form = this.filter
        form.order_name = this.search.workOrder
        form.name = this.search.construction

        this.constructions = []
        form.page = 1
        form.page_size = 15
        form.load_type = 'all'
        this.form = form
        this.loading = true
        this.allData = false
        api.getConstructions(this.form).then(res => {
          this.loading = false
          this.form.lastPage = res.data.last_page
          this.constructions = res.data.data
          this.allData = !(this.form.page === this.form.lastPage)
        }).catch(error => {
          this.loading = false
          this.$notify.error('Something went wrong!')
          console.log(error)
        })
      },
      loadMore () {
        if (!this.allData) return
        this.form.page++
        this.loading = true
        api.getConstructions(this.form).then(res => {
          this.loading = false
          this.form.lastPage = res.data.last_page
          this.constructions = [...this.constructions, ...res.data.data]
          this.allData = !(this.form.page === this.form.lastPage)
        }).catch(error => {
          this.loading = false
          this.form.page--
          this.$notify.error('Something went wrong!')
          console.log(error)
        })
      },
      deleteItem (event) {
        let index = event.index
        let order = event.order
        let construction = event.construction
        this.$confirm(`This will permanently delete the project - ${construction.name}, Allocation Order No- ${order.name} . Continue?`, 'Warning', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }).then(() => {
          api.deleteOrder(order.id)
            .then(res => {
              this.constructions.splice(index, 1)
              this.$notify({
                type: 'success',
                message: res.data.message
              })
            })
            .catch(error => {
              this.$notify({
                type: 'error',
                message: error.message
              })
              console.log(error)
            })
        })
      },
      constructionSelected (construction) {
        let idx = this.constructions.findIndex(val => {
          return val.id === construction.id
        })
        if (idx >= 0) {
          this.constructions.splice(idx, 1)
        }
        this.constructions.unshift(construction)
        this.constructions = this.constructions.map(val => {
          val.highlight = false
          return val
        })
        this.constructions[0].highlight = true
      },
      constructionUnSelected () {
        this.constructions[0].highlight = false
        let item = this.constructions.shift()
        this.constructions.unshift(item)
      }

    },
    created () {
      this.getData()
    }
  }
</script>

<style scoped>
  .dashboard-page {
    height: 100%;
    width: 100%;
    display: inline-flex;
    background-color: #324157;
  }
</style>



