import {
  cloneDeep
} from 'lodash'
import GeometryHelper from './GeometryHelper'

/**
 * GeoJSON format
 * {
 *   type: "Feature",
 *   geometry: {
 *     type: "Polygon",
 *     coordinates: [
 *        [
 *          ...[longitude, latitude]
 *        ]
 *     ]
 *   }
 * }
 */
class GeoJsonHelper {
  static findFirstPolygon (geojson) {
    switch (geojson.type.toLowerCase()) {
      case 'featurecollection':
        return this._handleFeatureCollection(geojson)
      case 'feature':
        return this._handleFeature(geojson)
      case 'polygon':
        const geometry = geojson
        return this._handlePolygon(geometry)
      default:
        throw new Error('Not found polygon')
    }
  }

  static normalizeGeometry (geometry) {
    // Remove z-axis from coordinates
    const newGeo = cloneDeep(geometry)
    newGeo.coordinates = [
      newGeo.coordinates[0].map(coor => coor.slice(0, 2))
    ]

    return newGeo
  }

  static _handleFeatureCollection (geojson) {
    const features = geojson.features

    for (let i = 0, len = features.length; i < len; i++) {
      if (features[i].geometry.type.toLowerCase() === 'polygon') {
        return features[i]
      }
    }

    throw new Error('Not found geometry type Polygon in feature collection')
  }

  static _handleFeature (geojson) {
    if (geojson.geometry.type.toLowerCase() === 'polygon') return geojson
    throw new Error(`Geometry type ${geojson.geometry.type} is not Polygon`)
  }

  static _handlePolygon (geometry) {
    return {
      type: 'Feature',
      geometry: geometry
    }
  }

  static _fromLatLngBounds (north, east, west, south) {
    return {
      type: 'Feature',
      geometry: {
        type: 'Polygon',
        coordinates: [
          [
            [west, north],
            [east, north],
            [east, south],
            [west, south]
          ]
        ]
      }
    }
  }

  static isGeoJSONGeometry (target) {
    return target.type && target.type.toLowerCase() === 'polygon'
  }

  static isGeoJSONMultiPolygonGeometry (target) {
    return target.type && target.type.toLowerCase() === 'multipolygon'
  }

  static isGeoJSONFeature (target) {
    return target.type && target.type.toLowerCase() === 'feature'
  }

  static isBBox (target) {
    return Array.isArray(target) && target.length === 4 && target[0] <= target[2] && target[1] <= target[3]
  }

  static isPoint (target) {
    return (this.isGeoJSONFeature(target) && target.geometry.type.toLowerCase() === 'point') || (target.type.toLowerCase() === 'point' && Array.isArray(target.coordinates))
  }

  static isPolygon (target) {
    return (this.isGeoJSONFeature(target) && target.geometry.type.toLowerCase() === 'polygon') || (target.type.toLowerCase() === 'polygon' && Array.isArray(target.coordinates))
  }

  static isNESWBounds (target) {
    return target.northeast && target.southwest
  }

  static isLatLngBounds (target) {
    return Array.isArray(target) && target.length > 0
  }

  static toRectangle (multiPolygonGeoJson) {
    const firstCoor = multiPolygonGeoJson.geometry.coordinates[0][0]

    const rect = multiPolygonGeoJson.geometry.coordinates.reduce((rect, polygon) => {
      return polygon.reduce((rect, coor) => {
        rect.minLat = Math.min(rect.minLat, coor[1])
        rect.minLng = Math.min(rect.minLng, coor[0])
        rect.maxLat = Math.max(rect.maxLat, coor[1])
        rect.maxLng = Math.max(rect.maxLng, coor[0])
        return rect
      }, rect)
    }, {
      minLat: firstCoor[1],
      minLng: firstCoor[0],
      maxLat: firstCoor[1],
      maxLng: firstCoor[0]
    })

    return {
      type: 'Feature',
      geometry: {
        type: 'Polygon',
        coordinates: [
          [
            [ rect.minLng, rect.minLat ],
            [ rect.maxLng, rect.minLat ],
            [ rect.maxLng, rect.maxLat ],
            [ rect.minLng, rect.maxLat ],
            [ rect.minLng, rect.minLat ]
          ]
        ]
      }
    }
  }

  static toGeoJson (target) {
    if (!target) return

    if (GeoJsonHelper.isNESWBounds(target)) {
      let coords = GeometryHelper.neswBoundsToLatLngBounds(target)
      coords = coords.map(c => [c[1], c[0]])
      coords.push([coords[0][0], coords[0][1]])

      return {
        type: 'Feature',
        geometry: {
          type: 'Polygon',
          coordinates: [
            coords
          ]
        }
      }
    } else if (GeoJsonHelper.isGeoJSONFeature(target)) {
      return target
    } else if (GeoJsonHelper.isGeoJSONGeometry(target)) {
      return {
        type: 'Feature',
        geometry: target
      }
    } else if (GeoJsonHelper.isBBox(target)) {
      let coords = GeometryHelper.bboxToLatLngBounds(target)
      coords = coords.map(c => [c[1], c[0]])
      coords.push([coords[0][0], coords[0][1]])

      return {
        type: 'Feature',
        geometry: {
          type: 'Polygon',
          coordinates: [
            coords
          ]
        }
      }
    }

    throw new Error('Can not convert to GeoJSON from target: ' + JSON.stringify(target))
  }
}

export default GeoJsonHelper



