<template>
  <v-app>
    <router-view v-loading="getMeRequest.isRequesting()" element-loading-text="Please wait a moment..."></router-view>
  </v-app>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'app',

  computed: mapGetters(['getMeRequest'])
}
</script>



