<template>
  <v-dialog title="Create User"  max-width="800px" v-model="dialogVisible">
    <v-btn
      color="primary"
      slot="activator">
      <v-icon left dark>person_add</v-icon>
      Add User
    </v-btn>
    <v-card>
      <v-card-title>
        <span class="headline">ADD USER</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-lg>
          <v-layout wrap>
            <v-flex xs12>
              <v-radio-group v-model="form.role" row>
                <v-radio
                  label="DEPARTMENT HEAD"
                  :value="6"
                />
                <v-radio
                  label="DIVISION HEAD"
                  :value="2"
                />
                <v-radio
                  label="DISTRICT"
                  :value="4"
                />
                <v-radio
                  label="ADMIN"
                  :value="5"
                />
              </v-radio-group>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="person"
                v-model="form.name"
                :messages="errors.name"
                :counter="64"
                label="Name"/>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="mail"
                v-model="form.email"
                :messages="errors.email"
                label="Email"/>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="phone"
                v-model="form.mobile"
                counter="10"
                :messages="errors.mobile"
                label="Mobile"/>
            </v-flex>
            <v-flex xs12 md6>
              <v-select
                v-if="form.role === 2 || form.role === 6"
                v-model="form.department_id"
                prepend-icon="home"
                :filter="customFilterDep"
                autocomplete
                :items="departments"
                :messages="errors.department_id"
                label="Select Department"
                item-value="id"
                item-text="name"/>
              <v-select
                v-if="form.role === 4"
                v-model="form.district"
                prepend-icon="place"
                :filter="customFilterDis"
                autocomplete
                :items="districts"
                :messages="errors.district"
                label="Select District"
                item-value="dist_name"
                item-text="dist_name"/>
              <!-- <v-select
                v-if="form.role === 5"
                v-model="form.department_id"
                prepend-icon="home"
                :items="departments"
                :messages="errors.department_id"
                label="Select Department"
                item-value="id"
                item-text="name"
                disabled/> -->
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="lock"
                type="password"
                browser-autocomplete="new-password"
                v-model="form.password"
                :messages="errors.password"
                label="Password"/>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="lock"
                type="password"
                browser-autocomplete="new-password"
                :messages="errors.password_confirmation"
                v-model="form.password_confirmation"
                label="Password confirmation"/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" flat @click="dialogVisible = false">Cancel</v-btn>
        <v-btn
          color="blue darken-1" flat
          :loading="loading"
          @click="submit">
          Submit
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  import api from '@/api'

  export default {
    name: 'create-user',

    data () {
      return {
        loading: false,
        dialogVisible: false,
        errors: {},
        form: {
          role: 6,
          name: '',
          mobile: '',
          email: '',
          district: '',
          department_id: undefined
        }
      }
    },
    props: {
      departments: {
        type: Array,
        required: true
      },
      districts: {
        type: Array,
        required: true
      }
    },
    methods: {
      customFilterDis (item, queryText, itemText) {
        const hasValue = val => val != null ? val : ''
        const text = hasValue(item.dist_name)
        const query = hasValue(queryText)
        return text.toString()
          .toLowerCase()
          .indexOf(query.toString().toLowerCase()) === 0
      },
      customFilterDep (item, queryText, itemText) {
        const hasValue = val => val != null ? val : ''
        const text = hasValue(item.name)
        const query = hasValue(queryText)
        return text.toString()
          .toLowerCase()
          .indexOf(query.toString().toLowerCase()) === 0
      },
      submit () {
        this.errors = {}
        if (this.form.role === 2 || this.form.role === 6) {
          this.form.district = undefined
        } else {
          this.form.department_id = undefined
        }
        if (this.form.role === 5) {
          this.form.department_id = undefined
          this.form.district = undefined
        }
        if (this.form.role === 2 || this.form.role === 6) {
          if (!this.form.department_id) {
            const message = 'The department is required'
            this.errors.department_id = [message]
            this.$notify.error({
              title: message
            })
            return
          }
        }
        if (this.form.role === 4) {
          if (!this.form.district) {
            const message = 'The district is required'
            this.errors.district = [message]
            this.$notify.error({
              title: message
            })
            return
          }
        }
        this.loading = true
        api.addUser(this.form)
          .then(res => {
            this.$notify.success({
              title: 'New account has been successfully created'
            })
            this.loading = false
            this.form = {
              role: 6,
              name: '',
              mobile: '',
              email: '',
              district: undefined,
              department_id: undefined
            }
            this.dialogVisible = false
            this.$emit('userCreated', res)
          })
          .catch(error => {
            console.log(error)
            this.$notify.error({
              title: error.message
            })
            if (error.status_code === 422) {
              this.errors = error.errors
            }
            this.loading = false
          })
      }
    }
  }
</script>

<style scoped>
</style>



