<template>
  <v-dialog title="Create User"  max-width="800px" v-model="dialogVisible">
    <v-card>
      <v-card-title>
        <span class="headline">EDIT USER</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-lg>
          <v-layout wrap>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="person"
                v-model="form.name"
                :messages="errors.name"
                :counter="64"
                label="Name"/>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="mail"
                v-model="form.email"
                :messages="errors.email"
                label="Email"/>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field
                prepend-icon="phone"
                v-model="form.mobile"
                counter="10"
                :messages="errors.mobile"
                label="Mobile"/>
            </v-flex>
            <v-flex xs12 md6>
              <v-select
                v-if="form.role === 2 || form.role === 6"
                v-model="form.department_id"
                prepend-icon="home"
                :filter="customFilterDep"
                autocomplete
                :items="departments"
                :messages="errors.department_id"
                label="Select Department"
                item-value="id"
                item-text="name"/>
              <v-select
                v-if="form.role === 4"
                v-model="form.district"
                prepend-icon="place"
                :filter="customFilterDis"
                autocomplete
                :items="districts"
                :messages="errors.district"
                label="Select District"
                item-value="dist_name"
                item-text="dist_name"/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" flat @click="dialogVisible = false">Cancel</v-btn>
        <v-btn
          color="blue darken-1" flat
          :loading="loading"
          @click="submit">
          Update
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  import api from '@/api'
  export default {
    name: 'update-user',

    data () {
      return {
        loading: false,
        dialogVisible: false,
        errors: {},
        form: {
          id: -1,
          name: '',
          mobile: '',
          email: '',
          district: undefined,
          department_id: undefined
        }
      }
    },
    props: {
      departments: {
        type: Array,
        required: true
      },
      districts: {
        type: Array,
        required: true
      }
    },
    methods: {
      customFilterDis (item, queryText, itemText) {
        const hasValue = val => val != null ? val : ''
        const text = hasValue(item.dist_name)
        const query = hasValue(queryText)
        return text.toString()
          .toLowerCase()
          .indexOf(query.toString().toLowerCase()) === 0
      },
      customFilterDep (item, queryText, itemText) {
        const hasValue = val => val != null ? val : ''
        const text = hasValue(item.name)
        const query = hasValue(queryText)
        return text.toString()
          .toLowerCase()
          .indexOf(query.toString().toLowerCase()) === 0
      },
      setUser (user) {
        this.form.id = user.id
        this.form.name = user.name
        this.form.mobile = user.mobile
        this.form.email = user.email
        this.form.district = user.district
        this.form.department_id = user.department_id
        this.form.role = user.roles[0].id
        this.dialogVisible = true
      },
      submit () {
        this.loading = true
        this.errors = {}
        if (this.form.role === 2 || this.form.role === 6) {
          this.form.district = undefined
        } else if (this.form.role === 4) {
          this.form.department_id = undefined
        } else {
          this.form.district = undefined
          this.form.department_id = undefined
        }
        api.updateUser(this.form)
          .then(res => {
            this.$notify.success({
              title: 'User information has been successfully updated'
            })
            this.loading = false
            this.form = {
              id: -1,
              name: '',
              mobile: '',
              email: '',
              district: undefined,
              department_id: undefined
            }
            this.dialogVisible = false
            this.$emit('userUpdated', res)
          })
          .catch(error => {
            console.log(error)
            this.$notify.error({
              title: error.message
            })
            if (error.status_code === 422) {
              this.errors = error.errors
            }
            this.loading = false
          })
      }
    }
  }
</script>

<style scoped>
</style>



